import { call, put, takeEvery } from "redux-saga/effects"
import { 
    GET_LEAVE_PLANS,
    GET_LEAVE_PLANS_SUCCESS,
    GET_LEAVE_PLANS_FAIL,
    GET_LEAVE_PLAN_DETAIL,
    GET_LEAVE_PLAN_DETAIL_SUCCESS,
    GET_LEAVE_PLAN_DETAIL_FAIL,
    ADD_LEAVE_PLAN,
    ADD_LEAVE_PLAN_SUCCESS,
    ADD_LEAVE_PLAN_FAIL,
    UPDATE_LEAVE_PLAN,
    UPDATE_LEAVE_PLAN_SUCCESS,
    UPDATE_LEAVE_PLAN_FAIL,
    DELETE_LEAVE_PLAN,
    DELETE_LEAVE_PLAN_SUCCESS,
    DELETE_LEAVE_PLAN_FAIL,
    GET_LEAVE_REQUESTS,
    GET_LEAVE_REQUESTS_SUCCESS,
    GET_LEAVE_REQUESTS_FAIL,
    GET_LEAVE_REQUEST_DETAIL,
    GET_LEAVE_REQUEST_DETAIL_SUCCESS,
    GET_LEAVE_REQUEST_DETAIL_FAIL,
    ADD_LEAVE_REQUEST,
    ADD_LEAVE_REQUEST_SUCCESS,
    ADD_LEAVE_REQUEST_FAIL,
    UPDATE_LEAVE_REQUEST,
    UPDATE_LEAVE_REQUEST_SUCCESS,
    UPDATE_LEAVE_REQUEST_FAIL,
    DELETE_LEAVE_REQUEST,
    DELETE_LEAVE_REQUEST_SUCCESS,
    DELETE_LEAVE_REQUEST_FAIL,
 } from "./actionTypes"

 import {
    getLeavePlansFail, 
    getLeavePlanDetailsSuccess,
    getLeavePlanDetails,
    addLeavePlan,
    updateLeavePlan,
    deleteLeavePlan,
    getLeaveRequestsSuccess, 
    getLeaveRequestsFail,
    getLeavePlansSuccess
} from "./actions"

import { 
    bkGetLeavePlans,
    bkGetLeavePlanDetails,
    bkGetLeaveRequests, 
    bkAddLeaveRequest, 
    bkUpdateLeaveRequest, 
    bkDeleteLeaveRequest  
} from "helpers/backend_helper"

function* onfetchLeavePlans() {
    try {
        const response = yield call(bkGetLeavePlans)
        yield put(getLeavePlansSuccess(response))
    } catch (error) {
        yield put(getLeavePlansFail(error))
    }
}
function* onfetchLeavePlanDetails({payload: leaveplanId}) {
    try {
        const response = yield call(bkGetLeavePlanDetails)
        yield put(getLeavePlanDetailsSuccess(response))
    } catch (error) {
        yield put(getLeavePlanDetailsFail(error))
    }
}
function* onAddLeavePlan({payload: leavePlan}) {
    try {
        const response = yield call(bkGetLeavePlans)
        yield put(getLeavePlansSuccess(response))
    } catch (error) {
        yield put(getLeavePlansFail(error))
    }
}
function* onUpdateLeavePlan() {}
function* onDeleteLeavePlan() {}
function* onfetchLeaveRequests() {
    try {
      const response = yield call(bkGetLeaveRequests)
      yield put(getLeaveRequestsSuccess(response))
    } catch (error) {
      yield put(getLeaveRequestsFail(error))
    }
  }
  function* onfetchLeaveRequestDetails() {}
  function* onAddLeaveRequest({ payload: leaveRequest }){
      try{
          const response = yield call(bkAddLeaveRequest, leaveRequest)
          yield put()
      } catch (error) {
  
      }
  }
  function* onfetchUpdateRequests() {}
  function* onfetchDeleteRequests() {}
  
  
  
  
  function* leaveSaga() {
    yield takeEvery(GET_LEAVE_REQUESTS, onfetchLeaveRequests)
    yield takeEvery(GET_LEAVE_PLANS, onfetchLeavePlans)
  }
  
  export default leaveSaga