import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle, UncontrolledTooltip, Form, Container, Button, Alert, Modal,UncontrolledDropdown, DropdownToggle,DropdownMenu, Table } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Select from 'react-select';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
import { useParams } from 'react-router-dom';
import {Link} from 'react-router-dom';

    const ContractDetails = () => {
        const history = useHistory();
        const {id} = useParams();
        toastr.options = TOAST_OPTIONS;
        const [contractorNo, setContractorNo] = useState('')
        const [contractorName, setContractorName] = useState('')
        const [contractManager, setContractManager] = useState('')

        const [subject, setSubject] = useState('');
        const [contractAmount, setContractAmount] = useState('');
        const [budgetCode, setBudgetCode] = useState('');
        const [currencyCode, setCurrencyCode] = useState('');
        const [costCenterCode, setCostCenterCode] = useState('');
        const [contractStartDate, setContractStartDate] = useState('');
        const [durationMeasurement, setDurationMeasurement] = useState('');
        const [contractLength, setContractLength] = useState('');
        const [contractEndDate, setContractEndDate] = useState('');
        const [rententionPercentage, setRententionPercentage] = useState('');
        const [vendorInvoiceNo, setVendorInvoiceNo] = useState('');
        const [purchaseRequisitionNo, setPurchaseRequisitionNo] = useState('');
        const [procurementReferenceNo, setProcurementRefenceNo] = useState('');
        const [state, setState] = useState('');
        const [status, setStatus] = useState('')


        const [contractLines, setContractLines] = useState([])

        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);

        const [modal_activities, setmodal_activities] = useState(false);
        const [modal_deliverables, setmodal_deliverables] = useState(false);
        const [modal_tracking, setmodal_tracking] = useState(false);

        const [contractTracking, setContractTracking] = useState([])

        const [addDetails, setAddDetails] = useState(false)
        const [phaseDesc, setPhaseDesc] = useState('')
        const [contractType, setContractType] = useState('')
        const [phaseAmount, setPhaseAmount] = useState('')
        const [selectedDates, setSelectedDates] = useState('')

        const [editLine, setEditLine] = useState(false)
        const [trackingSystemId, setTrackingSystemId] = useState('')
        const [trackingEtag, setTrackingEtag] = useState('')

        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
          }
        const tog_activities = () => {
            setmodal_activities(!modal_activities);
            removeBodyCss();
        }
        const tog_deiverables = () => {
            setmodal_deliverables(!modal_deliverables);
            removeBodyCss();
        }
        const tog_tracking = () => {
            setmodal_tracking(!modal_tracking);
            removeBodyCss();
        }
        
        const getData = () =>{
            setIsloading(true)
            axios.get(`${url.CONTRACTX}?$filter=contractNo eq '${id}'&$expand=contractDetails`, HRConfig).then((r)=>{
                console.log(r)
                setContractorNo(r.data.value[0].contractorNo)
                setContractorName(r.data.value[0].contractorName)
                setContractManager(r.data.value[0].contractManager)
                setSubject(r.data.value[0].SubjectofProcurement)
                setContractAmount(r.data.value[0].ContractAmount)
                setBudgetCode(r.data.value[0].BudgetCode)
                setCurrencyCode(r.data.value[0].CurrencyCode)
                setCostCenterCode(r.data.value[0].costCenterCode)
                setContractStartDate(r.data.value[0].contractStartDate)
                setContractEndDate(r.data.value[0].contractEndDate)
                setDurationMeasurement(r.data.value[0].durationMeasurement)
                setContractLength(r.data.value[0].contractLength)
                setRententionPercentage(r.data.value[0].retentionPercentage)
                setVendorInvoiceNo(r.data.value[0].vendorInvoiceNo)
                setPurchaseRequisitionNo(r.data.value[0].purchaseRequisitionNo)
                setProcurementRefenceNo(r.data.value[0].procurementReferenceNo)
                setState(r.data.value[0].State)
                setStatus(r.data.value[0].Status)
                setContractLines(r.data.value[0].contractDetails)
                axios.get(url.EMPLOYEES + "?$filter=No eq '"+ r.data.value[0].contractManager +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                        }else{
                            toastr.error("Contract manager details not found");
                        }
                        setIsloading(false)
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                        setIsloading(false)
                }) 
                axios.get(`${url.CONTRACTTRACKINGDETAILS} and contractNo eq '${id}' `, HRConfig).then((r)=>{
                    console.log(r)
                    setContractTracking(r.data.value)
                }).catch((e)=>{
                    console.log(e)
                })
            }).catch((e)=>{
                setIsloading(false)
                console.log(e)
            })

            
        }
       
        useEffect(() => {
           getData() 
        },[]);
        
        const handleAddTracking = () =>{
            const data = {
                contractNo: id,
                TrackingType: 'Other Specifications',
                PhaseDescription:phaseDesc,
                contractExpenseType: contractType,
                PhaseAmount: phaseAmount,
                StartDate: formatDate(selectedDates[0]),
                EndDate: formatDate(selectedDates[0])
            }
            console.log(JSON.stringify(data))
            setIsloading(true)
            axios.post(url.CONTRACTTRACKINGDETAILSX, JSON.stringify(data), HRConfig).then((r)=>{
                console.log(r)
                setIsloading(false)
                toastr.error('Tracking details saved')
                setPhaseDesc('')
                setContractType('')
                setPhaseAmount('')
                setSelectedDates(null)
                setAddDetails(false)
                getData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleEditTracking = (k) =>{
            setAddDetails(true)
            setEditLine(true)
            setPhaseDesc(contractTracking[k].PhaseDescription)
            setPhaseAmount(contractTracking[k].PhaseAmount)
            setContractType(contractTracking[k].contractExpenseType)
            setSelectedDates([new Date(contractTracking[k].StartDate), new Date(contractTracking[k].EndDate)])
            setTrackingSystemId(contractTracking[k].SystemId)
            setTrackingEtag(contractTracking[k]['@odata.etag'])

        }
        const handleUpdateTrackingLine = () =>{
            const data = {
                TrackingType: 'Other Specifications',
                PhaseDescription:phaseDesc,
                contractExpenseType: contractType,
                PhaseAmount: phaseAmount,
                StartDate: formatDate(selectedDates[0]),
                EndDate: formatDate(selectedDates[0])
            }
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : trackingEtag
                }
            }
            axios.patch(`${url.CONTRACTTRACKINGDETAILSX}(${trackingSystemId})`, JSON.stringify(data), Config).then((r)=>{
                setIsloading(false)
                toastr.error('Tracking details updated')
                setPhaseDesc('')
                setContractType('')
                setPhaseAmount('')
                setSelectedDates(null)
                setAddDetails(false)
                setEditLine(false)
                getData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleDeleteTracking = (k) =>{
 
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : contractTracking[k]['@odata.etag']
                }
            }
            axios.delete(`${url.CONTRACTTRACKINGDETAILSX}(${contractTracking[k].SystemId})`,  Config).then((r)=>{
                setIsloading(false)
                toastr.error('Tracking details deleted')
                setAddDetails(false)
                getData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const cancelApprovalRequest = () =>{
        
        }
        const handleDelete = () =>{
           
        }
        const handleApprovalHistory = () =>{
        
        }
        const handleArchive = () =>{
          
        }
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Contract Details | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Contract details" />
                        <div>
                        
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Contractor Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>Contractor No</label>
                                            <input type="text" className="form-control" disabled={true}  defaultValue={contractorNo} />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>Contractor Name</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={contractorName}   />
                                        </div>
                                    </Col>
                                    <Col md={12} className='mb-2'>
                                        <div className="mb-3">
                                            <label>Contract Manager</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={contractManager} />
                                        </div>
                                    </Col>
                                   
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                            <CardBody>
                            <CardTitle>Contract Details</CardTitle>
                                <Row>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Subject of procurement</label>
                                        <input  type="text" className="form-control" defaultValue={subject} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Contract amount</label>
                                        <input  type="text" className="form-control" defaultValue={contractAmount} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Currency code</label>
                                        <input  type="text" className="form-control" defaultValue={currencyCode} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Budget code</label>
                                        <input  type="text" className="form-control" defaultValue={budgetCode} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Cost center code</label>
                                        <input  type="text" className="form-control" defaultValue={costCenterCode} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Contract start date</label>
                                        <input  type="text" className="form-control" defaultValue={contractStartDate} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Contract end date</label>
                                        <input  type="text" className="form-control" defaultValue={contractEndDate} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Duration measurement</label>
                                        <input  type="text" className="form-control" defaultValue={durationMeasurement} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Contract length</label>
                                        <input  type="text" className="form-control" defaultValue={contractLength} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Rentention percentage</label>
                                        <input  type="text" className="form-control" defaultValue={rententionPercentage} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Purchase requisition no</label>
                                        <input  type="text" className="form-control" defaultValue={purchaseRequisitionNo} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Procurement reference no</label>
                                        <input  type="text" className="form-control" defaultValue={procurementReferenceNo} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Vendor invoice no</label>
                                        <input  type="text" className="form-control" defaultValue={vendorInvoiceNo} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Status</label>
                                        <input  type="text" className="form-control" defaultValue={status} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>State</label>
                                        <input  type="text" className="form-control" defaultValue={state} disabled={true}/>
                                    </div>
                                </Col>
                                
                                    
                                    </Row>
                                    </CardBody>
                                    </Card>
                                    {contractLines.map((i, k)=>(
                                        <Row key={k}>
                                            <Col>
                                            <Card>
                                            <CardBody>
                                           
                                                <Row className="mb-3">
                                                <Col xl={3} sm={6}>
                                                    <div className="mt-2">
                                                    <h5>Item  {k+1} </h5>
                                                    </div>
                                                </Col>
                                                <Col xl={9} sm={6}>
                                                    <div className="mt-4 mt-sm-0 float-sm-end d-flex align-items-center">
                                                    <div className="search-box mb-2 me-2">
                                                        <div className="position-relative">
                                                    
                                                        </div>
                                                    </div>

                                                    <UncontrolledDropdown className="mb-0" direction="right">
                                                        <DropdownToggle
                                                        color="white"
                                                        className="btn btn-link text-muted mt-n2"
                                                        >
                                                        <i className="mdi mdi-dots-vertical font-size-20"></i>
                                                        </DropdownToggle>

                                                        <DropdownMenu>
                                                        {(status == 'Open') && (
                                                            <Link className="dropdown-item" to="#">Edit payment requisition line</Link>
                                                        )}
                                                        </DropdownMenu>
                                                    </UncontrolledDropdown>
                                                    </div>
                                                </Col>
                                                </Row>
                                            
                                        
                               
                                            <Row >
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>Type</label>
                                                        <input  type="text" className="form-control" defaultValue={i.Type} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>No</label>
                                                        <input  type="text" className="form-control" defaultValue={i.contractNo} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>Description</label>
                                                        <input  type="text" className="form-control" defaultValue={i.Description} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>Description 2</label>
                                                        <input  type="text" className="form-control" defaultValue={i.Description2} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Quantity</label>
                                                        <input  type="text" className="form-control" defaultValue={i.Quantity} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Direct unit cost VAT%</label>
                                                        <input  type="text" className="form-control" defaultValue={i.directUnitCost} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>Line Amount</label>
                                                        <input  type="text" className="form-control" defaultValue={i.lineAmount} disabled={true} />
                                                    </div>
                                                </Col>
                                                
                                            </Row>
                                            </CardBody>
                                            </Card>
                                            </Col>
                                        </Row>
                                    ))}

                                    <Row> 
                                        <Col md={4} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" >
                                                    Management Activities
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={4} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" onClick={tog_deiverables}>
                                                    Contract Deliverables
                                                </Button>
                                            </div>
                                        </Col>  
                                        <Col md={4} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" onClick={tog_tracking}>
                                                   Tracking Details
                                                </Button>
                                            </div>
                                        </Col>  
                                
                                    </Row>

                                
                            </Col>
                            <Col>
                           
                                
                                    
                                <Modal isOpen={modal_activities} toggle={() => { tog_activities() }} centered >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Management Activities</h5>
                                    <button type="button" onClick={() => { setmodal_activities(false); }} className="close" data-dismiss="modal" aria-label="Close" >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                     <Row ></Row>
                                  </div>
                                  <div className="modal-footer">
                                    <button type="button" onClick={() => {  tog_activities() }} className="btn btn-danger" data-dismiss="modal">Cancel</button>
                                  </div>
                                </Modal>

                                <Modal isOpen={modal_deliverables} toggle={() => { tog_deiverables() }} centered >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Contract Deliverables</h5>
                                    <button type="button" onClick={() => { setmodal_deliverables(false); }} className="close" data-dismiss="modal" aria-label="Close" >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                     <Row ></Row>
                                  </div>
                                  <div className="modal-footer">
                                    <button type="button" onClick={() => {  tog_deiverables() }} className="btn btn-danger" data-dismiss="modal">Cancel</button>
                                  </div>
                                </Modal>

                                <Modal isOpen={modal_tracking} toggle={() => { tog_tracking() }} centered size="xl" >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Tracking Details</h5>
                                    <button type="button" onClick={() => { setmodal_tracking(false); }} className="close" data-dismiss="modal" aria-label="Close" >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                     <Row >
                                        <Col sm={12} onClick={()=>{setAddDetails(!addDetails)}}>
                                            <a href='#' ><h5 className="text-primary">{editLine ? `Edit details`: `Add details`}</h5></a>
                                            <hr/>
                                        </Col>
                                        {addDetails && (
                                        <>
                                        <Col lg={6}>
                                            <div className="mb-3">
                                                <label>Phase description</label>
                                                <input  type="text" className="form-control" value={phaseDesc} onChange={(e)=>setPhaseDesc(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col lg={6}>
                                            <div className="mb-3">
                                                <label>Contract expense type</label>
                                                <select className="form-select" value={contractType} onChange={(e)=>setContractType(e.target.value)} >
                                                    <option value=""></option>
                                                    <option value="Supplies">Supplies</option>
                                                    <option value="Professional Fee">Professional Fee</option>
                                                    <option value="Re-Imbursement">Re-Imbursement</option>
                                                </select>
                                            </div>
                                        </Col>
                                        <Col lg={6}>
                                            <div className="mb-3">
                                                <label>Phase amount</label>
                                                <input  type="number" className="form-control" value={phaseAmount} onChange={((e)=>setPhaseAmount(e.target.value))} />
                                            </div>
                                        </Col>
                                        <Col lg={6}>
                                            <div className="mb-3">
                                                <label>Start date - End date</label>
                                                <Flatpickr
                                                    className="form-control"
                                                    placeholder="dd M,yyyy"
                                                    options={{
                                                        mode: "range",
                                                        dateFormat: "Y-m-d"
                                                    }}
                                                    value={selectedDates}
                                                    onChange={(dates) =>{setSelectedDates(dates)} }
                                                    />
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-4'>
                                            <div className="d-grid">
                                            {isloading ? (<Button color="secondary" type="button" className="btn">Please wait...</Button>) : 
                                            (editLine ? (
                                                <Button color="primary" type="button" className="btn" onClick={handleUpdateTrackingLine}>
                                                    Update...
                                                </Button>
                                            ):(
                                                <Button color="primary" type="button" className="btn" onClick={handleAddTracking}>
                                                    Add...
                                                </Button>
                                            ))}
                                            </div>
                                        </Col>
                                        </>
                                        )}
                                        <Col sm={12}>
                                            <h5 className="text-primary">View details</h5>
                                            <hr/>
                                        </Col>
                                        <Col lg={12}>
                                            {(contractTracking.length > 0) ? (
                                            <div className="table-responsive">
                                                <Table className="table table mb-4">
                                                    <thead>
                                                        <tr>
                                                            <th>Phase description</th>
                                                            <th>Contract expense type</th>
                                                            <th>Phase amount</th>
                                                            <th>start date</th>
                                                            <th>End date</th>
                                                            <th></th>
                                                            
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                      	                {contractTracking.map((a,k)=>(
                                                            <tr key={k}>
                                                                <td>{a.PhaseDescription}</td>
                                                                <td>{a.contractExpenseType}</td>
                                                                <td>{a.PhaseAmount}</td>
                                                                <td>{a.StartDate}</td>
                                                                <td>{a.EndDate}</td>
                                                                <td> <>
                                                                    <div className="d-flex gap-3">
                                                                        <Link
                                                                        to="#"
                                                                        className="text-success"
                                                                        onClick={() => handleEditTracking(k)}
                                                                        >
                                                                        <i className="mdi mdi-pencil font-size-18" id="edittooltip" />
                                                                        <UncontrolledTooltip placement="top" target="edittooltip">
                                                                            Edit
                                                                        </UncontrolledTooltip>
                                                                        </Link>
                                                                        <Link
                                                                        to="#"
                                                                        className="text-danger"
                                                                        onClick={() => handleDeleteTracking(k)}
                                                                        >
                                                                        <i className="mdi mdi-delete font-size-18" id="deletetooltip" />
                                                                        <UncontrolledTooltip placement="top" target="deletetooltip">
                                                                            Delete
                                                                        </UncontrolledTooltip>
                                                                        </Link>
                                                                    </div>
                                                                    </></td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </Table>
                                                </div>
                                            ):(
                                                <div className="text-center">
                                                    <div className="mb-4">
                                                    <i className="mdi mdi-check-circle-outline text-success display-4" />
                                                    </div>
                                                    <div>
                                                    <h5>No details found</h5>
                                                    <p className="text-muted">
                                                            
                                                    </p>
                                                    </div>
                                                </div>
                                            )}
                                            </Col>
                    
                                     </Row>
                                  </div>
                                
                                </Modal>


                        </Col>
                        </Row>
                       
                        </div>


                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
         

        );
    }

export default ContractDetails;