import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardSubtitle, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const ApproveLeavePlan = () => {
        const history = useHistory();
        const {no,id} = useParams();
        const [userError, setUserError] = useState('');
        const [lPNo, setLPNo] = useState('');
        const [lPStatus, setLPStatus] = useState('');
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [empDept, setEmpDept] = useState('');
        const [employee, setEmployee] = useState([]);
        const [postingDate, setPostingDate] = useState('');
        const [leaveLines, setLeaveLines] = useState([]);
        const [isloading, setIsloading] = useState(false);
        const [cc, setCC] = useState('danger')
        const [showApproveBtn, setShowApproveBtn] = useState(true);
        const [showRejectBtn, setShowRejectBtn] = useState(false);
        const [comment, setComment] = useState('')
 

        useEffect(() => {
            setIsloading(true)
            axios.get(url.LEAVE_PLAN_X + "?$filter=No eq '"+ no +"' &$expand=leavePlanLines", HRConfig)
                .then(function(response){
                    if(response.data.value.length > 0){
                        toastr.success("Leave Plan Details Found");
                        setUserError(false);
                        setLPNo(response.data.value[0].No)
                        setEmpNo(response.data.value[0].EmployeeNo)
                        setEmpName(response.data.value[0].EmployeeName)
                        setEmpDept(response.data.value[0].ShortcutDimension1Code)
                        setLPStatus(response.data.value[0].Status);
                        (lPStatus == 'Open') ? setCC('danger') : (lPStatus == 'Pending Approval') ? setCC('warning') : setCC('success');
                        setPostingDate(response.data.value[0].PostingDate)
                        setLeaveLines(response.data.value[0].leavePlanLines)
                    }else{
                        toastr.error("Leave Plan Details Not Found");
                    }
                    setIsloading(false)
                }).catch(function (error) {
                    setUserError(true);
                    setIsloading(false)
                    console.log(error)
                })
        
        },[]);

        

        const [rows1, setrows1] = useState([{ id: 1 }]);

        const handleActionChange = () => {
            const selectedAction = document.getElementsByClassName('lAction')[0].value;
            if(selectedAction == 'APPROVE'){
                setShowApproveBtn(true)
                setShowRejectBtn(false)
            }else if(selectedAction == 'REJECT'){
                setShowApproveBtn(false)
                setShowRejectBtn(true)
            }
        }
        const [formRows, setFormRows] = useState([{ id: 1 }]);
        const [inputs, setInputs] = useState([]);
        toastr.options = TOAST_OPTIONS;
  
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            event.preventDefault();

        }  
 
        const handleApprovePlan = () => {
            setIsloading(true)
            const lNo = {"no": lPNo}
            axios.post(url.APPROVE_LP, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    console.log(response); 
                    if(response.status == '200'){
                        toastr.success("Leave Plan Approved" );
                        history.push(`/leave-request-approved/${lPNo}`) 
                    }else{
                        toastr.success("Could not Approve leave plan" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    console.log(error)
                    setIsloading(false)
                })
        }   
        const handleReject = () =>{
            setIsloading(true)
            const lNo = {"no": lPNo, "rejectioncomment":comment}
            axios.post(url.REJECT_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){ 
                    if(response.status == '200'){
                        toastr.success(response.data.value);
                        history.push(`/rejected/${lPNo}`) 
                    }else{
                        toastr.success("Could not Approve leave plan" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    console.log(error)
                    setIsloading(false)
            })  
        }   
        

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Approve {lPNo} | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem={`Approve ${lPNo} `} />
                        <Form onSubmit={handleSubmit}>
                        <Alert color={(lPStatus == 'Approved') ? `success` : (lPStatus == 'Pending Approval') ? `warning` : `danger`} role="alert">
                                {lPStatus}      
                        </Alert>
                     
                       
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Employee Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control"  value = {empNo} onChange={handleChange} disabled={true}  />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" defaultValue={empName} onChange={handleChange} disabled={true}  />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" defaultValue={empDept} onChange={handleChange} disabled={true}/>
                                        <label htmlFor="floatingDepartInput">Department</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                                <CardBody>
                           
                                <CardTitle className="mb-3">
                                
                                </CardTitle>
                                <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingDepartInput">Date Posted</label>
                                        <input name='postingdate'   type="text" className="form-control postingDate" defaultValue={postingDate} onChange={handleChange} disabled={true}/>
                                        
                                        </div>
                                    </Col>
                                </Row>
                                
                                <div>
                                        {(leaveLines).map((formRow, key) => (
                                            <Row key={key}>
                        
                                            <Col lg={5} className="mb-3">
                                            <div className="mb-3">
                                            <label htmlFor="floatingSelectGrid">{formRow.LineNo} Leave Category</label>
                                                <select defaultValue="0" className="form-select lType" disabled={true}>
                                                <option>{formRow.LeaveType}</option>
                                                    <option>ANNUAL</option>

                                                    <option>W-OUT PAY</option>
                                                </select>
                                                
                                            </div>
                                            </Col>

                                            <Col lg={5} className="mb-3">
                                                <div className="mb-3">
                                                <label htmlFor="floatingPlanningDateInput">Start Date - End Date</label>
                                                <Flatpickr
                                                    className="form-control lDates"
                                                    placeholder="dd M,yyyy"
                                                    options={{
                                                        mode: "range",
                                                        dateFormat: "Y-m-d",
                                                        defaultDate: [`${formRow.StartDate}`, `${formRow.EndDate}`]
                                                    }}
                                                    />
                                                
                                                </div>
                                            </Col>
                                            <Col lg={2} className="mb-3">
                                                <div className="mb-3">
                                                <label htmlFor="floatingDepartInput">Days</label>
                                                <input name='days'   type="text" className="form-control days" defaultValue={formRow.Quantity} onChange={handleChange} disabled={true} />
                                                
                                                </div>
                                            </Col>

                                            </Row>
                                            
                                        ))}
                               
                                            </div>
                                            <Row>
                                                <Col md={12}>
                                                    <div className="mb-3">
                                                    <label htmlFor="floatingDepartInput">Status</label>
                                                    <input name='status'  type="text" className="form-control" disabled={true} defaultValue={lPStatus} onChange={handleChange} />
                                                    
                                                    </div>
                                                </Col>
                                            </Row>
                                            <Row>
                                                <Col md={12}>
                                                    <div className="mb-3">
                                                    <label htmlFor="floatingSelectGrid"> Select Action</label>
                                                        <select  className="form-select lAction" onChange={handleActionChange}>
                                                            
                                                            <option value="APPROVE">APPROVE LEAVE PLAN</option>
                                                            <option value="REJECT">REJECT LEAVE PLAN</option>
                                    
                                                        </select>
                                                        
                                                    </div>
                                                </Col>
                                            </Row>
                                            {showRejectBtn && (
                                                <Row>
                                                <Col md={12}>
                                                    <div className="mb-3">
                                                    <label htmlFor="floatingSelectGrid"> Rejection Comment</label>
                                                        <input name='rejectComment'  type="text" className="form-control rejectComment"  onChange={(e)=>setComment(e.target.value)}   />
                                                        
                                                    </div>
                                                </Col>
                                                </Row>
                                            )}
                                            </CardBody>
                                            </Card>
                                            
                                       
                  
                                                    {showRejectBtn && (
                                                    <Col md={12} className="reject">
                                                        <div className="d-grid gap-2">
                                                            <Button color="danger" type="button" className="btn btnRejectRequest" onClick={handleReject}>
                                                                Reject
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                    )}
                                                    {showApproveBtn && (
                                                    <Col md={12} className="approve">
                                                        <div className="d-grid gap-2">
                                                            <Button color="success" type="button" className="btn btnApproveRequest" onClick={handleApprovePlan}>
                                                            Approve Leave Plan
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                    )}
                                             
                                            
                    
                        </Col>
                        </Row>
                        </Form>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default ApproveLeavePlan;