import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert,UncontrolledDropdown, DropdownToggle,DropdownMenu, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { IPAS_X, IPALINE, SENDIPA4APPROVAL, CANCELIPAAPPROVAL, EMPLOYEES, PIPS, APPNAME, MIP_X, MIP_LINES } from 'helpers/url_helper';
import * as url from "../../helpers/url_helper"
import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Timeline from './Timeline';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const PIPDetails = () => {
        const {id} = useParams();
        const history = useHistory();
        toastr.options = TOAST_OPTIONS;
        const [rows1, setrows1] = useState([{ id: 1 }]);
        //const [th, setTh] = useState(['Customer','Financial','Internal Business Process','Learning and Growth'])
        const [th, setTh] = useState(['Customer/Stakeholder',' Financial Stewardship','Internal Business Process','Organization Capacity'])
        const [periods , setPeriods] = useState(['January-June', 'July-December'])
        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [etag, setEtag] = useState('');
        const [systemId, setSystemId] = useState('');
        const [isloading, setIsloading] = useState(false);

        const [mipNo, setMIPNo] = useState('')
        const [pipPeriod, setPIPPeriod] = useState('')
        const [appraisalNo, setAppraisalNo] = useState('')
        const [month, setMonth] = useState('')
        const [status, setStatus] = useState('')
        const [meetingDate, setMeetingDate] = useState('')

        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')
        const [ipaStatus, setIpaStatus] = useState('')
        const [ipaDate, setIpaDate] = useState('')
        const [ipaPerformanceYear, setIpaPerformanceYear] = useState('')
        const [ipaConvert, setIpaConvert] = useState('')
        const [appraisalPeriod, setAppraisalPeriod] = useState('')
        const [appraiserNo, setAppraiserNo] = useState('')
        const [appraiserName, setAppraiserName] = useState('')
        const [appraiserTitle, setAppraiserTitle] = useState('')
        const [documentType, setDocumentType] = useState('')
        const [performanceType, setPerformanceType] = useState('')
        const [stage, setStage] = useState('')
        const [lines, setLines] = useState([])
        const [st, setSt] = useState(0)
        const [isEdit, setIsEdit] = useState(false)

        const [totalScore, setTotalScore] = useState('')
        const [staffComment, setStaffComment] = useState('')
        const [lineManagerComment, setLineManagerComment] = useState('')
        const [nextMeetingDate, setNextMeetingDate] = useState('')

        const [targetAchievements, setTargetAchievements] = useState('')
        const [actualAchievements, setActualAchievements] = useState('')
        const [failedTargets, setFailedTargets] = useState('')
        const [reasonsForNotAchieving, setReasonsForNotAchieving] = useState('')
        const [planForClosingGap, setPlanForClosingGap] = useState('')

        const [lineEtag, setLineEtag] = useState('')
        const [lineSystemId, setLineSystemId] = useState('')

        const [modal_line, setmodal_line] = useState(false)
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
        }
        const tog_line = () => {
            setmodal_line(!modal_line);
            removeBodyCss();
        }
        

        const fetchData = () => {
            setIsloading(true)
            axios.get(MIP_X + "("+ id +")?$expand=improvementLines", HRConfig)
                .then(function(response){
                    axios.get(`${EMPLOYEES}?$filter=No eq '${response.data.EmployeeNo}'`, HRConfig).then((r)=>{ 
                        setEmpName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setEmpTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    axios.get(`${EMPLOYEES}?$filter=No eq '${response.data.Appraiser}'`, HRConfig).then((r)=>{ 
                        setAppraiserName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setAppraiserTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    setEmpNo(response.data.EmployeeNo)
                    setMIPNo(response.data.No)
                    setAppraiserNo(response.data.Appraiser)
                   
                    setPIPPeriod(response.data.PIPPeriod)
                    setAppraisalNo(response.data.PerformanceAppraisalNo)
                    setMonth(response.data.PeriodMonth)
                    setStatus(response.data.Status)
                    setMeetingDate(response.data.MeetingDate)
                    setLines(response.data.improvementLines)

                    setSystemId(response.data.SystemId)
                    setEtag(response.data['@odata.etag'])
                    
                    axios.get(`${PIPS}(${response.data.SystemId})`, HRConfig).then((r)=>{
                        setStaffComment(r.data.commentsbyStaff)
                        setLineManagerComment(r.data.commentsbyLineManager)
                        setTotalScore(r.data.totalScore)
                        setNextMeetingDate(r.data.nextMeetingDate)
                    }).catch((e)=>{
                        console.log(e)
                    }) 

                    setIsloading(false)
                }).catch(function (error) {
                    toastr.error(error);
                    setUserError(true);
                    setIsloading(false)
                })
                
        }
        useEffect(() => {
           fetchData();
        },[]);

       
        const handleCancelApproval = () => {
            setIsloading(true)
            const lNo = {'no': ipaNo}
            axios.post(CANCELIPAAPPROVAL, JSON.stringify(lNo), HRConfig)
                .then(function(response){      
                    if(response.status == '200'){
                        toastr.success(response.data.value); 
                        //history.push("/leave-plans");
                        //window.location.reload();
                        fetchData()
                    }else{
                        toastr.success("Could not Cancel Approval Request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error);
                    setIsloading(false)
                })
                
        }

        const handleDeleteMIP = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : etag
                }
              }
            axios.delete(`${MIP_X}(${systemId})`, Config).then((r)=>{
                setIsloading(false)
                toastr.success('MIP deleted')
                history.push('/monthly-improvement-plan')
            }).catch((e)=>{
                console.log(e)
                toastr.error(e)
                setIsloading(false)
            })
        }
        const handleUpdateLine = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lineEtag
                }
            }
            const data = {
                targetAchievements  : targetAchievements,
                actualAchievements  : actualAchievements,
                failedTargets       : failedTargets,
                reasonsForNotAchieving  : reasonsForNotAchieving,
                planForClosingGap       : planForClosingGap
            }            
            axios.patch(`${MIP_LINES}(${lineSystemId})`, JSON.stringify(data), Config).then((r)=>{
                setIsloading(false)
                toastr.success('IPA line updated')
                tog_line()
                fetchData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
                toastr.error(e)
            })
        }
        const handleAddLine = () =>{
            setIsloading(true)
            const data = {
                documentNo          : mipNo,
                targetAchievements  : targetAchievements,
                actualAchievements  : actualAchievements,
                failedTargets       : failedTargets,
                reasonsForNotAchieving  : reasonsForNotAchieving,
                planForClosingGap       : planForClosingGap
            }   
            //console.log(data)         
            axios.post(`${MIP_LINES}`, JSON.stringify(data), HRConfig).then((r)=>{
                setIsloading(false)
                toastr.success('Improvement plan line added')
                tog_line()
                fetchData()
            }).catch((e)=>{
                console.log(e)
                toastr.error(e)
                setIsloading(false)
            })
        } 
        const handleDeleteLine = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lineEtag
                }
              }
            axios.delete(`${MIP_LINES}(${lineSystemId})`, Config).then((r)=>{
                setIsloading(false)
                toastr.success('MIP line deleted')
                tog_line()
                fetchData()
            }).catch((e)=>{
                console.log(e)
                toastr.error(e)
                setIsloading(false)
            })
        }
        const handleUpdateMIP = () =>{
 
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : '*'
                }
            }
            const data = {
                meetingDate         : formatDate(meetingDate),
                periodMonth         : parseInt(month),
                totalScore          : parseInt(totalScore),
                commentsbyStaff     : staffComment,
                commentsbyLineManager   : lineManagerComment,
                nextMeetingDate         : formatDate(nextMeetingDate)
            } 
            setIsloading(true)
            console.log(data)
            axios.patch(`${PIPS}(${systemId})`, JSON.stringify(data), Config).then((r)=>{
                toastr.success('Details saved')
                setIsloading(false)
            }).catch((e)=>{
                console.log(e)
                toastr.error(e)
                setIsloading(false)
            }) 
          
        }
        const handleClickLineDetails = (key) =>{
            setIsEdit(true)
            tog_line()
            console.log(lines[key])
            setLineEtag(lines[key]['@odata.etag'])
            setLineSystemId(lines[key].SystemId)
        }
        const clearLineData = () =>{
            setTargetAchievements('')
            setActualAchievements('')
            setFailedTargets('')
            setReasonsForNotAchieving('')
            setPlanForClosingGap('')
            setLineEtag('')
            setLineSystemId('') 
        }
        const handleRegenerate = () =>{
            setIsloading(true)
            const lNo = {'no': mipNo}
            axios.post(url.REGENERATEMIP , JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    toastr.success('MIP Regenerated')
                    history.push(`/monthly-improvement-plan`)
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error);
                    console.log(error)
                    setIsloading(false)
                })
        }
        const handleDownload = async () => {
            const data = {no:mipNo}
            setIsloading(true)
            axios.post(url.PRINTMIP, JSON.stringify(data), HRConfig).then((r)=>{
              const urlx = process.env.REACT_APP_APACHE_URL + r.data.value
                axios({
                  url: urlx,
                  method: 'GET',
                  responseType: 'blob',
                })
                  .then(response => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'Monthly-Improvement-Plan.pdf');
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                    setIsloading(false)
                  })
                  .catch(error => {
                    console.error('Error downloading file:', error);
                    setIsloading(false)
                  });
              
              setIsloading(false)
            }).catch((e)=>{
              console.log(e)
              toastr.error(e)
              setIsloading(false)
            })
          }
        return (
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
            <div className="page-content">
            <MetaTags>
            <title>MIP Details | {APPNAME}</title>
            </MetaTags>
            <Container fluid={true}>
                <Breadcrumbs title={APPNAME} breadcrumbItem="Monthly Improvement Plan Details" />
                <div>


                                <Modal isOpen={modal_line} toggle={() => { tog_line(); }}  size="l" centered  backdrop={'static'}  >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">{isEdit?`Update/Delete MIP Line`:`Add MIP Line` }</h5>
                                    <button type="button"  onClick={() => { setmodal_line(false);  }}  className="close" data-dismiss="modal"aria-label="Close">
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                  <Row>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Target achievements</label>
                                                <textarea rows={3} className="form-control" value={targetAchievements} onChange={(e)=>setTargetAchievements(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Actual achievements</label>
                                                <textarea rows={3} className="form-control" value={actualAchievements} onChange={(e)=>setActualAchievements(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Failed targets</label>
                                                <textarea rows={3} className="form-control" value={failedTargets} onChange={(e)=>setFailedTargets(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Reason for not achieving</label>
                                                <textarea rows={3} className="form-control"value={reasonsForNotAchieving} onChange={(e)=>setReasonsForNotAchieving(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Plan for closing the performance gap</label>
                                                <textarea rows={3} className="form-control" value={planForClosingGap} onChange={(e)=>setPlanForClosingGap(e.target.value)}/>
                                            </div>
                                        </Col>
                                        
                                  </Row>
                                  </div>
                                  
                                  <div className="modal-footer justify-content-center">
                                  {isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-danger" onClick={()=>handleDeleteLine()}>Delete IPA line...</button> ))
                                  }
                                  {isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleUpdateLine()}>Update line details...</button> ))
                                  }
                                  {!isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleAddLine()}>Save line details...</button> ))
                                  }</div>
                                </Modal>
        

                
                <Row>
                <Col xl={4}>
                <Card>
                    <CardBody>
                    <CardTitle>Employee Details</CardTitle>
                            <Row>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Employee No</label>
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {empNo}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Name</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={empName}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Title</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={empTitle}  />
                                
                                </div>
                            </Col>
                            </Row>
                           
                        </CardBody>
                    </Card>
                    <Card>
                        <CardBody>
                            <CardTitle>Appraiser</CardTitle>
                            <Row>
                      
                      
                   
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Employee No</label>
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {appraiserNo}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Name</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserName}   />
                                
                                </div>
                            </Col>
                    
                
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Title</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserTitle}  />
                                
                                </div>
                            </Col>
                            
                        </Row>
                        </CardBody>
                    </Card>

                    </Col>
                    <Col  xl={8}>
                    
                    <Card>
                        <CardBody>
                        <CardTitle className="mb-3">MIP Details</CardTitle>
                        <Row>
                        <Col md={6}>
                                <div className="mb-3">
                                <label>MIP No</label>
                                <input  type="text" className="form-control dNo" defaultValue={mipNo} disabled={true}  />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Appraisal No</label>
                                <input  type="text" className="form-control c2A" defaultValue={appraisalNo} disabled={true}   />
                                
                                </div>
                            </Col>
                    
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Period Month</label>
                                <input  type="number" className="form-control sT" value={month} onChange={(e)=>setMonth(e.target.value)}  />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Meeting date</label>
                                <Flatpickr
                                    className="form-control"
                                    options={{
                                        altInput: true,
                                        altFormat: "F j, Y",
                                        dateFormat: "Y-m-d",
                                        disabled: false
                                    }}
                                    value={meetingDate}
                                    onChange={(date) => setMeetingDate(date)}
                                    />
                                </div>
                            </Col>
                        
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Status</label>
                                <input  type="text" className="form-control pY" defaultValue={status} disabled={true}   />
                                
                                </div>
                            </Col>
                           
                            </Row>
                        
                        </CardBody>
                    </Card>

                    <Card>
                        <CardBody>
                        <CardTitle className="mb-3">General Comments</CardTitle>
                        <Row>
                        
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Staff Comment</label>
                                <textarea rows={4} className="form-control"  value={staffComment} onChange={(e)=>setStaffComment(e.target.value)} />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Line Manager Comment</label>
                                <textarea rows={4} className="form-control" value={lineManagerComment} onChange={(e)=>setLineManagerComment(e.target.value)}  />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Total Score</label>
                                <input  type="number" className="form-control" value={totalScore} onChange={(e)=>setTotalScore(e.target.value)}   />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label> Next Meeting date</label>
                                <Flatpickr
                                    className="form-control"
                                    options={{
                                        altInput: true,
                                        altFormat: "F j, Y",
                                        dateFormat: "Y-m-d",
                                        disabled: false
                                    }}
                                    value={nextMeetingDate}
                                    onChange={(date) => setNextMeetingDate(date)}
                                    />
                                </div>
                            </Col>
                            
                        
                          
                            </Row>
                        
                        </CardBody>
                    </Card>

                   
                   
                        
                        <div>
                                {(lines || []).map((formRow, key) => (
                                    
                              <Card key={key}>
                                <CardBody>
                                <div>
                                    <Row className="mb-3 align-items-center">
                                    <Col sm={(status == 'Open') ? 9:12}>
                                        <div className="mt-2">
                                        <h5>MIP Line {key+1}</h5>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Target achievements:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.targetAchievements}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Actual achievements:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.actualAchievements}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Failed targets:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.failedTargets}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Reason for failing to achieve targets:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.reasonsForNotAchieving}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Plan for closing the gaps:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.planForClosingGap}</Col>
                                         </Row>
                                  
                                        </div>
                                    </Col>
                                    {(status == 'Open') && (
                                    <Col sm={3}>
                                        <div className="float-sm-end d-flex align-items-center">
                
                                            
                                                <Button
                                                    type="button"
                                                    color="info"
                                                    className="btn-sm btn-rounded"
                                                    onClick={()=>handleClickLineDetails(key)}
                                                    >
                                                    Edit/Delete MIP Line
                                                </Button>
                                            
                                   
                                        </div>
                                    </Col>
                                    )}
                                    </Row>
                                </div>
                        
                
                                    </CardBody>
                                    </Card>
                                    
                                ))}
                                <Row className='justify-content-center'>
                                {(status == 'Pending Approval') && (
                                    <Col md={3}>
                                        <div className="d-grid gap-2">
                                        
                                            <input
                                                type="button"
                                                className="btn btn-danger mt-3 mt-lg-0"
                                                value="Cancel Approval Request"
                                                onClick={handleCancelApproval}
                                            />
                                      
                                        </div>
                                    </Col>
                                    ) }
                                    {(status == 'Open') && (
                                        <>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn" onClick={handleDeleteMIP} >
                                                    Delete MIP
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <Button color="warning" type="button" className="btn" onClick={handleUpdateMIP} >
                                                    Update MIP
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" onClick={()=>{setIsEdit(false); clearLineData(); tog_line()}} >
                                                    Add MIP Line
                                                </Button>
                                            </div>
                                        </Col>
                                        {/*<Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <input
                                                    type="button"
                                                    className="btn btn-success"
                                                    value="Send For Approval"
                                                    onClick={handleSendForApproval}
                                                />
                                            </div>
                                        </Col>*/}
                                        </>
                                    ) }
                                    <Col md={3} className='mb-2'>
                                        <div className="d-grid gap-2">
                                            <Button color="primary" type="button" className="btn" onClick={handleRegenerate} >
                                                Regenerate MIP
                                            </Button>
                                        </div>
                                    </Col>
                                    <Col md={3} className='mb-2'>
                                        <div className="d-grid gap-2">
                                            <Button color="primary" type="button" className="btn" onClick={handleDownload} >
                                                Print MIP
                                            </Button>
                                        </div>
                                    </Col>
                                    
                                    </Row>

                                    </div>
                                    <br/>
      
                                   
            
                </Col>
                </Row>

                </div>


                
                   

            </Container>
        </div>
        </LoadingOverlay>
        );
    }

export default PIPDetails;