import React from "react";
import { Row, Col, Card, CardBody } from "reactstrap";
import SimpleBar from "simplebar-react"
import ReactApexChart from "react-apexcharts";
import PropTypes from 'prop-types';

const SalesAnalytics = props => {
  const series = [56, 38, 26,94];
  const options = {
    labels: ["Operations", "Maintenance", "Human Resources","Legal"],
    colors: ["#556ee6", "#34c38f", "#f46a6a","#f1b44c"],
    legend: { show: !1 },
    plotOptions: {
      pie: {
        donut: {
          size: "100%",
        },
      },
    },
  };

  return (
    <React.Fragment>
      <Col xl="4">
        
        <Card>
          <CardBody>
          <h4 className="card-title mb-4">Contracts</h4>
          <SimpleBar style={{ maxHeight: "250px" }}>
              <ol className="list-group" type="1">
                {props.contracts && props.contracts.map((c,k)=>(
                  <li className="font-size-11" key={k}>{c.ContractExpirationText}</li>
                ))}
                {props.contracts && (props.contracts.length < 1) && (
                  <li className="font-size-14" >No Pending Contract Notification</li>
                )}
              </ol>
          </SimpleBar>
          </CardBody>
        </Card>
      </Col>
    </React.Fragment>
  );
};
SalesAnalytics.propTypes = {
  contracts: PropTypes.any
};

export default SalesAnalytics;
