import React, {useEffect, useState} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Container } from "reactstrap";
import { useHistory } from 'react-router-dom';
import {
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    Badge,
    Form,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
  } from "reactstrap"
  import { Link } from "react-router-dom"
  //Import Flatepicker
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';


import axios from 'axios';
import { HRConfig, formatDate, TOAST_OPTIONS } from "../../helpers/backend_helper"
import { APPNAME, EXIT_INTERVIEW } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
   
    const ListExitINterviews = () => {

      const history = useHistory()
      toastr.options = TOAST_OPTIONS;
      
      const [eIList, setEIList] = useState([])
      
      useEffect(() => {
        let obj = JSON.parse(localStorage.getItem("authUser"));
        let EmployeeNo = obj.uid;
        axios.get(`${EXIT_INTERVIEW}?$filter=EmployeeNo eq '${EmployeeNo}'`, HRConfig).then(function(response){ setEIList(response.data.value);   })
      }, []);
      const handleExitInterviewDelete = () => {
        alert('Deleted')
      }
      const { SearchBar } = Search;
      const columns = [
        {
        dataField: 'Date',
        text: 'Date',
        sort: true
        },
      {
        dataField: 'No',
        text: 'Exit No',
        sort: true,
        formatter: noFormatter
      },
      {
        dataField: 'EmployeeNo',
        text: 'Employee No',
        sort: true
      }, 
      {
        dataField: 'EmployeeName',
        text: 'Name',
        sort: true
      },  
      {
        dataField: 'PositionName',
        text: 'Position',
        sort: true
      }, 
     
      {
        dataField: 'Status',
        text: 'Status',
        sort: true,
        formatter: statusFormatter
      },
      {
        dataField: "action",
        isDummyField: true,
        text: "",
        formatter: actionFormatter
      }];

      const defaultSorted = [{
        dataField: 'No',
        order: 'desc'
      }];

      function noFormatter(cellContent, row){
        return(
          <strong>{cellContent}</strong>
        )
      }

      function actionFormatter(cellContent, row){
        return(
         <>
            <div className="d-flex gap-3">
            <UncontrolledDropdown direction="left">
                <DropdownToggle href="#" className="card-drop" tag="i">
                  <i className="mdi mdi-dots-horizontal font-size-18" />
                </DropdownToggle>
                <DropdownMenu className="dropdown-menu-end">
                  <DropdownItem
                    onClick={()=>{ history.push(`/exit-interview-details/${row.SystemId}`); }}
                  >
                    <i className="fas fa-address-card text-warning me-1" />
                    Interview Details
                  </DropdownItem>
                  <DropdownItem
                    onClick={() => handleExitInterviewDelete(row)}
                  >
                    <i className="mdi mdi-delete text-danger me-1" />
                    Delete
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </div>
          </>
        )
      }
      function statusFormatter(cellContent, row) {
        if (row.Status == "Pending Approval") {
          return (
            <div>
                <Badge className="me-2 bg-danger">
                { cellContent }
                </Badge>
            </div>
          );
        }else if (row.Status == "Approved"){
          return (
            <div>
                <Badge className="me-2 bg-success">
                { cellContent }
                </Badge>
            </div>
          );
        }else{
          return (
            <div>
                <Badge className="me-2 bg-warning">
                { cellContent }
                </Badge>
            </div>
          );
        }
      }

        return (
            <>
                <div className="page-content">
                    <MetaTags>
                    <title>Exit Interviews | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Exit Interviews" />               
                    
        <Row>
            <Col lg={12}>
              <Card>
                <CardBody>
                  <CardTitle></CardTitle>
                  <CardSubtitle className="mb-3">
                    
                  </CardSubtitle>

                 {eIList && (eIList.length > 0) && (
                  <ToolkitProvider
                      keyField="No"
                      data={ eIList }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className="mb-2">
                              <Col sm="4">
                                <div className="search-box me-2 mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="8">
                               
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                <div className="table">
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ eIList } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table align-middle table-nowrap table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             </div>
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  

                  )}
                  {eIList && (eIList.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Exit Interview Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                  </div>

                  )}
                  
                </CardBody>
              </Card>
            </Col>
          </Row>
                        
                           

                    </Container>
                </div>
            </>
        );
    }

export default ListExitINterviews;