import React, {useEffect, useState} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Container } from "reactstrap";
import { useHistory } from 'react-router-dom';
import {
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    Badge,
    Form,
    Button
  } from "reactstrap"
  import { Link } from "react-router-dom"
  //Import Flatepicker
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';


import axios from 'axios';
import { HRConfig, formatDate, TOAST_OPTIONS } from "../../helpers/backend_helper"
import { LEAVE_PLAN, COST_CENTERS, LEAVE_PLAN_X, APPNAME  } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
   
    const LeavePlans = () => {

      const history = useHistory()
      toastr.options = TOAST_OPTIONS;
      
      const [leavePlans, setLeavePlans] = useState([]);
      const [isLoading, setIsLoading] = useState(false)
  
      
      useEffect(() => {
        let obj = JSON.parse(localStorage.getItem("authUser"));
        let EmployeeNo = obj.uid;
        setIsLoading(true)
        axios.get(LEAVE_PLAN_X+"?$filter=EmployeeNo eq '"+EmployeeNo+"'&$expand=leavePlanLines", HRConfig).then((r)=>{
          setLeavePlans(r.data.value);
          setIsLoading(false)
        }).catch((e)=>{
          console.log(e)
          setIsLoading(false)
        })
      }, []);

      
    

      const { SearchBar } = Search;
      const columns = [
      {
        dataField: 'No',
        text: 'Plan No',
        sort: true,
        formatter: noFormatter
      },
      {
        dataField: 'EmployeeNo',
        text: 'No',
        sort: true
      }, 
      {
        dataField: 'EmployeeName',
        text: 'Name',
        sort: true
      },  
      {
        dataField: 'PostingDate',
        text: 'Posting Date',
        sort: true
      }, 
      {
        dataField: "planlines",
        isDummyField: true,
        text: "Plan Lines",
        formatter: linesFormatter
      },
      {
        dataField: 'Status',
        text: 'Status',
        sort: true,
        formatter: statusFormatter
      },
      {
        dataField: "action",
        isDummyField: true,
        text: "",
        formatter: actionFormatter
      }];

      const defaultSorted = [{
        dataField: 'No',
        order: 'desc'
      }];

      function linesFormatter(cellContent, row){
        return(
          <strong>{row.leavePlanLines.length}</strong>
        )
      }
      function noFormatter(cellContent, row){
        return(
          <strong>{cellContent}</strong>
        )
      }

      function actionFormatter(cellContent, row){
        return(
          <Button
            type="button"
            color="info"
            className="btn-sm btn-rounded"
            onClick={()=>{ history.push(`/leave-plan-details/${row.SystemId}`); }}
          >
            Details
          </Button>
        )
      }
      function statusFormatter(cellContent, row) {
        if (row.Status == "Pending Approval") {
          return (
            <div>
                <Badge className="me-2 bg-danger">
                { cellContent }
                </Badge>
            </div>
          );
        }else if (row.Status == "Approved"){
          return (
            <div>
                <Badge className="me-2 bg-success">
                { cellContent }
                </Badge>
            </div>
          );
        }else{
          return (
            <div>
                <Badge className="me-2 bg-warning">
                { cellContent }
                </Badge>
            </div>
          );
        }
      }

        return (
            <>
              <LoadingOverlay
                active={isLoading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Leave | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Leave Plans" />

                                 
                    
        <Row>
            <Col lg={12}>
              <Card>
                <CardBody>
                  <CardTitle></CardTitle>
                  <CardSubtitle className="mb-3">
                    
                  </CardSubtitle>

                 {leavePlans && (leavePlans.length > 0) && (
                  <ToolkitProvider
                      keyField="No"
                      data={ leavePlans }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className='mb-5'>
                              <Col lg={8}>
                                  <div  className="rounded bg-light d-grid gap-2">
                                    <SearchBar {...toolkitProps.searchProps} className="form-control bg-transparent border-0 searchTm m-0" />
                                  </div>
                              </Col>
                              <Col lg={4}> 
                                <div className="d-grid gap-2">
                                  <Link
                                    className="btn btn-primary"
                                    to="/add-leave-plan"
                                  >
                                    <i className="mdi mdi-plus me-1" />
                                    Add Leave Plan
                                  </Link>
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                <div className="table">
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ leavePlans } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table align-middle table-nowrap table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             </div>
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  

                  )}
                  {leavePlans && (leavePlans.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Leave Plan Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                      <div className="text-center mt-4">
                          <Link
                          to="/add-leave-plan"
                          className="btn btn-primary  btn-sm"
                          >
                          Create Leave Plan <i className="mdi mdi-arrow-right ms-1" />
                          </Link>
                      </div>
                  </div>

                  )}
                  
                </CardBody>
              </Card>
            </Col>
          </Row>
                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default LeavePlans;