import React, {useState} from "react";
import MetaTags from 'react-meta-tags';
import { Link } from "react-router-dom";
import { Col, Container, Form, Row, Input, Label, FormFeedback } from "reactstrap";

import axios from "axios";
import * as url from "../../helpers/url_helper"
import toastr from "toastr";
import "toastr/build/toastr.min.css";
import { TOAST_OPTIONS2, HRConfig2 } from "helpers/backend_helper";
import { useHistory } from "react-router-dom";

// import images
import logodark from "../../assets/images/logo-dark.png";
import logolight from "../../assets/images/logo-light.png";
import CarouselPage from "./CarouselPage";

const PublicApplicantsLogin = () => {
    const history = useHistory();
    toastr.options = TOAST_OPTIONS2;
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const handleSubmit = () =>{
        //console.log(`${email} - ${password}`)
        const data = {
            "email": email,
            "password": password
        }
        axios.post(url.APPLICANT_LOGIN, JSON.stringify(data), HRConfig2).then((r)=>{
            if(r.data.status == "1"){
                history.push("/applications")
                localStorage.setItem("authApplicant", JSON.stringify(r.data.applicant))
                toastr.success('logging in...')
            }else{
              toastr.error(r.data.message)
            }
        }).catch((e)=>{
            console.log(e)
        })
    }

  return (
    <React.Fragment>
      <div>
        <MetaTags>
          <title>Login | UEGCL</title>
        </MetaTags>
        <Container fluid className="p-0">
          <Row className="g-0">
            <CarouselPage />

            <Col xl={3}>
              <div className="auth-full-page-content p-md-5 p-4" style={{ backgroundColor: 'white' }}>
                <div className="w-100">
                  <div className="d-flex flex-column h-100">
                    <div className="mb-4 mb-md-5">
                      <Link to="#" className="d-block auth-logo">
                        <img
                          src={logodark}
                          alt=""
                          height="78"
                          className="auth-logo-dark"
                        />
                        <img
                          src={logolight}
                          alt=""
                          height="78"
                          className="auth-logo-light"
                        />
                      </Link>
                    </div>
                    <div className="my-auto">
                      <div>
                        <p className="text-muted">
                          Sign in to continue with your job application...
                        </p>
                      </div>
                     

                      <div className="mt-4">
                        
                          <div className="mb-3">
                            <Label className="form-label">Email</Label>
                            <Input
                              className="form-control"
                              type="email"
                              onChange={(e)=>setEmail(e.target.value)}
                            />
                 
                          </div>

                          <div className="mb-3">
                            <Label className="form-label">Password</Label>
                            <Input
                              type="password"
                              onChange={(e)=>setPassword(e.target.value)}
                            />
                     
                          </div>

                          <div className="mt-4 d-grid">
                            <button
                              className="btn btn-primary btn-block "
                              type="button"
                              onClick={handleSubmit}
                            >
                              Log In
                            </button>
                          </div>

                        

                          <div className="mt-4 text-center">
                            <Link to="/applicant-reset-password" className="text-muted">
                              <i className="mdi mdi-lock me-1" />
                              Forgot your password?
                            </Link>
                          </div>
                        <div className="mt-4 text-center">
                          <p>
                            
                            <Link
                              to="/applicants-registration"
                              className="fw-medium text-primary"
                            >
                              Create account
                            </Link>
                          </p>
                        </div>
                      </div>
                    </div>

                    <div className="mt-4 mt-md-5 text-center">
                      <p className="mb-0">
                         {new Date().getFullYear()} UEGCL
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default PublicApplicantsLogin;
