import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle, Modal, Form, Container, Button, Alert, UncontrolledTooltip, } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'



    const LeavePlanDetails = () => {
        const history = useHistory();
        const {id} = useParams();

        const [userError, setUserError] = useState('');
        const [lPNo, setLPNo] = useState('');
        const [lPStatus, setLPStatus] = useState('');
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [empDept, setEmpDept] = useState('');
        const [employee, setEmployee] = useState([]);
        const [postingDate, setPostingDate] = useState('');
        const [leaveLines, setLeaveLines] = useState([]);
        const [isloading, setIsloading] = useState(false);
        const [cc, setCC] = useState('danger')
        const [leaveLine, setLeaveLine] = useState({})
        const [lineSelectedDates, setLineSelectedDates] = useState([])
        
        const [modal_item, setmodal_item] = useState(false);
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
          }
          const tog_item = () => {
            setmodal_item(!modal_item);
            removeBodyCss();
          }
        const handleEditLeaveLine = (ll) =>{
            setLeaveLine(ll)
            tog_item();
        }
        const handleUpdateLine = () =>{
            const data = {
                StartDate: formatDate(lineSelectedDates[0]),
                EndDate: formatDate(lineSelectedDates[1])
            }
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : leaveLine['@odata.etag']
                }
              }
            axios.patch(`${url.LEAVE_PLAN_LINES}(${leaveLine.SystemId})`, JSON.stringify(data), Config).then((r)=>{ 
                toastr.success(`Leave Plan Line Updated`); 
                tog_item()
                history.go(0)
            }).catch((e)=>{ console.log(e) })
    
        }
        const handleDeleteLine = () =>{
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : leaveLine['@odata.etag']
                }
              }
            axios.delete(`${url.LEAVE_PLAN_LINES}(${leaveLine.SystemId})`,  Config).then((r)=>{ 
                toastr.success(`Leave Plan Line Deleted`); 
                tog_item()
                history.go(0)
            }).catch((e)=>{ console.log(e) })
        }

        useEffect(() => {
            setIsloading(true)
            axios.get(url.LEAVE_PLAN_X + "("+ id +")?$expand=leavePlanLines", HRConfig)
                .then(function(response){
                    setUserError(false);
                    setLPNo(response.data.No)
                    setEmpNo(response.data.EmployeeNo)
                    setEmpName(response.data.EmployeeName)
                    setEmpDept(response.data.ShortcutDimension1Code)
                    setLPStatus(response.data.Status);
                    (lPStatus == 'Open') ? setCC('danger') : (lPStatus == 'Pending Approval') ? setCC('warning') : setCC('success');
                    setPostingDate(response.data.PostingDate)
                    setLeaveLines(response.data.leavePlanLines)
                    setIsloading(false)
                    axios.get(url.EMPLOYEES + "?$filter=No eq '"+ response.data.EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpTitle(response.data.value[0].JobTitle);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                    })
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setUserError(true);
                    setIsloading(false)
                    console.log(error)
            })
        },[]);

        const [inputs, setInputs] = useState([]);
        toastr.options = TOAST_OPTIONS;
  
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            event.preventDefault();
           
          
        }  
       
        const handlePostPlan = () => {
            setIsloading(true)
            const lNo = {'no': lPNo}
            axios.post(url.SEND_LP_APPROVAL_REQUEST , JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("Leave Plan Sent for Approval Request" ); 
                        window.location.reload();
                    }else{
                        toastr.success("Could not Post leave plan" )
                    }
                    setIsloading(false)
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
            })
        }  
        const handleCancelApprovalRequest = () => {
            setIsloading(true)
            const lNo = {'no': lPNo}
            axios.post(url.CANCEL_LP_APPROVAL_REQUEST, JSON.stringify(lNo), HRConfig)
                .then(function(response){      
                    if(response.status == '200'){
                        toastr.success("Approval Request Cancelled" ); 
                        window.location.reload();
                    }else{
                        toastr.success("Could not Cancel Approval Request" )
                    }
                    setIsloading(false)
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
            })
    
        }
        const handleDeletePlan = (e) => {
            setIsloading(true)
            const lNo = {'no': lPNo}
            axios.post(url.DELETELP, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    console.log(response); 
                    if(response.status == '200'){
                        toastr.success(response.data.value);
                        history.push("/leave-plans")
                    }else{
                        toastr.success("Could not Delete leave plan" )
                    }
                   setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    console.log(error)
                    setIsloading(false)
                })
        }      
        

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>{lPNo} Leave Plan Details | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem={`${lPNo} Leave Plan Details`} />
                        <Form onSubmit={handleSubmit}>
                        <Alert color={(lPStatus == 'Approved') ? `success` : (lPStatus == 'Pending Approval') ? `warning` : `danger`} role="alert">
                                {lPStatus}      
                        </Alert>
                     
                       
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Employee Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control"  value = {empNo} onChange={handleChange} disabled={true} />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" defaultValue={empName} onChange={handleChange} disabled={true}  />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" defaultValue={empTitle} onChange={handleChange} disabled={true} />
                                        <label htmlFor="floatingDepartInput">Title</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                                <CardBody>
                           
                                <CardTitle className="mb-3">
                                
                                </CardTitle>
                                <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingDepartInput">Date Posted</label>
                                        <input name='postingdate'   type="text" className="form-control postingDate" defaultValue={postingDate} onChange={handleChange} disabled={true} />
                                        
                                        </div>
                                    </Col>
                                </Row>
                                
                                <div>
                                        {(leaveLines).map((formRow, key) => (
                                            <Row key={key}>
                        
                                            <Col lg={4} className="mb-3">
                                            <div className="mb-3">
                                            <label htmlFor="floatingSelectGrid">{key+1}. Leave Category</label>
                                                <select defaultValue="0" className="form-select lType" disabled={true}>
                                                <option>{formRow.LeaveType}</option>
                                                    <option>ANNUAL</option>
                                                    <option>W-OUT PAY</option>
                                                </select>
                                                
                                            </div>
                                            </Col>

                                            <Col lg={5} className="mb-3">
                                                <div className="mb-3">
                                                    <label htmlFor="floatingPlanningDateInput">Start Date - End Date</label>
                                                    <input type="text" className='form-control' defaultValue={`${formRow.StartDate} - ${formRow.EndDate}`} disabled={true}/>
                                                
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div className="mb-3">
                                                    <label htmlFor="floatingDepartInput">Days</label>
                                                    <input name='days'   type="text" className="form-control days" defaultValue={formRow.Quantity} onChange={handleChange} disabled={true}/>
                                                </div>
                                            </Col>
                                            <Col lg={5} className="mb-3">
                                            { (lPStatus == 'Open') && (
                                            <div className="form-floating d-flex gap-3 ">
                                            <button
                                                type="button"
                                                onClick={() =>{ handleEditLeaveLine(leaveLines[key]) }}
                                                className="btn btn-dark "
                                                data-dismiss="modal"
                                                >
                                                <i className="mdi mdi-pencil font-size-18" id="edittooltip" />
                                                {` `} Edit line
                                            </button>
                                               
                                            </div>
                                            )}
                                            </Col>
                                  
                                            
                                            </Row>
                                        ))}
                               
                                            </div>
                                            <Row>
                                                <Col md={12}>
                                                    <div className="mb-3">
                                                    <label htmlFor="floatingDepartInput">Status</label>
                                                    <input name='status'  type="text" className="form-control" disabled={true} defaultValue={lPStatus} onChange={handleChange} />
                                                    
                                                    </div>
                                                </Col>
                                            </Row>
                                            </CardBody>
                                            </Card>
                                            
                                            
                                                    <Row>
                                                    { (lPStatus == 'Open') && (
                                                        <>
                                                        <Col md={6}>
                                                            <div className="d-grid gap-2">
                                                                <Button color="danger" type="button" className="btn" onClick={handleDeletePlan}>
                                                                    Delete
                                                                </Button>
                                                            </div>
                                                        </Col>
                                                        
                                                        <Col md={6}>
                                                            <div className="d-grid gap-2">
                                                                <Button color="warning" type="button" className="btn" onClick={handlePostPlan}>
                                                                    Send Approval Request
                                                                </Button>
                                                            </div>
                                                        </Col>
                                                        </>
                                                        )}
                                                        { (lPStatus == 'Pending Approval') && (
                                                        <>
                                                            <Col md={12}>
                                                                <div className="d-grid gap-2">
                                                                    <Button color="danger" type="button" className="btn btnApprovePlan" onClick={handleCancelApprovalRequest}>
                                                                        Cancel Approval Request
                                                                    </Button>
                                                                </div>
                                                            </Col>
                                                          
                                                        </>
                                                        )}
                                                        { (lPStatus == 'Approved') && (
                                                        <Col md={12} color="success">
                                                            <div className="d-grid gap-2">
                                                                <h5>No Pending Action</h5>
                                                            </div>
                                                        </Col>
                                                        )}
                                                        
                                            
                                                    </Row>
                                       
                                            
                    
                                </Col>
                                </Row>
                                </Form>

                                <Modal
                                  isOpen={modal_item}
                                  toggle={() => {
                                    tog_item()
                                  }}
                                  centered
                                >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Update LP Line</h5>
                                    <button
                                      type="button"
                                      onClick={() => {
                                        setmodal_item(false);
                                      }}
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                  <Row >
                                    <Col lg={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingDepartInput">Leave Type</label>
                                        <input  type="text" className="form-control" defaultValue={leaveLine.LeaveType} disabled={true}  />  
                                        </div>
                                    </Col>
                                    <Col lg={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingDepartInput">Description</label>
                                        <input  type="text" className="form-control" defaultValue={leaveLine.Description}  disabled={true} />  
                                        </div>
                                    </Col>
                                    <Col lg={12}>
                                    <div className="mb-3">
                                        <label htmlFor="floatingSelectGrid">Start Date - End Date</label>
                                        <Flatpickr
                                            className="form-control"
                                            options={{
                                                mode: 'range',
                                                dateFormat: 'Y-m-d',
                                                defaultDate: [ new Date(`${leaveLine.StartDate}`), new Date(`${leaveLine.EndDate}`)]
                                            }}
                                            //value={selectedDates}
                                            onChange={(dates) =>{setLineSelectedDates(dates)} }
                                        />
                                        
                                    </div>
                                    </Col>
                                    
                                    
                                
                                    </Row>
                      
                                  </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      onClick={() => {  tog_item() }}
                                      className="btn btn-dark "
                                      data-dismiss="modal"
                                    >
                                      Cancel
                                    </button>
                                    <button
                                      type="button"
                                      onClick={() => {  handleDeleteLine() }}
                                      className="btn btn-danger "
                                      data-dismiss="modal"
                                    >
                                      Delete Plan Line
                                    </button>
                                    <button
                                      type="button"
                                      className="btn btn-success "
                                      onClick={()=>{handleUpdateLine()}}
                                    >
                                      Update Plan Line...
                                    </button>
                                  </div>
                                </Modal>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default LeavePlanDetails;