import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Select from 'react-select';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
import { Link } from 'react-router-dom';

    const AddPaymentRequisition = () => {
        const history = useHistory();

        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');


        const [categories, setCategories] = useState([])
        const [subCategories, setSubCategories] = useState([])
        const [payeeTypes, setPayeeTypes] = useState(['Customer', 'Vendor', 'Bank', 'Employee', 'Imprest'])
        const [payeeOptions, setPayeeOptions] = useState([])
        const [currencyList, setCurrencyList] = useState([])
        const [supplierDocTypeList, setSupplierDocTypeList] = useState(['LPO', 'Call-off Order', 'Direct Invoice', 'Contract'])
        const [docOptions, setDocOptions] = useState([])
        const [costCenterList, setCostCenterList] = useState([])
        const [projectList, setProjectList] = useState([])
        const [approverList, setApproverList] = useState([])
        const [accType, setAccType] = useState(['G/L Account', 'Vendor', 'Customer', 'Bank Account'])
       
        const [paymentNo, setPaymentNo] = useState('')
        const [selectedPayee, setSelectedPayee] = useState('')
        const [payeeType, setPayeeType] = useState('')
        const [selectedDoc, setSelectedDoc] = useState('')
        const [paymentCategory, setPaymentCategory] = useState('')
        const [paymentSubCategory, setPaymentSubCategory] = useState('')
        const [purpose, setPurpose] = useState('')
        const [currencyCode, setCurrencyCode] = useState('')
        const [costCenter, setCostCenter] = useState('')
        const [projectCode, setProjectCode] = useState('')
        const [payeeNo, setPayeeNo] = useState('')
        const [payeeName, setPayeeName] = useState('')
        const [supplierDocNo, setSupplierDocNo] = useState('')
        const [supplierDocName, setSupplierDocName] = useState('')
        const [approverCode, setApproverCode] = useState('')
        const [amount, setAmount] = useState('')

        const [selectedType, setSelectedType] = useState('')
        const [selectedNo, setSelectedNo] = useState(null)
        const [description, setDescription] = useState('')
        const [quantity, setQuantity] = useState('')
        const [rate, setRate] = useState('')

        const [paymentRequisitionLines, setPaymentRequisitionLines] = useState([])

        const [noo, setNoo] = useState([])

        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')

        const [showPayeeNo, setShowPayeeNo] = useState(false)
        const [showSupplier, setShowSupplier] = useState(false)

        const [modal_item, setmodal_item] = useState(false);
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
          }
          const tog_item = () => {
            setmodal_item(!modal_item);
            removeBodyCss();
          }

        toastr.options = TOAST_OPTIONS;
        
        const handleCategoryChange = (e) =>{
            setPaymentCategory(e.target.value)
            setSubCategories([]) 
            axios.get(`${url.PAYMENTSUBCATEGORIES}?$filter=paymentType eq '${e.target.value}'`, HRConfig).then((r)=>{ 
                setSubCategories(r.data.value)
            }).catch((e)=>{ console.log(e)})
        }
        const handlePayeeTypeChange = (e) =>{
            const type = e.target.value
            setSelectedPayee(null)
            setPayeeNo('')
            setPayeeName('')
            setPayeeOptions([])
            let options = []
            if(type == ""){
                setShowPayeeNo(false)
            }else{
                setShowPayeeNo(true)
            }
            if(type == "Customer"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_CUSTOMER}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if(type == "Vendor"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_VENDOR}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.vendorName,'value':v.vendorNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if(type == "Bank"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_BANK}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.bankName,'value':v.bankNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((type == "Employee")){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_IMPREST}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((type == "Imprest")){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_IMPREST}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
        }

        const handleSupplierDocTypeChange = (e) =>{
            const docType = e.target.value
            setSelectedDoc(null)
            setSupplierDocNo('')
            setSupplierDocName('')
            setDocOptions([])  
            let options = []
            if(docType == " "){ setShowSupplier(false); }else{ setShowSupplier(true) }
            if((docType == "LPO")){
                setIsloading(true)
                axios.get(`${url.LPOLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Call-off Order")){
                setIsloading(true)
                axios.get(`${url.CALLOFFORDERSLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Direct Invoice")){
                setIsloading(true)
                axios.get(`${url.DIRECTINVOICESLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Contract")){
                setIsloading(true)
                axios.get(`${url.CONTRACTSLIST}?$filter=contractorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
        }
        const handleDocChange = (doc)=>{
            setSelectedDoc(doc)
            setSupplierDocNo(doc.value)
            setSupplierDocName(doc.label)
        }
        const handleAddItem = () =>{
 
            const itemLine =  {
                "accountType": selectedType,
                "accountNo": selectedNo.value,
                "description": description,
                "quantity": parseInt(quantity),
                "rate": parseInt(rate) 
            }
            setPaymentRequisitionLines(state =>[...state, itemLine])
            tog_item()
        }
        const handleRemoveItem = (key) =>{
            const newItems = [...paymentRequisitionLines];
            newItems.splice(key, 1);
            setPaymentRequisitionLines(newItems);
        }

        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleTypeChange = (e) =>{
            setSelectedType(e.target.value)
            setSelectedNo(null)
            setIsloading(true)
            setSelectedPayee('')
            let options = []
            
            if(e.target.value == 'G/L Account'){
                axios.get(url.GLACCOUNTS, HRConfig).then((r)=>{
                    setIsloading(false)
                    r.data.value.map((a)=>{
                        options.push({label:a.Name, value:a.No})
                    })
                    setNoo(options)
                }).catch((e)=>{
                    setIsloading(false)
                    console.log(e)
                })
                
            }else if(e.target.value == "Vendor"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_VENDOR}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.vendorName,'value':v.vendorNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else if(e.target.value == "Bank Account"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_BANK}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.bankName,'value':v.bankNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else if(e.target.value == "Customer"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_CUSTOMER}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else{
                setIsloading(false) 
            }
        }
        const handleNoChange = (e)=>{
            setSelectedNo(e); 
            if(selectedType == 'Item'){
                axios.get(`${url.UNITOFMEASURE}?$filter=ItemNo eq '${e.value}'`, HRConfig).then((r)=>{setSelectedUnitOfMeasure(r.data.value[0].Code)}).catch((e)=>{ console.log(e)}) 
            }
        }
        
        const handleInitiate = () =>{
            if((paymentCategory.length < 2) || (paymentSubCategory.length < 2) ){
                toastr.error('Select payment category and sub category')
            }else{
                const data = {
                    requisitionedBy: empNo,
                    paymentCategory: paymentCategory,
                    paySubcategory: paymentSubCategory
                }
                setIsloading(true); 
                axios.post(url.PAYMENTREQUISITIONX, JSON.stringify(data), HRConfig)
                    .then(function(response){ 
                        console.log(response.data)
                        toastr.success("Payment Requisition No "+ response.data.paymentReqNo+" Initiated" ); 
                        history.push(`/payment-requisition-details/${response.data.SystemId}`)
                        setIpaNo(response.data.paymentReqNo)
                        setPaymentNo(response.data.paymentReqNo)
                        
                        setIsloading(false);
                   
                    })
                    .catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setIsloading(false);
                })
            }
        }
        const handleSubmit = () => {
            
            const data = {
                "requisitionedBy": empNo,
                "paymentCategory": paymentCategory,
                "paySubcategory": paymentSubCategory,
                "PayeeNo": payeeNo,
                "PayeeName": payeeName,
                "Purpose": purpose,
                "currencyCode": currencyCode,
                "supplierDocType": supplierDocName,
                "supplierDocNo": supplierDocNo,
                "costCenter": costCenter,
                "projectCode": projectCode,
                "approverCode": approverCode,
                "paymentReqSubform": paymentRequisitionLines
            };
            
            setIsloading(true); 
            axios.post(url.PAYMENTREQUISITIONX, JSON.stringify(data), HRConfig)
                .then(function(response){ 
                    if(response.data.paymentReqNo){
                        toastr.success("Payment Requisition No "+ response.data.paymentReqNo+" created" ); 
                        setIpaSaved(true);
                        setIpaNo(response.data.paymentReqNo)
                        setIsloading(false);
                    }else{
                        toastr.success("Could not Create Payment Requisition" )
                        setIsloading(false);
                    }
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false);
            })
          
        }

        useEffect(() => {
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                const EmployeeNo = obj.uid;
                setIsloading(true)
                axios.get(url.EMPLOYEES + "?$filter=No eq '"+ EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            setUserError(false);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                        setIsloading(false)
                    }).catch(function (error) {
                        setUserError(true);
                        console.log(error);
                        setIsloading(false)
                }) 
            }
            axios.get(url.PAYMENTCATEGORIES, HRConfig).then((r)=>{ 
                setCategories(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.COSTCENTERCODES, HRConfig).then((r)=>{ 
                setCostCenterList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.PROJECTCODES, HRConfig).then((r)=>{ 
                setProjectList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.CURRENCYLIST, HRConfig).then((r)=>{ 
                setCurrencyList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.APPROVERCODELIST, HRConfig).then((r)=>{ 
                setApproverList(r.data.value)
            }).catch((e)=>{ console.log(e)})
        },[]);
        
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Payment Requisition | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Payment Requisition" />
                        <div>
                        {userError && ( 
                        <Alert color="danger" role="alert">
                                    Your Employee Number Not Found in Dynamics 365 Business Central
                        </Alert>
                        )}

                        
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Requisitioner</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>No</label>
                                            <input type="text" className="form-control" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>Name</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        </div>
                                    </Col>
                                    {/*<Col md={12}>
                                        <div className="mb-3">
                                            <label>Title</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        </div>
                                    </Col>*/}
                                   
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                            <CardBody>
                            <CardTitle>Payment Requisition Type</CardTitle>
                                <Row>
                                <Col md={12}>
                                    <div className="mb-3">
                                        <label>Payment Category</label>
                                        <select className="form-select" onChange={(e)=>handleCategoryChange(e)}>
                                            <option value={``}></option>
                                            {categories.map((c,k)=>(
                                                <option key={k} value={c.Code}>{c.Description}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                <Col md={12}>
                                <div className="mb-3">
                                        <label>Payment SubCategory</label>
                                        <select className="form-select" onChange={(e)=>setPaymentSubCategory(e.target.value)} >
                                            <option value={``}></option>
                                            {subCategories.map((c,k)=>(
                                                <option key={k} value={c.code}>{c.name}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                <Row className='justify-content-center'>
                                    
                                            <Col md={4} className='mb-2 mt-3'>
                                                <div className="d-grid gap-2">
                                                    <Button color="primary" type="button" className="btn" onClick={handleInitiate}>
                                                        Initiate Payment
                                                    </Button>
                                                </div>
                                            </Col>
                                           
                                    </Row>
                                
                                
                                </Row>
                                </CardBody>
                                </Card>
                                </Col>
                                </Row>
                        </div>
                    </Container>
                </div>
                </LoadingOverlay>
         

        );
    }

export default AddPaymentRequisition;