import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { LEAVE_PLAN, EMPLOYEES, APPNAME } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
LoadingOverlay.propTypes = undefined
import BounceLoader from 'react-spinners/BounceLoader'
import SweetAlert from "react-bootstrap-sweetalert"


    const AddLeavePlan = () => {
        const history = useHistory();
        const [rows1, setrows1] = useState([{ id: 1 }]);

        function handleAddRowNested() {
            const modifiedRows = [...rows1];
            modifiedRows.push({ id: modifiedRows.length + 1 });
            setrows1(modifiedRows);
        }

        function handleRemoveRow(id) {
            if (id !== 1) {
            var modifiedRows = [...rows1];
            modifiedRows = modifiedRows.filter(x => x["id"] !== id);
            setrows1(modifiedRows);
            }
        }

        const [formRows, setFormRows] = useState([{ id: 1 }]);

        const onAddFormRow = () => {
            const modifiedRows = [...formRows];
            modifiedRows.push({ id: modifiedRows.length + 1 });
            setFormRows(modifiedRows);
        };

        const onDeleteFormRow = id => {
            if (id !== 1) {
            var modifiedRows = [...formRows];
            modifiedRows = modifiedRows.filter(x => x["id"] !== id);
            setFormRows(modifiedRows);
            }
        };

        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [empDept, setEmpDept] = useState('');
        const [employee, setEmployee] = useState([]);
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [lPSaved, setLPSaved] = useState(false)
        const [lPNo, setLPNo] = useState('')

        toastr.options = TOAST_OPTIONS;
  
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            setIsloading(true);
            
            const planLs = [];
            
            for(let i = 0; i < formRows.length; i++){
                const plan = {
                    "LeaveType":  document.getElementsByClassName('lType')[i].value,
                    "StartDate":  formatDate(document.getElementsByClassName('lDates')[i]._flatpickr.selectedDates[0]) ,
                    "EndDate":    formatDate(document.getElementsByClassName('lDates')[i]._flatpickr.selectedDates[1]) ,
                }
                planLs.push(plan);
            }
            const submittedPlan = {
                "EmployeeNo": document.getElementById('EmployeeNo').value,
                "leavePlanLines": planLs 
            };
            axios.post(LEAVE_PLAN, JSON.stringify(submittedPlan), HRConfig)
                .then(function(response){     
                    if(response.data.No){
                        toastr.success("Leave Plan No "+ response.data.No+" created" ); 
                        setLPSaved(true);
                        setLPNo(response.data.No)
                        setIsloading(false);
                    }else{
                        toastr.success("Could not Create leave Plan" )
                        setIsloading(false);
                    }
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false);
            })
            
        }
        const submit = () =>{
            setconfirm_alert(true)
        }

        useEffect(() => {
            setIsloading(true)
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                const EmployeeNo = obj.uid;
                axios.get(EMPLOYEES + "?$filter=No eq '"+ EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            setUserError(false);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                        setIsloading(false)
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                        setIsloading(false)
                })
            }
        },[]);

        const [confirm_alert, setconfirm_alert] = useState(false)

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                {confirm_alert ? (
                    <SweetAlert
                      title="Are you sure?"
                      warning
                      showCancel
                      confirmButtonText="Yes, Submit!"
                      confirmBtnBsStyle="success"
                      cancelBtnBsStyle="danger"
                      onConfirm={() => {
                        setconfirm_alert(false)
                        handleSubmit()
                      }}
                      onCancel={() => setconfirm_alert(false)}
                    >
                      You won&apos;t be able to revert this!
                    </SweetAlert>
                  ) : null}

                <div className="page-content">
                    <MetaTags>
                    <title>Add Leave Plan | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Add Leave Plan" />
                        <div>
                        {userError && ( 
                        <Alert color="danger" role="alert">
                                    Your Employee Number Not Found in Dynamics 365 Business Central
                        </Alert>
                        )}

                        {(lPSaved == false) ? (
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Employee Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        <label htmlFor="floatingDepartInput">Title</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                       
                                
                                <div>
                                        {(formRows || []).map((formRow, key) => (
                                    <Card key={key}>
                                        <CardBody>
                                
                                        <CardTitle className="mb-3">
                                        Add Leave Line {formRow.id} 
                                        </CardTitle>
                                            <Row >
                        
                                            <Col lg={5} className="mb-3">
                                            <div className="mb-3">
                                            <label htmlFor="floatingSelectGrid">Leave Category</label>
                                                <select defaultValue="0" className="form-select lType">
                                                    <option>ANNUAL</option>

                                                    <option>W-OUT PAY</option>
                                                </select>
                                                
                                            </div>
                                            </Col>

                                            <Col lg={5} className="mb-3">
                                                <div className="mb-3">
                                                <label htmlFor="floatingPlanningDateInput">Start Date - End Date</label>
                                                <Flatpickr
                                                    className="form-control lDates"
                                                    placeholder="dd M,yyyy"
                                                    options={{
                                                        mode: "range",
                                                        dateFormat: "Y-m-d"
                                                    }}
                                                    />
                                                
                                                </div>
                                            </Col>
                                            <Col lg={2} className="align-self-center">
                                                <div className="mb-3">
                                                <label htmlFor="floatingPlanningDateInput">&nbsp;</label>
                                                <input
                                                    type="button"
                                                    className="btn btn-primary"
                                                    value="Remove"
                                                    onClick={() => onDeleteFormRow(formRow.id)}
                                                />
                                                </div>
                                            </Col>
                                            </Row>
                                            </CardBody>
                                            </Card>
                                        ))}
                                        <Row>
                                            <Col md={6}>
                                            <div className="d-grid gap-2">
                                                <input
                                                    type="button"
                                                    className="btn btn-success mt-3 mt-lg-0"
                                                    value="Add Leave Line"
                                                    onClick={() => onAddFormRow()}
                                                />
                                            </div>
                                            </Col>
                                            <Col md={6}>
                                                <div className="d-grid gap-2">
                                                    <Button color="primary" type="button" className="btn btnSubmitPlan" onClick={submit}>
                                                        Submit Leave Plan
                                                    </Button>
                                                </div>
                                            </Col>
                                            </Row>
                                            </div>
                                           
                                            
                      
                                            
                    
                        </Col>
                        </Row>
                        ):(
                            <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>Saved</h5>
                                        <p className="text-muted">
                                                Leave Plan <strong>{lPNo}</strong> Saved!
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        )}
                        </div>


                    </Container>
                    
                       
                    
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default AddLeavePlan;