import React, {useEffect, useState} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { 
    Container, 
    Row, Col, Card, 
    CardBody, CardTitle, Table,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem, Modal, Alert, Input, Button, Badge} from "reactstrap";
import { Link } from 'react-router-dom';
import Breadcrumbs from "../../components/Common/Breadcrumb";
import * as url from "../../helpers/url_helper"
import axios from 'axios';
import { formatDate, HRConfig, TOAST_OPTIONS } from './../../helpers/backend_helper';

import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";
import Select from 'react-select';
import CreatableSelect from 'react-select/creatable';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import PropTypes from "prop-types";
import { useHistory } from 'react-router-dom';
import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

const PaymentRequisitions = () => {
        const history = useHistory()
        const [pList, setPList] = useState([])
        const [isLoading, setIsLoading] = useState(false)
        const [empNo, setEmpNo] = useState('')
        const [empName, setEmpName] = useState('')
        const [empTitle, setEmpTitle] = useState('')


        toastr.options = TOAST_OPTIONS;

        const getData = () =>{
            setIsLoading(true)
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                setEmpNo(obj.uid)
                setEmpName(obj.username)
                axios.get(`${url.EMPLOYEES}?$filter=No eq '${obj.uid}'`, HRConfig).then((r)=>{ 
                    setEmpTitle(r.data.value[0].JobTitle)
                }).catch((e)=>{ console.log(e)})
                axios.get(`${url.PAYMENTREQUISITIONS}?$filter=requisitionedBy eq '${obj.uid}'`, HRConfig).then((r)=>{ 
                    setIsLoading(false)
                    setPList(r.data.value)
                }).catch((e)=>{ console.log(e); setIsLoading(false); })
            }

        }
        useEffect(()=>{
           getData()
        },[])
        const { SearchBar } = Search;
        const columns = [
            {
              dataField: 'paymentReqNo',
              text: 'No',
              sort: true,
              formatter: noFormatter
            },
            {
                dataField: 'documentDate',
                text: 'Date',
                sort: true
              }, 
            {
              dataField: 'PayeeNo', 
              text: 'Payee No',
              sort: true
            }, 
            {
                dataField: 'payeeName',
                text: 'Payee Name',
                sort: true
              }, 
              {
                dataField: 'Purpose',
                text: 'Purpose',
                sort: true
              }, 
          
            {
                dataField: 'totalAmount',
                text: 'Total Amount',
                sort: true,
                align:'right',
                formatter:currencyFormatter,
                headerStyle: { textAlign: 'right' } 
                
              },
            {
              dataField: 'Status',
              text: 'Status',
              sort: true,
              align:'center',
              headerStyle: { textAlign: 'center' }, 
              formatter: statusFormatter
            },
            {
              dataField: "action",
              isDummyField: true,
              text: "",
              align:'center',
              formatter: actionFormatter
            }];
      
            const defaultSorted = [{
              dataField: 'paymentReqNo',
              order: 'desc'
            }];
      
            function amountFormatter(cellContent, row){
              
                return(
                  
                  <span> {NumberFormat.getInstance().format(ParseDouble(cellContent))}</span>
                )
              }
            function noFormatter(cellContent, row){
              return(
                <strong>{cellContent}</strong>
              )
            }
            function currencyFormatter(value) {
              // Format the value as currency
              const formattedValue = new Intl.NumberFormat('en-US', { style: 'decimal' }).format(value);
              return formattedValue;
            }
      
            function actionFormatter(cellContent, row){
              return(
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={()=>{ history.push(`/payment-requisition-details/${row.SystemId}`); }}
                >
                  Details
                </Button>
              )
            }
            function statusFormatter(cellContent, row) {
              if (row.Status == "Open") {
                return (
                  <div>
                      <Badge className="me-2 bg-danger">
                      { cellContent }
                      </Badge>
                  </div>
                );
              }else if ((row.Status == "Approved") || (row.Status == "Released")){
                return (
                  <div>
                      <Badge className="me-2 bg-success">
                      { cellContent }
                      </Badge>
                  </div>
                );
              }else if (row.Status == "Withdrawn(Cancelled)"){
                return (
                  <div>
                      <Badge className="me-2 bg-secondary">
                      { cellContent }
                      </Badge>
                  </div>
                );
              }else{
                return (
                  <div>
                      <Badge className="me-2 bg-warning">
                      { cellContent }
                      </Badge>
                  </div>
                );
              }
            }
        
        return (
            <>
                  <LoadingOverlay
                active={isLoading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Payment Requisitions | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Payment Requisitions" />
                        <Row>
                        <Col lg={12}>
                                {isLoading ? (<Alert  color="danger" role="alert">Please wait...</Alert>) : null}
                            </Col>
                        <Col xl={12}>
                            <Card>
                                <CardBody>
                               {(pList.length > 0) && (<>

                                    <ToolkitProvider
                                        keyField="paymentReqNo"
                                        data={ pList }
                                        columns={ columns }
                                        search
                                        >
                                        { toolkitProps => (
                                            <React.Fragment>
                                                <Row className='mb-5'>
                                                  <Col lg={8} className='mb-2'>
                                                  <div  className="rounded bg-light d-grid gap-2">
                                           
                                                      <SearchBar {...toolkitProps.searchProps} className="form-control bg-transparent border-0 searchTm m-0" />
                                                 
                                                    </div>
                                                  </Col>
                                            
                                                  <Col lg={4}> 
                                                  <div className="d-grid gap-2">
                                                    <Link
                                                      className="btn btn-primary"
                                                      to="/create-payment-requisition"
                                                    >
                                                      <i className="mdi mdi-plus me-1" />
                                                      Create Payment Requisition
                                                    </Link>
                                                  </div>
                                                  </Col>
                                                </Row>
                                                <Row>
                                                <Col xl="12">
                                                    <div className="table-responsive">
                                                
                                                    <BootstrapTable 
                                                        keyField='paymentReqNo' 
                                                        data={ pList } 
                                                        columns={ columns } 
                                                        striped
                                                        hover
                                                        condensed
                                                        bordered={ false }
                                                        noDataIndication="No Records" 
                                                        defaultSorted={ defaultSorted }
                                                        classes={ "table align-middle table-nowrap table-check" }
                                                            {...toolkitProps.baseProps}
                                                        />
                                                </div>
                                                </Col>
                                                </Row>
                                                </React.Fragment>
                                                
                                        )
                                        }
                                    </ToolkitProvider>
                  

                              
                                </>)}
                                {(pList.length < 1) && (<>
                                    <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                
                                    
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>No Payment Requisitions found</h5>
                                        <p className="text-muted">
                                                
                                        </p>
                                        </div>
                                    </div>
                                    <div className="text-center mt-4">
                                        <Link
                                        to="/create-payment-requisition"
                                        className="btn btn-primary  btn-sm"
                                        >
                                        Create Requisition <i className="mdi mdi-arrow-right ms-1" />
                                        </Link>
                                    </div>
                              
                                </Col>
                            </Row>
                                </>)}
                                </CardBody>
                            </Card>
                            </Col>
                        </Row>
                           

                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default PaymentRequisitions;