import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardSubtitle, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate, downloadFile } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
import { REJECT_LR } from './../../helpers/url_helper';


    const ApproveLeaveRequest = () => {

        const history = useHistory();
        const {no, id} = useParams();

        const [systemId, setSystemId] = useState('')
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [gender, setGender] = useState('');
        const [department, setDepartment] = useState('');
        const [leaveNo, setLeaveNo] = useState('');
        const [telephone, setTelephone] = useState('');
        const [leaveAddress, setLeaveAddress] = useState('');
        const [leaveCategory, setLeaveCategory] = useState('');
        const [startDate, setStartDate] = useState('dd');
        const [endDate, setEndDate] = useState('dd');
        const [leaveStatus, setLeaveStatus] = useState('');
        const [st, setSt] = useState('warning');
        const [noOfDays, setNoOfDays] = useState('0');
        const [leaveBalance, setLeaveBalance] = useState(0);
        const [documentDate, setDocumentDate] = useState('');
        const [leaveDueCurrentYear, setLeaveDueCurrentYear] = useState(0);
        const [leaveDuePreviousYear, setLeaveDuePreviousYear] = useState('');
        const [leaveInlineWithRooster, setLeaveInlineWithRooster] = useState('No');
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [showApproveBtn, setShowApproveBtn] = useState(true);
        const [showRejectBtn, setShowRejectBtn] = useState(false);
        const [comment, setComment] = useState('')

        toastr.options = TOAST_OPTIONS;
        const [inputs, setInputs] = useState([]);
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            toastr.success("Leave Request No  created" ); 
        }

        const handleCancelApprovalRequest = (e) => {
            setIsloading(true)
            const lNo = {'no': leaveNo}
            axios.post(url.CANCEL_APPROVAL_REQUEST, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    console.log(response); 
                    if(response.status == '200'){
                        toastr.success("Approval Request Cancelled" ); 
                        setTimeout(function(){ window.location.reload(); } ,5000 );
                    }else{
                        toastr.success("Could not Cancel Approval Request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    setIsloading(false)
                    toastr.error(error.response.data.error.message);
                })
        }
        const handleActionChange = () => {
            const selectedAction = document.getElementsByClassName('lAction')[0].value;
            if(selectedAction == 'APPROVE'){
                setShowApproveBtn(true)
                setShowRejectBtn(false)
            }else if(selectedAction == 'REJECT'){
                setShowApproveBtn(false)
                setShowRejectBtn(true)
            }
        }

        const handleRejectLeaveRequest = () => {
            setIsloading(true)
            const lNo = {"no": leaveNo, "rejectioncomment":comment}
            axios.post(url.REJECT_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){ 
                    if(response.status == '200'){
                        toastr.success(response.data.value);
                        history.push(`/rejected/${leaveNo}`) 
                    }else{
                        toastr.success("Could not Approve leave request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    console.log(error)
                    setIsloading(false)
            })  
        }
        const handleApproveLeaveRequest = (e) => {
            setIsloading(true)
            const lNo = {'no': leaveNo}
            axios.post(url.APPROVE_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("Leave Request Approved" );
                        history.push(`/leave-request-approved/${leaveNo}`) 
                    }else{
                        toastr.success("Could not Approve leave request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error);
                    setIsloading(false)
            })
        }

        const handleCheckCalendar = (e) => {
            toastr.success("Directing you to leave calendar" );
            history.push(`/leave-calendar`) 
        }


        useEffect(() => {
            axios.get(url.LEAVE_REQUESTS + "?$filter=No eq '"+ no +"'", HRConfig)
                .then(function(response){
                    if(response.data.value.length > 0){
                        setSystemId(response.data.value[0].SystemId);
                        setEmpNo(response.data.value[0].Employee_No);
                        setEmpName(response.data.value[0].Employee_Name);
                        setEmpTitle(response.data.value[0].Employee_Title);
                        setGender('');
                        setLeaveNo(response.data.value[0].No);
                        setTelephone(response.data.value[0].Telephone_Number);
                        setLeaveAddress(response.data.value[0].Leave_Address);
                        setLeaveStatus(response.data.value[0].Status);
                        setStartDate(response.data.value[0].From_Date);
                        setEndDate(response.data.value[0].To_Date);
                        setNoOfDays(response.data.value[0].No_of_Days);
                        setLeaveBalance(response.data.value[0].Balance);
                        setDocumentDate(response.data.value[0].Document_Date);
                        setLeaveDueCurrentYear(response.data.value[0].Leave_Due_Current_Year);
                        setLeaveDuePreviousYear(response.data.value[0].Leave_Due_Previous_Year);
                        (response.data.value[0].Leave_in_Line_with_Roster == false) ? setLeaveInlineWithRooster('No') : setLeaveInlineWithRooster('Yes') ;
                        if(response.data.value[0].Annual_Leave_WS == 'Yes'){
                            setLeaveCategory('ANNUAL');
                        }else if(response.data.value[0].Compassionate_Leave_WS == 'Yes'){
                            setLeaveCategory('COMPASSION');
                        }else if(response.data.value[0].Maternity_Leave_WS == 'Yes'){
                            setLeaveCategory('MATERNITY');
                        }else if(response.data.value[0].Paternity_Leave_WS == 'Yes'){
                            setLeaveCategory('PATERNITY');
                        }else if(response.data.value[0].Sick_Leave_WS == 'Yes'){
                            setLeaveCategory('SICK');
                        }else if(response.data.value[0].Other_Leave_WS == 'Yes'){
                            setLeaveCategory('OTHER');
                        }else if(response.data.value[0].Study_Leave_WS == 'Yes'){
                            setLeaveCategory('STUDY');
                        }else if(response.data.value[0].Unpaid_Leave_WS == 'Yes'){
                            setLeaveCategory('UNPAID');
                        }
                        if(leaveStatus == 'Pending Approval'){
                            setSt('warning');
                        }else if(leaveStatus == 'Approved'){
                            setSt('success');
                        }
                    
                        setUserError(false);
                    }else{
                        toastr.error("Leave Request Details Not Found");
                    }
                }).catch(function (error) {
                    console.log(error)
                    setUserError(true);
                })
                .then(function () {
                // always executed
            });
        
        },[]);

        
        

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Approve {leaveNo}  | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem={`Approve ${leaveNo}`} />
                        <Form onSubmit={handleSubmit}>
                    
                            <Alert color={`${st}`} role="alert">
                                    <strong>{leaveStatus}</strong>
                            </Alert>
           
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Personal Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control"  value = {empNo} onChange={handleChange} disabled={true} />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" defaultValue={empName} onChange={handleChange} disabled={true}  />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" defaultValue={empTitle} onChange={handleChange} disabled={true} />
                                        <label htmlFor="floatingDepartInput">Title</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                                <CardBody>
                                    <CardTitle className="mb-3">
                                        Details
                                    </CardTitle>
                                    <Row>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingIdInput">Leave Request No</label>
                                        <input  type="text" className="form-control" defaultValue={leaveNo}   disabled={true} />
                                        
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingIdInput">Telephone Number</label>
                                        <input name='Telephone' type="text" className="form-control" defaultValue={telephone}   onChange={handleChange} disabled={true} />
                                        
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingnameInput">Leave Address</label>
                                        <input name='Address'  type="text" className="form-control" defaultValue={leaveAddress}  onChange={handleChange} disabled={true}  />
                                        
                                        </div>
                                    </Col>
                                    
                                        <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingSelectGrid"> Leave Category</label>
                                                <select  className="form-select lType"  disabled={true}>
                                                    <option>{leaveCategory}</option>
                                                    <option>ANNUAL</option>
                                                    <option>COMPASSION</option>
                                                    <option>MATERNITY</option>
                                                    <option>PATERNITY</option> 
                                                    <option>STUDY</option>
                                                    <option>W-OUT PAY</option>
                                                </select>
                                                
                                            </div>
                                        </Col>
                                    
                              
                 
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingnameInput">Start Date - End Date </label>
                                            <input name="leaveDates" disabled={true} type="text" className="form-control" value={`${startDate} to ${endDate}`} onChange={handleChange}  />
                                            
                                            </div>
                                        </Col>
                                       
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Number Of Days</label>
                                            <input name='noOfDays' type="text" className="form-control noOfDays" disabled={true}  value={noOfDays}  onChange={handleChange}  />
                                            
                                            </div>
                                        </Col>
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Leave Balanace</label>
                                            <input name='leaveBalance' type="text" className="form-control leaveBalance" disabled={true} value={leaveBalance}   onChange={handleChange}  />
                                            
                                            </div>
                                        </Col>
                                       
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Document Date</label>
                                            <input name='documentDate' type="text" className="form-control documentDate" disabled={true} value={documentDate}   onChange={handleChange}  />
                                            
                                            </div>
                                        </Col>
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Leave Due in Current Year</label>
                                            <input name='leaveDueCurrentYear' type="text" className="form-control leaveDueCurrentYear" disabled={true} value={leaveDueCurrentYear}  onChange={handleChange}  />
                                            
                                            </div>
                                        </Col>
                                   
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingnameInput">Status</label>
                                            <input name='leaveStatus' disabled={true}  type="text" className="form-control" defaultValue={leaveStatus}  onChange={handleChange}   />
                                            
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingSelectGrid"> Select Action</label>
                                                <select  className="form-select lAction" onChange={handleActionChange}>
                                                    
                                                    <option value="APPROVE">APPROVE LEAVE REQUEST</option>
                                                    <option value="REJECT">REJECT LEAVE REQUEST</option>
                               
                                                </select>
                                                
                                            </div>
                                        </Col>
                                    </Row>
                                    {showRejectBtn && (
                                        <Row>
                                        <Col md={12}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingSelectGrid"> Rejection Comment</label>
                                                <input name='rejectComment'  type="text" className="form-control rejectComment"  onChange={(e)=>setComment(e.target.value)}   />
                                                
                                            </div>
                                        </Col>
                                        </Row>
                                    )}
          
                                </CardBody>
                            </Card>
                                            
                            
                                    <Row>
                                        <Col md={6}>
                                            <div className="d-grid gap-2">
                                                <Button color="secondary" type="button" className="btn " onClick={handleCheckCalendar}>
                                                    Check Leave Calendar
                                                </Button>
                                            </div>
                                        </Col>
                                        {showRejectBtn && (
                                        <Col md={6} className="reject">
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn btnRejectRequest" onClick={handleRejectLeaveRequest}>
                                                    Reject
                                                </Button>
                                            </div>
                                        </Col>
                                        )}
                                        {showApproveBtn && (
                                        <Col md={6} className="approve">
                                            <div className="d-grid gap-2">
                                                <Button color="success" type="button" className="btn btnApproveRequest" onClick={handleApproveLeaveRequest}>
                                                    Approve Leave Request
                                                </Button>
                                            </div>
                                        </Col>
                                        )}
                        
                                   
                                    </Row>
                              
                                            
                    
                        </Col>
                        </Row>
                        </Form>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default ApproveLeaveRequest;