import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert, Modal,UncontrolledDropdown, DropdownToggle,DropdownMenu, Table } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Select from 'react-select';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
import { useParams } from 'react-router-dom';
import {Link} from 'react-router-dom';
import { classname } from 'classnames';

    const PaymentRequisitionDetails = () => {
        const history = useHistory();
        const {id} = useParams();

        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
  

        const [categories, setCategories] = useState([])
        const [subCategories, setSubCategories] = useState([])
        const [payeeTypes, setPayeeTypes] = useState(['Customer', 'Vendor', 'Bank', 'Employee', 'Imprest','Statutory'])
        const [payeeOptions, setPayeeOptions] = useState([])
        const [currencyList, setCurrencyList] = useState([])
        const [supplierDocTypeList, setSupplierDocTypeList] = useState(['LPO', 'Call-off Order', 'Direct Invoice', 'Contract'])
        const [docOptions, setDocOptions] = useState([])
        const [costCenterList, setCostCenterList] = useState([])
        const [projectList, setProjectList] = useState([])
        const [approverList, setApproverList] = useState([])
        const [accType, setAccType] = useState(['G/L Account', 'Vendor', 'Customer', 'Bank Account'])
        const [aHistory, setAHistory] = useState([])
       
        const [selectedPayee, setSelectedPayee] = useState('')
        const [selectedDoc, setSelectedDoc] = useState('')
        const [paymentCategory, setPaymentCategory] = useState('')
        const [paymentSubCategory, setPaymentSubCategory] = useState('')
        const [purpose, setPurpose] = useState('')
        const [currencyCode, setCurrencyCode] = useState('')
        const [costCenter, setCostCenter] = useState('')
        const [projectCode, setProjectCode] = useState('')
        const [payeeNo, setPayeeNo] = useState('')
        const [payeeName, setPayeeName] = useState('')
        const [supplierDocType, setSupplierDocType] = useState('')
        const [supplierDocNo, setSupplierDocNo] = useState('')
        const [supplierDocName, setSupplierDocName] = useState('')
        const [approverCode, setApproverCode] = useState('')
        const [amount, setAmount] = useState('')
        const [no, setNo] = useState('')
        const [status, setStatus] = useState('')
        const [payeeType, setPayeeType] = useState('')
        const [etag, setEtag] = useState('')
        const [systemId, setSystemId] = useState('')

        const [selectedType, setSelectedType] = useState('')
        const [selectedNo, setSelectedNo] = useState(null)
        const [selectedNox, setSelectedNox] = useState('')
        const [description, setDescription] = useState('')
        const [quantity, setQuantity] = useState('')
        const [rate, setRate] = useState('')
        const [lineEtag, setLineEtag] = useState('')
        const [lineSystemId, setLineSystemId] = useState('')

        const [paymentRequisitionLines, setPaymentRequisitionLines] = useState([])

        const [noo, setNoo] = useState([])

        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')

        const [showPayeeNo, setShowPayeeNo] = useState(false)
        const [showSupplier, setShowSupplier] = useState(false)

        const [modal_itemA, setmodal_itemA] = useState(false);
        const [modal_item, setmodal_item] = useState(false);
        const [modal_history, setmodal_history] = useState(false);

        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
        }
        const tog_itemA = () => {
            setmodal_itemA(!modal_itemA);
            removeBodyCss();
        }
        const tog_item = () => {
            setmodal_item(!modal_item);
            removeBodyCss();
        }
        const tog_history = () => {
            setmodal_history(!modal_history);
            removeBodyCss();
        }

        toastr.options = TOAST_OPTIONS;
        
        const handleCategoryChange = (e) =>{
            setPaymentCategory(e.target.value)
            setSubCategories([]) 
            axios.get(`${url.PAYMENTSUBCATEGORIES}?$filter=paymentType eq '${e.target.value}'`, HRConfig).then((r)=>{ 
                setSubCategories(r.data.value)
            }).catch((e)=>{ console.log(e)})
        }
        const handlePayeeTypeChange = (type) =>{
            
            setSelectedPayee(null)
            //setPayeeNo('')
            //setPayeeName('')
            setPayeeOptions([])
            setAccType(['G/L Account'])
            let options = []
            if(type == ""){
                setShowPayeeNo(false)
            }else{
                setShowPayeeNo(true)
            }
            if(type == "Customer"){
                setAccType(['G/L Account', 'Customer'])
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_CUSTOMER}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if(type == "Vendor"){
                setAccType(['Vendor'])
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_VENDOR}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.vendorName,'value':v.vendorNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if(type == "Bank"){
                setAccType(['Bank Account'])
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_BANK}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.bankName,'value':v.bankNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((type == "Employee")){
                setAccType(['G/L Account'])
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_IMPREST}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((type == "Imprest")){
                setAccType(['G/L Account'])
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_IMPREST}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setPayeeOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
        }
        const handleSelectedPayee = (p) =>{
            setSelectedPayee(p)
            setPayeeNo(p.value)
            setPayeeName(p.label)
            setNoo([{label:p.label, value:p.value}])
        }
        const handleSupplierDocTypeChange = (e) =>{
            const docType = e.target.value
            setSupplierDocType(e.target.value)
            setSelectedDoc(null)
            setSupplierDocNo('')
            setSupplierDocName('')
            setDocOptions([])  
            let options = []
            if(docType == " "){ setShowSupplier(false); }else{ setShowSupplier(true) }
            if((docType == "LPO")){
                setIsloading(true)
                axios.get(`${url.LPOLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Call-off Order")){
                setIsloading(true)
                axios.get(`${url.CALLOFFORDERSLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Direct Invoice")){
                setIsloading(true)
                axios.get(`${url.DIRECTINVOICESLIST}?$filter=buyfromVendorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            if((docType == "Contract")){
                setIsloading(true)
                axios.get(`${url.CONTRACTSLIST}?$filter=contractorNo eq '${payeeNo}'`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.postingDescription,'value':v.No})
                    })
                    setDocOptions(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
        }
        const handleDocChange = (doc)=>{
            setSelectedDoc(doc)
            setSupplierDocNo(doc.value)
            setSupplierDocName(doc.label)
        }
        const clearLine = () =>{
            setSelectedType('')
            setSelectedNox('')
            setSelectedNo(null)
            setDescription('')
            setQuantity(1)
            setRate('')
        }
        const handleAddItem = () =>{
            const itemLine =  {
                "documentType":"Payment Requisition",
                "documentNo": no,
                "accountType": selectedType,
                "accountNo": selectedNox,
                "description": description,
                "quantity": parseInt(quantity),
                "rate": parseInt(rate) 
            }
          
            if((selectedType == 'Vendor') && (selectedNox != payeeNo)){
                toastr.error('Account no selected in Payment Line should match Document Payee Name. i.e '+payeeName)
            }else if((selectedType == 'Vendor') && (parseInt(quantity) != 1)){
                toastr.error('For vendor payments, Quantity should always equal to 1')
            }else if((parseInt(quantity) < 0) || (parseInt(rate) < 0)){
                toastr.error('Negative values are not allowed for Quantities and Amount')
            }else{
                setIsloading(true)
                axios.post(`${url.PAYMENTLINE}`, JSON.stringify(itemLine), HRConfig).then((r)=>{ 
                    console.log(r)
                    toastr.success(`Line details added`);
                    setIsloading(false)
                    tog_itemA()
                    getData()
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }
            
          
            
            
            
        }
        const handleRemoveItem = () =>{
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lineEtag
                }
              }
            setIsloading(true)
            axios.delete(`${url.PAYMENTLINE}(${lineSystemId})`, Config).then((r)=>{ 
                toastr.success(`Line details deleted`);
                setIsloading(false)
                tog_item()
                //getData()
                history.go(0)
            }).catch((e)=>{ 
                console.log(e)
                setIsloading(false)
            })
            
        }

        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleTypeChange = (e) =>{
            setSelectedType(e.target.value)
            setSelectedNo(null)
            setIsloading(true)
            setSelectedPayee('')
            let options = []
            
            if(e.target.value == 'G/L Account'){
                axios.get(url.GLACCOUNTS, HRConfig).then((r)=>{
                    setIsloading(false)
                    r.data.value.map((a)=>{
                        options.push({label:a.Name, value:a.No})
                    })
                    setNoo(options)
                }).catch((e)=>{
                    setIsloading(false)
                    console.log(e)
                })
                
            }else if(e.target.value == "Vendor"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_VENDOR}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.vendorName,'value':v.vendorNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else if(e.target.value == "Bank Account"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_BANK}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.bankName,'value':v.bankNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else if(e.target.value == "Customer"){
                setIsloading(true)
                axios.get(`${url.PAYEETYPE_CUSTOMER}`, HRConfig).then((r)=>{ 
                    r.data.value.map((v)=>{
                        options.push({'label': v.customerName,'value':v.customerNo})
                    })
                    setNoo(options)
                    setIsloading(false)
                }).catch((e)=>{ 
                    console.log(e)
                    setIsloading(false)
                })
            }else{
                setIsloading(false) 
            }
        }
        const handleNoChange = (e)=>{
            setSelectedNo(e); 
            setSelectedNox(e.value)
            if(selectedType == 'Item'){
                axios.get(`${url.UNITOFMEASURE}?$filter=ItemNo eq '${e.value}'`, HRConfig).then((r)=>{setSelectedUnitOfMeasure(r.data.value[0].Code)}).catch((e)=>{ console.log(e)}) 
            }
        }
        const handleSubmit = () => {     
            const data = {
                "requisitionedBy": empNo,
                "paymentCategory": paymentCategory,
                "paySubcategory": paymentSubCategory,
                "PayeeNo": payeeNo,
                "PayeeName": payeeName,
                "Purpose": purpose,
                "currencyCode": currencyCode,
                "supplierDocType": supplierDocType,
                "supplierDocNo": supplierDocNo,
                "costCenter": costCenter,
                "projectCode": projectCode,
                "approverCode": approverCode
            };
            console.log(data)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : etag
                }
              }
            setIsloading(true); 
            axios.patch(`${url.PAYMENTREQUISITIONS}(${id})`, JSON.stringify(data), Config)
                .then(function(response){ 
                    toastr.success("Updated payment requisition" )
                    setIsloading(false);
                    getData()
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false);
            })
          
        }
        const getData = () =>{
            axios.get(url.PAYMENTCATEGORIES, HRConfig).then((r)=>{ 
                setCategories(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(`${url.PAYMENTSUBCATEGORIES}`, HRConfig).then((r)=>{ 
                setSubCategories(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.COSTCENTERCODES, HRConfig).then((r)=>{ 
                setCostCenterList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.PROJECTCODES, HRConfig).then((r)=>{ 
                setProjectList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.CURRENCYLIST, HRConfig).then((r)=>{ 
                setCurrencyList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.APPROVERCODELIST, HRConfig).then((r)=>{ 
                setApproverList(r.data.value)
            }).catch((e)=>{ console.log(e)})
            setIsloading(true)
            axios.get(`${url.PAYMENTREQUISITIONS}(${id})?$expand=paymentReqSubform`, HRConfig).then((r)=>{
                //console.log(r.data)
                setEtag(r.data['@odata.etag'])
                setAmount(r.data.totalAmount)
                setPaymentRequisitionLines(r.data.paymentReqSubform)
                setApproverCode(r.data.approverCode)
                setCostCenter(r.data.costCenter)
                setPaymentCategory(r.data.paymentCategory)
                
                if(r.data.PayeeNo != ""){
                    setShowPayeeNo(true)
                }
                setPayeeName(r.data.payeeName)
                setPayeeNo(r.data.PayeeNo)
                setPurpose(r.data.Purpose)
                setProjectCode(r.data.projectCode)
                setNo(r.data.paymentReqNo)
                setStatus(r.data.Status)
                setSupplierDocNo(r.data.supplierDocNo)
                setSupplierDocName(r.data.supplierDocType)
                if(r.data.supplierDocNo != ""){
                    setShowSupplier(true)
                }
                setPaymentCategory(r.data.paymentCategory)
                setPaymentSubCategory(r.data.paySubcategory)
                setPayeeType(r.data.payeeType)
                handlePayeeTypeChange(r.data.payeeType)
                setCurrencyCode(r.data.currencyCode)
                setSupplierDocNo(r.data.supplierDocNo)
                setSupplierDocName(r.data.supplierType)
                axios.get(url.EMPLOYEES + "?$filter=No eq '"+ r.data.requisitionedBy +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            setUserError(false);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                        setIsloading(false)
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                        setIsloading(false)
                }) 
            }).catch((e)=>{
                setIsloading(false)
                console.log(e)
            })
        }
        const handleClickLineDetails = (k) =>{
            tog_item()
            setLineSystemId(paymentRequisitionLines[k].SystemId)
            setLineEtag(paymentRequisitionLines[k]['@odata.etag'])
            setSelectedType(paymentRequisitionLines[k].accountType)
            setSelectedNo({value:paymentRequisitionLines[k].accountNo, label:paymentRequisitionLines[k].accountName})
            setSelectedNox(paymentRequisitionLines[k].accountNo)
            setDescription(paymentRequisitionLines[k].description)
            setQuantity(paymentRequisitionLines[k].quantity)
            setRate(paymentRequisitionLines[k].rate)
        }
        useEffect(() => {
           getData() 
        },[]);
        
        const sendForApproval = () =>{
            setIsloading(true)
            const data = {paymentRequisitionNo:no}
            axios.post(url.SEND4APPROVAL, JSON.stringify(data), HRConfig).then((r)=>{
                toastr.success("Payment requisition sent for approval")
                getData()
                setIsloading(false)
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const cancelApprovalRequest = () =>{
            setIsloading(true)
            const data = {paymentRequisitionNo:no}
            axios.post(url.CANCELAPPROVAL, JSON.stringify(data), HRConfig).then((r)=>{
                toastr.success("Approval request cancelled")
                getData()
                setIsloading(false)
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleDelete = () =>{
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : etag
                }
              }
            setIsloading(true)
            axios.delete(`${url.PAYMENTREQUISITIONS}(${id})`, Config).then((r)=>{ 
                toastr.success(`Payment requisition deleted`);
                setIsloading(false)
                history.push(`/payment-requisitions`)
            }).catch((e)=>{ 
                console.log(e)
                setIsloading(false)
            })
        }
        const handleApprovalHistory = () =>{
            setIsloading(true)
            axios.get(`${url.APPROVALHISTORY}?$filter=DocumentNo eq '${no}'`, HRConfig).then((r)=>{
                setAHistory(r.data.value)
                setIsloading(false)
                tog_history()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleArchive = () =>{
            setIsloading(true)
            const data = {paymentRequisitionNo:no}
            axios.post(url.ACHIEVEPAYMENTREQUISITION, JSON.stringify(data), HRConfig).then((r)=>{
                toastr.success("Payment requisition archived")
                setIsloading(false)
                history.push(`/payment-requisitions`)
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Payment Requisition | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Payment Requisition" />
                        <div>
                     

                        {(ipaSaved == false) ? (
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Requisitioner</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>No</label>
                                            <input type="text" className="form-control" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>Name</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                            <label>Title</label>
                                            <input  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        </div>
                                    </Col>
                                   
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                            <CardBody>
                            <CardTitle>Requisition Details</CardTitle>
                                <Row>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Requisition No</label>
                                        <input  type="text" className="form-control" defaultValue={no} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Status</label>
                                        <input  type="text" className="form-control" defaultValue={status} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Payment Category</label>
                                        <input type='text' className="form-control" defaultValue={paymentCategory} disabled={true}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Payment SubCategory</label>
                                        <input type='text' className="form-control" defaultValue={paymentSubCategory} disabled={true}/>
                                    </div>
                                </Col>
                                
                               {(payeeType != 'Statutory') && (
                                <>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Select Payee</label>
                                        <Select options={payeeOptions} value={selectedPayee} onChange={(p)=>handleSelectedPayee(p)} />
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Payee No</label>
                                        <input  type="text" className="form-control" defaultValue={payeeNo} disabled={true}  />
                                    </div>
                                </Col>
                                </>
                                )}
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Payee Name</label>
                                        <input  type="text" className="form-control" value={payeeName} onChange={(e)=>setPayeeName(e.target.value)}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Purpose</label>
                                        <input  type="text" className="form-control" value={purpose} onChange={(e)=>setPurpose(e.target.value)}  />
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Currency Code</label>
                                        <select className="form-select" value={currencyCode} onChange={(e)=>setCurrencyCode(e.target.value)}>
                                            <option value={``}>UGX</option>
                                            {currencyList.map((c,k)=>(
                                                <option key={k} value={c.currencyCode}>{c.currencyCode} {` `} {c.description}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                {(payeeType == "Vendor") && (
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Supplier Doc Type</label>
                                        <select className="form-select" onChange={(e)=>handleSupplierDocTypeChange(e)}>
                                            <option value={``}></option>
                                            {supplierDocTypeList.map((c,k)=>(
                                                <option key={k} value={c}>{c}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                )}
                                {showSupplier && (
                                <>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Doc</label>
                                        <Select options={docOptions} value={selectedDoc} onChange={(doc)=>handleDocChange(doc)}/>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Supplier Doc No</label>
                                        <input  type="text" className="form-control" defaultValue={supplierDocNo} disabled={true}  />
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Supplier Doc Name</label>
                                        <input  type="text" className="form-control" defaultValue={supplierDocName} disabled={true} />
                                    </div>
                                </Col>
                                </>
                                )}
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Cost Center Code</label>
                                        <select className="form-select" value={costCenter} onChange={(e)=>setCostCenter(e.target.value)}>
                                            <option value={``}></option>
                                            {costCenterList.map((c,k)=>(
                                                <option key={k} value={c.dimensionValueCode}>{c.dimensionValueName}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Project Code</label>
                                        <select className="form-select" value={projectCode} onChange={(e)=>setProjectCode(e.target.value)}>
                                            <option value={``}></option>
                                            {projectList.map((c,k)=>(
                                                <option key={k} value={c.dimensionValueCode}>{c.dimensionValueName}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Approver Name</label>
                                        <select className="form-select" value={approverCode} onChange={(e)=>{setApproverCode(e.target.value)}}>
                                            <option value={``}></option>
                                            {approverList.map((c,k)=>(
                                                <option key={k} value={c.staffCode}>{c.staffName}</option>
                                            ))}
                                        </select>
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Approver Code</label>
                                        <input  type="text" className="form-control" defaultValue={approverCode} disabled={true} />
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Total Amount</label>
                                        <input  type="text" className="form-control" value={amount} onChange={(e)=>setAmount(e.target.value)} disabled={true} />
                                    </div>
                                </Col>
                                </Row><Row>
                                    
                                    </Row>
                                    </CardBody>
                                    </Card>
                                    {paymentRequisitionLines.map((i, k)=>(
                                        <Row key={k}>
                                            <Col>
                                            <Card>
                                            <CardBody>
                                           
                                                <Row className="mb-3">
                                                <Col xl={3} sm={6}>
                                                   
                                                    <h5>Item  {k+1} </h5>
                                                    
                                                </Col>
                                                {(status == 'Open') && (
                                                <Col sm={9}>
                                                    <div className="float-sm-end d-flex">
                                                        <Button color="info" className="btn-sm btn-rounded" onClick={()=>handleClickLineDetails(k)}>
                                                            Edit/Delete Payment Line
                                                        </Button>
                                                    </div>
                                                </Col>
                                                )}
                                                </Row>
                                            
                                        
                               
                                            <Row >
                                            
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Account Type</label>
                                                        <input  type="text" className="form-control" defaultValue={i.accountType} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>No</label>
                                                        <input  type="text" className="form-control" defaultValue={i.accountNo} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={6}>
                                                    <div className="mb-3">
                                                        <label>Description</label>
                                                        <input  type="text" className="form-control" defaultValue={i.description} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Quantity</label>
                                                        <input  type="text" className="form-control" defaultValue={i.quantity} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Rate</label>
                                                        <input  type="text" className="form-control" defaultValue={i.rate} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Amount</label>
                                                        <input  type="text" className="form-control" defaultValue={i.amount} disabled={true} />
                                                    </div>
                                                </Col>
                                                <Col lg={3}>
                                                    <div className="mb-3">
                                                        <label>Amount LCY</label>
                                                        <input  type="text" className="form-control" defaultValue={i.amountLCY} disabled={true} />
                                                    </div>
                                                </Col>
                                                
                                            </Row>
                                            </CardBody>
                                            </Card>
                                            </Col>
                                        </Row>
                                    ))}

                                    <Row className='justify-content-center'> 
                                        
                                        
                                        {(status == 'Open') && (
                                        <>
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button className="success" onClick={() => {  clearLine(); tog_itemA() }}>
                                                    Add Requisition Item 
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="success" type="button" className="btn" onClick={handleSubmit}>
                                                    Update Requisition
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="warning" type="button" className="btn" onClick={sendForApproval}>
                                                    Send for Approval
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="dark" type="button" className="btn" onClick={handleDelete}>
                                                    Delete
                                                </Button>
                                            </div>
                                        </Col>
                                        </>
                                        )}
                                        {(status == 'Pending Approval') && (
                                        <>
                                        <Col md={6} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="warning" type="button" className="btn" onClick={cancelApprovalRequest}>
                                                    Cancel approval request
                                                </Button>
                                            </div>
                                        </Col>
                                        </>
                                        )}
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn" onClick={handleArchive} >
                                                    Archive
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" onClick={handleApprovalHistory}>
                                                    Approval History
                                                </Button>
                                            </div>
                                        </Col>  
                                        <Col md={3} className='mb-3'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" >
                                                    Attachments
                                                </Button>
                                            </div>
                                        </Col>  
                                    </Row>

                                
                            </Col>
                            <Col>
                           
                            <Modal
                                  isOpen={modal_itemA}
                                  toggle={() => {
                                    tog_itemA()
                                  }}
                                  centered
                                >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Add Requisition Item</h5>
                                    <button
                                      type="button"
                                      onClick={() => {
                                        setmodal_itemA(false);
                                      }}
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                            
                                    
                                
                                     
                                            <Row >
                        
                                            <Col lg={12}>
                                            {isloading && ( <Alert color="danger" role="alert">Please wait...   </Alert>)}
                                                <div className="mb-3">
                                                    <label>Account Type</label>
                                                    <select className="form-select" value={selectedType} onChange={(e) =>{handleTypeChange(e)} }>
                                                        <option value=" ">Select</option>
                                                        {accType.map((a,k)=>(
                                                            <option key={k} value={a}>{a}</option>
                                                        ))}
                                                    </select>
                                                </div>
                                            </Col>
                                     
                                            <Col lg={12}>
                                                
                                                <div className="mb-3">
                                                    <label>Account No</label>
                                                    <Select options={noo} value={selectedNo} onChange={handleNoChange} />
                                                    
                                                </div>
                                            </Col>
                                            
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Description</label>
                                                    <input  type="text" className="form-control" onChange={(e)=>setDescription(e.target.value)}   />
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Quantity</label>
                                                    <input  type="number" className="form-control" onChange={(e)=>setQuantity(e.target.value)}  />  
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                <label>Rate</label>
                                                    <input  type="number" className="form-control"  onChange={(e)=>{ setRate(e.target.value)}} />
                                                    
                                                </div>
                                            </Col>
                                            
                                            
                                         
                                            </Row>
                                          
                                            
                                            </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      onClick={() => {  tog_itemA() }}
                                      className="btn btn-danger "
                                      data-dismiss="modal"
                                    >
                                      Cancel
                                    </button>
                                    <button
                                      type="button"
                                      className="btn btn-success "
                                      onClick={()=>{handleAddItem()}}
                               
                                    >
                                      Add Item...
                                    </button>
                                  </div>
                                </Modal> 
                                    
                            <Modal isOpen={modal_item} toggle={() => { tog_item() }} centered >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Edit Requisition Item</h5>
                                    <button
                                      type="button"
                                      onClick={() => {
                                        setmodal_item(false);
                                      }}
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                            
                                    
                                
                                     
                                            <Row >
                        
                                            <Col lg={12}>
                                            {isloading && ( <Alert color="danger" role="alert">Please wait...   </Alert>)}
                                                <div className="mb-3">
                                                    <label>Account Type</label>
                                                    <select className="form-select" value={selectedType} onChange={(e) =>{handleTypeChange(e)} }>
                                                        <option value=" ">Select</option>
                                                        {accType.map((a,k)=>(
                                                            <option key={k} value={a}>{a}</option>
                                                        ))}
                                                    </select>
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                
                                                <div className="mb-3">
                                                    <label>Account No </label>
                                                    <input className="form-control" value={selectedNox} onChange={(e)=>setSelectedNox(e.target.value)} disabled={true} />
                                                    
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                
                                                <div className="mb-3">
                                                    <label>Change Account No</label>
                                                    <Select options={noo} value={selectedNo} onChange={handleNoChange} />
                                                    
                                                </div>
                                            </Col>
                                            
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Description</label>
                                                    <input  type="text" className="form-control" value={description} onChange={(e)=>setDescription(e.target.value)}   />
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Quantity</label>
                                                    <input  type="number" className="form-control" value={quantity} onChange={(e)=>setQuantity(e.target.value)}  />  
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                <label>Rate</label>
                                                    <input  type="number" className="form-control" value={rate}  onChange={(e)=>{ setRate(e.target.value)}} />
                                                    
                                                </div>
                                            </Col>
                    
                                            </Row>
                                          
                                            
                                            </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      onClick={() => {  tog_item() }}
                                      className="btn btn-danger "
                                      data-dismiss="modal"
                                    >
                                      Cancel
                                    </button>

                                    <button
                                      type="button"
                                      className="btn btn-dark "
                                      onClick={()=>{handleRemoveItem()}}
                               
                                    >
                                      Delete Item...
                                    </button>
                                    <button
                                      type="button"
                                      className="btn btn-success "
                                      onClick={()=>{handleAddItem()}}
                               
                                    >
                                      Update Item...
                                    </button>
                                  </div>
                                </Modal>

                                <Modal isOpen={modal_history} toggle={() => { tog_history() }} centered  size="xl">
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Approval history</h5>
                                    <button
                                      type="button"
                                      onClick={() => {
                                        setmodal_history(false);
                                      }}
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                                       
                                            <Row >
                                            <Col lg={12}>
                                            {(aHistory.length > 0) ? (
                                            <div className="table-responsive">
                                                <Table className="table table-sm m-0">
                                                    <thead>
                                                        <tr>
                                                            <th>Document Type</th>
                                                            <th>Document No</th>
                                                            <th>Description</th>
                                                            <th>Status</th>
                                                            <th>Sender ID</th>
                                                            <th>Approver ID</th>
                                                            <th>Last Date Modified</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                      	                {aHistory.map((a,k)=>(
                                                            <tr key={k}>
                                                                <td>{a.DocumentType}</td>
                                                                <td>{a.DocumentNo}</td>
                                                                <td>{a.Description}</td>
                                                                <td>{a.Status}</td>
                                                                <td>{a.SenderID}</td>
                                                                <td>{a.ApproverID}</td>
                                                                <td>{a.LastDateTimeModified}</td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </Table>
                                                </div>
                                            ):(
                                                <div className="text-center">
                                                    <div className="mb-4">
                                                    <i className="mdi mdi-check-circle-outline text-success display-4" />
                                                    </div>
                                                    <div>
                                                    <h5>No approval history found</h5>
                                                    <p className="text-muted">
                                                            
                                                    </p>
                                                    </div>
                                                </div>
                                            )}
                                            </Col>
                    
                                            </Row>
                                          
                                            
                                            </div>
                              
                                </Modal>
              
                                            
                                            <br/>
              
                                           
                    
                        </Col>
                        </Row>
                        ):(
                            <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>Saved</h5>
                                        <p className="text-muted">
                                                Requisition <strong>{ipaNo}</strong> Saved!
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        )}
                        </div>


                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
         

        );
    }

export default PaymentRequisitionDetails;