import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Container } from "reactstrap";
import {
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    Badge,
    Label,
    Button,
    Form,
    Input,
    InputGroup,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem, Modal
  } from "reactstrap"
import { Link, useHistory } from "react-router-dom"
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';


import axios from 'axios';
import { HRConfig, HRConfig2, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { STAFF, POST_PASSWORD_FORGET, APPNAME } from '../../helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import Select from 'react-select';
import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const SystemUsers = () => {
      const history = useHistory();
      toastr.options = TOAST_OPTIONS;
      const [uList, setUList] = useState([])
      const [isLoading, setIsLoading] = useState(false)
      const [isEdit, setIsEdit] = useState(false)
      const [modal_staff, setmodal_staff] = useState(false)
      const [employeeNo, setEmployeeNo] = useState('')
      const [firstName, setFirstName] = useState('')
      const [lastName, setLastName] = useState('')
      const [otherNames, setOtherNames] = useState('')
      const [email, setEmail] = useState('')
      const [navUsername, setNavUsername] = useState('')
      const [eStatus, setEStatus] = useState('')
      const [changePass, setChangePass] = useState('')
      const [level, setLevel] = useState('')

      const removeBodyCss = () => {
        document.body.classList.add("no_padding");
      }
      const tog_staff = () => {
        setmodal_staff(!modal_staff)
        removeBodyCss()
      }
      const fetchData = () =>{
        setIsLoading(true)
        axios.get(STAFF, HRConfig).then((r)=>{
            setUList(r.data);
            setIsLoading(false)
        }).catch((e)=>{
            console.log(e)
            setIsLoading(false)
        })
      }
      useEffect(() => {
        fetchData()
      }, []);
      
      const handleClickDetails = (row) =>{
        clearData()
        setIsEdit(true)
        setEmployeeNo(row.no)
        setFirstName(row.firstName)
        setLastName(row.lastName)
        setOtherNames(row.otherNames)
        setEmail(row.email)
        setNavUsername(row.username)
        setEStatus(row.state)
        setChangePass(row.mustChangePass)
        setLevel(row.level)
        tog_staff()
      }
      const clearData = () =>{
        setEmployeeNo('')
        setFirstName('')
        setLastName('')
        setOtherNames('')
        setEmail('')
        setNavUsername('')
        setEStatus('')
        setChangePass('')
        setLevel('')
      }
      const handleClickAdd = () =>{
        setIsEdit(false)
        clearData()
        setChangePass('Yes')
        setEStatus('Enabled')
        tog_staff()
      }
      const handleSaveStaff = () =>{
        let data = {}
        if(isEdit){
            data = {
                staffNo     : employeeNo,
                firstName   : firstName,
                lastName    : lastName,
                otherNames  : otherNames,
                state       : eStatus,
                changePass  : changePass,
                email       : email,
                username    : navUsername,
                level       : level,
                action      : "edit"
            }
        }else{          
            data = {
                staffNo     : employeeNo,
                firstName   : firstName,
                lastName    : lastName,
                otherNames  : otherNames,
                state       : eStatus,
                changePass  : changePass,
                email       : email,
                username    : navUsername,
                level       : level,
                action      : "add"
            }
        }
        if(employeeNo.trim().length < 5){
            toastr.error("Enter valid employee no")
        }else if(firstName.trim().length < 3){
            toastr.error("Enter first name")
        }else if(lastName.trim().length < 3){
            toastr.error("Enter last name")
        }else if(eStatus == ''){
            toastr.error("Select employee status")
        }else if(changePass == ''){
            toastr.error("Indicate if you wish to have the employee must change his/her password on next login")
        }else if(email.trim().length < 3){
            toastr.error("Enter valid email address")
        }else if(level == ''){
            toastr.error("Select employee's system access level")
        }else{
            setIsLoading(true)
            axios.post(STAFF, JSON.stringify(data), HRConfig).then((r)=>{
                if(r.data.status == '1'){
                    toastr.success(r.data.message)
                    tog_staff()
                    fetchData()
                  }else{
                    toastr.error(r.data.message)
                  }
                  setIsLoading(false)
            }).catch((e)=>{
                console.log(e)
                setIsLoading(false)
            })
        }
      }
      const handleResetPassword = () =>{
            const data = {email:email}
            setIsLoading(true)
            axios.post(POST_PASSWORD_FORGET, JSON.stringify(data), HRConfig2).then((r)=>{
              if(r.request.status == '200'){
                toastr.success(r.data.message)
                tog_staff()
              }else{
                toastr.error("Contact System Administrator")
              }
              setIsLoading(false)
            }).catch((e)=>{
                console.log(e)
                setIsLoading(false)
            })
      }
      const { SearchBar } = Search;
      const columns = [
      {
        dataField: 'no',
        text: 'No',
        sort: true,
        formatter: noFormatter
      },
      {
        dataField: 'staffName',
        text: 'Employee',
        sort: true
      }, 
      {
        dataField: 'email',
        text: 'Email',
        sort: true
      }, 
      {
        dataField: 'username',
        text: 'Username',
        sort: true
      }, 
      {
        dataField: 'level',
        text: 'Level',
        sort: true
      }, 
      {
        dataField: 'lastLogin',
        text: 'Last Login',
        sort: true
      }, 
      {
        dataField: 'state',
        text: 'Status',
        sort: true,
        formatter: statusFormatter
      },
      {
        dataField: "action",
        isDummyField: true,
        text: "",
        formatter: actionFormatter
      }];

      const defaultSorted = [{
        dataField: 'No',
        order: 'desc'
      }];

      function noFormatter(cellContent, row){
        return(
          <strong>{cellContent}</strong>
        )
      }
      
      function actionFormatter(cellContent, row){
        return(
          <Button
            type="button"
            color="info"
            className="btn-sm btn-rounded"
            onClick={()=>{ handleClickDetails(row) }}
          >
            Details
          </Button>
        )
      }
      function statusFormatter(cellContent, row) {
        if (row.state == "Enabled") {
          return (
            <div>
                <Badge className="me-2 bg-success">
                { cellContent }
                </Badge>
            </div>
          );
        }else{
          return (
            <div>
                <Badge className="me-2 bg-danger">
                { cellContent }
                </Badge>
            </div>
          );
        }
      }

        return (
            <>
            <LoadingOverlay
                active={isLoading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>System Users | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="System Users" />
                    <Row>



                                <Modal isOpen={modal_staff} toggle={() => { tog_staff(); }}  size="xl" centered  backdrop={'static'}  >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">{ isEdit? `Update Staff`:`Add Staff` }</h5>
                                    <button type="button"  onClick={() => { setmodal_staff(false);  }}  className="close" data-dismiss="modal"aria-label="Close">
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                  <Row>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Employee no</label>
                                                <input type='text' className="form-control" value={employeeNo} onChange={(e)=>setEmployeeNo(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>First name</label>
                                                <input type='text' className="form-control" value={firstName} onChange={(e)=>setFirstName(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Last name</label>
                                                <input type='text' className="form-control" value={lastName} onChange={(e)=>setLastName(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Other names</label>
                                                <input type='text' className="form-control" value={otherNames} onChange={(e)=>setOtherNames(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Email</label>
                                                <input type='text' className="form-control" value={email} onChange={(e)=>setEmail(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>NAV Username</label>
                                                <input type='text' className="form-control" value={navUsername} onChange={(e)=>setNavUsername(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Access level</label>
                                                <select className="form-select" value={level} onChange={(e)=>setLevel(e.target.value)}>
                                                    <option value={``}></option>
                                                    <option value="User">User</option>
                                                    <option value="Admin">Admin</option>
                                                </select>
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Status</label>
                                                <select className="form-select" value={eStatus} onChange={(e)=>setEStatus(e.target.value)}>
                                                    <option value={``}></option>
                                                    <option value="Enabled">Enabled</option>
                                                    <option value="Disabled">Disabled</option>
                                                </select>
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="mb-3">
                                                <label>Change password on next login</label>
                                                <select className="form-select" value={changePass} onChange={(e)=>setChangePass(e.target.value)}>
                                                    <option value={``}></option>
                                                    <option value="No">No</option>
                                                    <option value="Yes">Yes</option>
                                                </select>
                                            </div>
                                        </Col>
                                  </Row>
                                  </div>
                                  
                                  <div className="modal-footer">
                                  {isEdit && (!isLoading?<button type="button" className="btn btn-danger" onClick={handleResetPassword}>Reset user password</button>:<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)}
                                  {isLoading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleSaveStaff()}>Save staff details</button> )
                                  }</div>
                                </Modal>
          

            <Col lg={12}>
              <Card>
                <CardBody>
                   {uList && (uList.length > 0) && (
                    <ToolkitProvider
                      keyField="no"
                      data={ uList }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className="mb-2">
                              <Col sm="4">
                                <div className="search-box me-2 mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="8">
                              <div className="text-sm-end">
                                  <Link
                                    className="btn btn-primary"
                                    to="#"
                                    onClick={handleClickAdd}
                                  >
                                    <i className="mdi mdi-plus me-1" />
                                    Add User
                                  </Link>
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                
                            
                                <BootstrapTable 
                                  keyField='no' 
                                  data={ uList } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table-sm align-middle table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  )}
                  {uList && (uList.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Users Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                      <div className="text-center mt-4">
                          <Link
                          to="/add-leave-request"
                          className="btn btn-primary  btn-sm"
                          >
                          Add System User <i className="mdi mdi-arrow-right ms-1" />
                          </Link>
                      </div>
                  </div>

                  )}
                </CardBody>
              </Card>
            </Col>

            

          </Row>

                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default SystemUsers;