import React, {useEffect, useState}  from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import {     
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    Button,
    Badge,
    Form,
    Input,
    InputGroup, Container } from "reactstrap";
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";
import Select from 'react-select';
import { Link, useHistory} from "react-router-dom"

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import axios from 'axios';
import { HRConfig, formatDate, TOAST_OPTIONS } from "../../helpers/backend_helper"
import { APPNAME, IPAS_X  } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";


import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import PropTypes from "prop-types";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const IPAs = () => {
        
        const [ipaList, setIpaList] = useState([])
        const [isLoading, setIsLoading] = useState(false)

        useEffect(() => {
            let obj = JSON.parse(localStorage.getItem("authUser"));
            let EmployeeNo = obj.uid;
            setIsLoading(true)
            axios.get(`${IPAS_X}?$filter=EmployeeNo eq '${EmployeeNo}'&$expand=ipaLines`, HRConfig).then(function(response){
                setIpaList(response.data.value);
                setIsLoading(false)
            }).catch((e)=>{
                setIsLoading(false)
                console.log(e)
            })
          }, []);
     
        const history = useHistory();
        
        const { SearchBar } = Search;
        const columns = [
        {
          dataField: 'No',
          text: 'Plan No',
          sort: true,
          formatter: noFormatter
        },
        {
          dataField: 'EmployeeNo',
          text: 'No',
          sort: true
        }, 
        {
          dataField: 'AppraisalPeriod', 
          text: 'Period',
      
          sort: true,
          //formatter: periodFormatter
        }, 
        {
          dataField: 'PerformanceYear', 
          text: 'Year',
          sort: true
        }, 
        {
          dataField: 'PostingDate',
          text: 'Posting Date',
          sort: true
        },
        {
            dataField: 'ConvertToAppraisal',
            text: 'PA',
            sort: true,
            formatter: paFormatter
          },  
        {
          dataField: "planlines",
          isDummyField: true,
          text: "Lines",
          formatter: linesFormatter
        },
        {
          dataField: 'Status',
          text: 'Status',
          sort: true,
          formatter: statusFormatter
        },
        {
          dataField: "action",
          isDummyField: true,
          text: "",
          formatter: actionFormatter
        }];
  
        const defaultSorted = [{
          dataField: 'No',
          order: 'desc'
        }];
  
        function periodFormatter(row){
          return(
            <strong>{row.AppraisalPeriod} {row.PerformanceYear}</strong>
          )
        }
        function linesFormatter(cellContent, row){
          return(
            <strong>{row.ipaLines.length}</strong>
          )
        }
        function noFormatter(cellContent, row){
          return(
            <strong>{cellContent}</strong>
          )
        }
  
        function actionFormatter(cellContent, row){
          return(
            <Button
              type="button"
              color="info"
              className="btn-sm btn-rounded"
              onClick={() => {history.push(`/individual-performance-agreement-details/${row.SystemId}`); } }
            >
              Details
            </Button>
          )
        }
        function statusFormatter(cellContent, row) {
          if (row.Status == "Pending Approval") {
            return (
              <div>
                  <Badge className="me-2 bg-danger">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else if (row.Status == "Approved"){
            return (
              <div>
                  <Badge className="me-2 bg-success">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else{
            return (
              <div>
                  <Badge className="me-2 bg-warning">
                  { cellContent }
                  </Badge>
              </div>
            );
          }
        }
        function paFormatter(cellContent, row) {
          if (row.ConvertToAppraisal == "No") {
            return (
              <div>
                  <Badge className="me-2 bg-secondary">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else{
            return (
              <div>
                  <Badge className="me-2 bg-success">
                  { cellContent }
                  </Badge>
              </div>
            );
          }
        }
        return (
            <>
            <LoadingOverlay
            active={isLoading}
            spinner={<BounceLoader />}
            text='Please wait...'
            >
                <div className="page-content">
                    <MetaTags>
                    <title>Individual Performance Agreement | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Individual Performance Agreement " />
                        
                         
                      <Row>
            <Col lg={12}>
              <Card>
                <CardBody>
                  <CardTitle></CardTitle>
                  <CardSubtitle className="mb-3">
                    
                  </CardSubtitle>

                 {ipaList && (ipaList.length > 0) && (
                  <ToolkitProvider
                      keyField="No"
                      data={ ipaList }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className='mb-5'>
                              <Col lg={8}>
                                  <div  className="rounded bg-light d-grid gap-2">
                                    <SearchBar {...toolkitProps.searchProps} className="form-control bg-transparent border-0 searchTm m-0" />
                                  </div>
                              </Col>
                              
                                
                              <Col lg={4}> 
                                <div className="d-grid gap-2">
                                  <Link
                                    className="btn btn-primary"
                                    to="/add-individual-performance-agreement"
                                  >
                                    <i className="mdi mdi-plus me-1" />
                                    Add IPA
                                  </Link>
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ ipaList } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table align-middle table-nowrap table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             </div>
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  

                  )}
                  {ipaList && (ipaList.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Individual Performance Agreement Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                      <div className="text-center mt-4">
                          <Link
                          to="/add-individual-performance-agreement"
                          className="btn btn-primary  btn-sm"
                          >
                          Create Individual Performance Agreement <i className="mdi mdi-arrow-right ms-1" />
                          </Link>
                      </div>
                  </div>

                  )}
                  
                </CardBody>
              </Card>
            </Col>
          </Row>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default IPAs;