export const formatDate = date => {
  var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

  if (month.length < 2) 
      month = '0' + month;
  if (day.length < 2) 
      day = '0' + day;

  return [year, month, day].join('-');
}

export const yearsPast = (initialDate) => { 
  if(initialDate != '0001-01-01'){
    var years = Math.abs(new Date(new Date() - new Date(initialDate)).getUTCFullYear() - 1970);
    return years;
  }else{
    return '-'
  }
}

