import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert,UncontrolledDropdown, DropdownToggle,DropdownMenu, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { IPAS_X, IPALINE, SENDIPA4APPROVAL, CANCELIPAAPPROVAL, EMPLOYEES, CONVERT2PA, APPNAME } from 'helpers/url_helper';
import * as url from "../../helpers/url_helper"
import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Timeline from './Timeline';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const IPADetails = () => {
        const {id} = useParams();
        const history = useHistory();
        toastr.options = TOAST_OPTIONS;
        const [rows1, setrows1] = useState([{ id: 1 }]);
        //const [th, setTh] = useState(['Customer','Financial','Internal Business Process','Learning and Growth'])
        const [th, setTh] = useState(['Customer/Stakeholder','Financial Stewardship','Internal Business Process','Organization Capacity'])
        const [periods , setPeriods] = useState(['January-June', 'July-December'])
        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [etag, setEtag] = useState('');
        const [systemId, setSystemId] = useState('');
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')
        const [ipaStatus, setIpaStatus] = useState('')
        const [ipaDate, setIpaDate] = useState('')
        const [ipaPerformanceYear, setIpaPerformanceYear] = useState('')
        const [ipaConvert, setIpaConvert] = useState('')
        const [appraisalPeriod, setAppraisalPeriod] = useState('')
        const [appraiserNo, setAppraiserNo] = useState('')
        const [appraiserName, setAppraiserName] = useState('')
        const [appraiserTitle, setAppraiserTitle] = useState('')
        const [documentType, setDocumentType] = useState('')
        const [performanceType, setPerformanceType] = useState('')
        const [stage, setStage] = useState('')
        const [lines, setLines] = useState([])
        const [st, setSt] = useState(0)
        const [isEdit, setIsEdit] = useState(false)
        const [cycles, setCycles] = useState(['Probation', '1st Cycle', '2nd Cycle', 'End of Contract'])
        const [cycle, setCycle] = useState('')

        const [perspective, setPerspective] = useState('')
        const [objective, setObjective] = useState('')
        const [individualObjective, setIndividualObjective] = useState('')
        const [initiatives, setInitiatives] = useState('')
        const [deliverables, setDeliverables] = useState('')
        const [mDate, setMDate] = useState('')
        const [weight, setWeight] = useState('')

        const [lineEtag, setLineEtag] = useState('')
        const [lineSystemId, setLineSystemId] = useState('')

        const [modal_line, setmodal_line] = useState(false)
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
        }
        const tog_line = () => {
            setmodal_line(!modal_line);
            removeBodyCss();
        }
        

        const fetchData = () => {
            setIsloading(true)
            axios.get(IPAS_X + "("+ id +")?$expand=ipaLines", HRConfig)
                .then(function(response){
                    //console.log(response)
                    axios.get(`${EMPLOYEES}?$filter=No eq '${response.data.EmployeeNo}'`, HRConfig).then((r)=>{ 
                        setEmpName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setEmpTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    axios.get(`${EMPLOYEES}?$filter=No eq '${response.data.Appraiser}'`, HRConfig).then((r)=>{ 
                        setAppraiserName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setAppraiserTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    setEmpNo(response.data.EmployeeNo)
                    
                    setAppraiserNo(response.data.Appraiser)
                   
                    setIpaDate(response.data.PostingDate)
                    setIpaPerformanceYear(response.data.PerformanceYear)
                    setIpaStatus(response.data.Status)
                    setIpaConvert(response.data.ConvertToAppraisal)
                    setAppraisalPeriod(response.data.AppraisalPeriod)
                    setIpaNo(response.data.No)
                    setDocumentType(response.data.DocumentType)
                    setPerformanceType(response.data.PerformanceType)
                    setStage(response.data.Stage)
                    setCycle(response.data.appraisalCycle)
                    setLines(response.data.ipaLines)
                    if(response.data.Status == 'Open'){
                        setSt(0);
                    }else if(response.data.Status == 'Pending Approval'){
                        setSt(1);
                    }
                    setSystemId(response.data.SystemId)
                    setEtag(response.data['@odata.etag'])
                    setIsloading(false)
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setUserError(true);
                    setIsloading(false)
                })
                
        }
        useEffect(() => {
           fetchData();
        },[]);

        const handleSendForApproval = () => {
            setIsloading(true)
            const lNo = {'no': ipaNo}
            axios.post(SENDIPA4APPROVAL , JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success(response.data.value ); 
                        //window.location.reload();
                        fetchData()
                    }else{
                        toastr.success("Could not Post IPA" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    console.log(error)
                    setIsloading(false)
                })
        }
        const handleCancelApproval = () => {
            setIsloading(true)
            const lNo = {'no': ipaNo}
            axios.post(CANCELIPAAPPROVAL, JSON.stringify(lNo), HRConfig)
                .then(function(response){      
                    if(response.status == '200'){
                        toastr.success(response.data.value); 
                        //history.push("/leave-plans");
                        //window.location.reload();
                        fetchData()
                    }else{
                        toastr.success("Could not Cancel Approval Request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                })
                
        }

        const handleConverToAppraisal = () =>{
            setIsloading(true)
            const lNo = {'no': ipaNo}
            axios.post(CONVERT2PA, JSON.stringify(lNo), HRConfig)
                .then(function(response){  
                    //console.log(response)  
                    if(response.status == '204'){
                        toastr.success(response.data.value); 
                        history.push("/performance-appraisals");
                        //window.location.reload();
                        fetchData()
                    }else{
                        toastr.success("Could not Convert To Performance Appraisal" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                })
        }

        const handleFillAppraisal = () =>{
            history.push("/performance-appraisals");
        }
        const handleDownload = async () => {
            const data = {no:ipaNo}
            setIsloading(true)
            axios.post(url.PRINTIPA, JSON.stringify(data), HRConfig).then((r)=>{
              const urlx = process.env.REACT_APP_APACHE_URL + r.data.value
                axios({
                  url: urlx,
                  method: 'GET',
                  responseType: 'blob',
                })
                  .then(response => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', 'Individual-Performance-Agreements.pdf');
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                    setIsloading(false)
                  })
                  .catch(error => {
                    console.error('Error downloading file:', error);
                    setIsloading(false)
                  });
              
              setIsloading(false)
            }).catch((e)=>{
              console.log(e)
              setIsloading(false)
            })
          }

        const handleDeleteIPA = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : etag
                }
              }
            axios.delete(`${IPAS_X}(${systemId})`, Config).then((r)=>{
                setIsloading(false)
                toastr.success('IPA deleted')
                history.push('/individual-performance-agreement')
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleUpdateLine = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lineEtag
                }
            }
            const data = {
                "Perspective"       : perspective,
                "strategicObjective": objective,
                "individualObjective": individualObjective,
                "Initiatives"       : initiatives,
                "Measures"          : deliverables,
                "TargetDate"        : formatDate(mDate),
                "Weights"           : parseInt(weight)
            }            
            axios.patch(`${IPALINE}(${lineSystemId})`, JSON.stringify(data), Config).then((r)=>{
                setIsloading(false)
                toastr.success('IPA line updated')
                tog_line()
                fetchData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleAddLine = () =>{
            setIsloading(true)

            const data = {
                "DocumentNo"        : ipaNo,
                "Perspective"       : perspective,
                "strategicObjective": objective,
                "individualObjective": individualObjective,
                "Initiatives"       : initiatives,
                "Measures"          : deliverables,
                "TargetDate"        : formatDate(mDate),
                "Weights"           : parseInt(weight)
            }  
            if((parseInt(weight) > 100) || (parseInt(weight) < 0)){
                toastr.error('Weight should be between 0 - 100')
            }else{
                axios.post(`${IPALINE}`, JSON.stringify(data), HRConfig).then((r)=>{
                    setIsloading(false)
                    toastr.success('IPA line added')
                    tog_line()
                    fetchData()
                }).catch((e)=>{
                    console.log(e)
                    setIsloading(false)
                })
            }         
            
        } 
        const handleDeleteLine = () =>{
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lineEtag
                }
              }
            axios.delete(`${IPALINE}(${lineSystemId})`, Config).then((r)=>{
                setIsloading(false)
                toastr.success('IPA line deleted')
                tog_line()
                fetchData()
            }).catch((e)=>{
                console.log(e)
                setIsloading(false)
            })
        }
        const handleClickLineDetails = (key) =>{
            setIsEdit(true)
            tog_line()
            setPerspective(lines[key].Perspective)
            setObjective(lines[key].strategicObjective)
            setIndividualObjective(lines[key].individualObjective)
            setInitiatives(lines[key].Initiatives)
            setDeliverables(lines[key].Measures)
            setMDate(lines[key].TargetDate)
            setWeight(lines[key].Weights)
            setLineEtag(lines[key]['@odata.etag'])
            setLineSystemId(lines[key].SystemId)
        }
        const clearLineData = () =>{
            setPerspective('')
            setObjective('')
            setIndividualObjective('')
            setInitiatives('')
            setDeliverables('')
            setMDate('')
            setWeight('')
            setLineEtag('')
            setLineSystemId('') 
        }
        return (
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
            <div className="page-content">
            <MetaTags>
            <title>IPA Details | {APPNAME}</title>
            </MetaTags>
            <Container fluid={true}>
                <Breadcrumbs title={APPNAME} breadcrumbItem="Individual Performance Agreement" />
                <div>


                                <Modal isOpen={modal_line} toggle={() => { tog_line(); }}  size="l" centered  backdrop={'static'}  >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">{isEdit?`Update/Delete IPA Line`:`Add IPA Line` }</h5>
                                    <button type="button"  onClick={() => { setmodal_line(false);  }}  className="close" data-dismiss="modal"aria-label="Close">
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                  <Row>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Perspective and Strategic Thematic Areas</label>
                                                <select className="form-select" value={perspective} onChange={(e)=>setPerspective(e.target.value)}>
                                                <option value=""></option>
                                                {th.map((t,k)=>(
                                                    <option value={t} key={k}>{t}</option>
                                                ))}
                                                </select>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Strategic Objective/KRAs</label>
                                                <textarea rows={3} className="form-control" value={objective} onChange={(e)=>setObjective(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Individual Objectives</label>
                                                <textarea rows={3} className="form-control" value={individualObjective} onChange={(e)=>setIndividualObjective(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Initiatives/Priority Strategies</label>
                                                <textarea rows={3} className="form-control"value={initiatives} onChange={(e)=>setInitiatives(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Measures/Deliverables</label>
                                                <textarea rows={3} className="form-control" value={deliverables} onChange={(e)=>setDeliverables(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Target/Milestone Date</label>
                                                <Flatpickr
                                                    className="form-control pD"
                                                    options={{
                                                        altInput: true,
                                                        altFormat: "F j, Y",
                                                        dateFormat: "Y-m-d",
                                                        disabled: false
                                                    }}
                                                    value={mDate}
                                                    onChange={(date) => setMDate(date)}
                                                    />
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Weight</label>
                                                <input type='number' className="form-control" value={weight} onChange={(e)=>setWeight(e.target.value)}/>
                                            </div>
                                        </Col>
                                  </Row>
                                  </div>
                                  
                                  <div className="modal-footer justify-content-center">
                                  {isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-danger" onClick={()=>handleDeleteLine()}>Delete IPA line...</button> ))
                                  }
                                  {isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleUpdateLine()}>Update line details...</button> ))
                                  }
                                  {!isEdit && (isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleAddLine()}>Save line details...</button> ))
                                  }</div>
                                </Modal>
        

                
                <Row>
                <Col xl={4}>
                <Card>
                    <CardBody>
                    <CardTitle>Employee Details</CardTitle>
                            <Row>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Employee No</label>
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {empNo}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Name</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={empName}   />
                                
                                </div>
                            </Col>
                            </Row>
                            <Row>
                      
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraisee Title</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={empTitle}  />
                                
                                </div>
                            </Col>
                            <hr/>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Employee No</label>
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {appraiserNo}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Name</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserName}   />
                                
                                </div>
                            </Col>
                            </Row>
                            <Row>
                      
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Appraiser Title</label>
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserTitle}  />
                                
                                </div>
                            </Col>
                           
                        </Row>
                        </CardBody>
                    </Card>

                    </Col>
                    <Col  xl={5}>
                    <Card>
                        <CardBody>
                        <CardTitle className="mb-3">IPA Details</CardTitle>
                        <Row>
                        <Col md={6}>
                                <div className="mb-3">
                                <label>No</label>
                                <input  type="text" className="form-control dNo" defaultValue={ipaNo} disabled={true}  />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Document Type</label>
                                <input  type="text" className="form-control c2A" defaultValue={documentType} disabled={true}   />
                                
                                </div>
                            </Col>
                    
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Status</label>
                                <input  type="text" className="form-control sT" defaultValue={ipaStatus} disabled={true}  />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Posting Date</label>
                                <input  type="date" className="form-control c2A" defaultValue={ipaDate} disabled={true}   />
                                
                                </div>
                            </Col>
                        
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Performance Year</label>
                                <input  type="text" className="form-control pY" defaultValue={ipaPerformanceYear} disabled={true}   />
                                
                                </div>
                            </Col>
                           
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Convert To Appraisal</label>
                                <input  type="text" className="form-control c2A" defaultValue={ipaConvert} disabled={true}   />
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Performance Type</label>
                                <input  type="text" className="form-control c2A" defaultValue={performanceType} disabled={true}   />
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Appraisal Period</label>
                                <select className="form-select aPd" disabled={true} >
                                            <option value={appraisalPeriod} >{appraisalPeriod}</option>
                                            {periods.map((a,k)=>(
                                                <option value={a} key={k}>{a}</option>
                                            ))}
                                        </select>
                                
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="mb-3">
                                <label>Appraisal Cycle</label>
                                <select className="form-select c2A" value={cycle} onChange={(e)=>setCycle(e.target.value)} disabled={true}>
                                    <option value={``}></option>
                                    {cycles.map((c,k)=>(
                                        <option value={c} key={k}>{c}</option>
                                    ))}
                                </select>
                                
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="mb-3">
                                <label>Stage</label>
                                <input  type="text" className="form-control c2A" defaultValue={stage} disabled={true}   />
                                
                                </div>
                            </Col>
                            </Row>
                        
                        </CardBody>
                    </Card>

                    </Col>
                    <Col  xl={3}>
                        <Timeline stage={st}/>
                    </Col>
                    <Col>
                   
                        
                        <div>
                                {(lines || []).map((formRow, key) => (
                                    
                              <Card key={key}>
                                <CardBody>
                                <div>
                                    <Row className="mb-3 align-items-center">
                                    <Col sm={(ipaStatus == 'Open') ? 9:12}>
                                        <div className="mt-2">
                                        <h5>IPA Line {key+1}</h5>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Perspective and Strategic Thematic Areas:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Perspective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Strategic Objective/KRAs:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.strategicObjective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Individual Objectives:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.individualObjective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Initiatives/Priority Strategies:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Initiatives}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Measures/Deliverables:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Measures}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Target/Milestone Date</h4> </Col>
                                            <Col sm={12}><h4><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.TargetDate} </h4></Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Weight</h4> </Col>
                                            <Col sm={12}><h4><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Weights}</h4></Col>
                                         </Row>
                                        
                                         
                                        </div>
                                    </Col>
                                    {(ipaStatus == 'Open') && (
                                    <Col sm={3}>
                                        <div className="float-sm-end d-flex align-items-center">
                
                                            
                                                <Button
                                                    type="button"
                                                    color="info"
                                                    className="btn-sm btn-rounded"
                                                    onClick={()=>handleClickLineDetails(key)}
                                                    >
                                                    Edit/Delete IPA Line
                                                </Button>
                                            
                                   
                                        </div>
                                    </Col>
                                    )}
                                    </Row>
                                </div>
                        
                
                                    </CardBody>
                                    </Card>
                                    
                                ))}
                                <Row className='justify-content-center'>
                                {(ipaStatus == 'Pending Approval') && (
                                    <Col md={3}>
                                        <div className="d-grid gap-2">
                                        
                                            <input
                                                type="button"
                                                className="btn btn-danger mt-3 mt-lg-0"
                                                value="Cancel Approval Request"
                                                onClick={handleCancelApproval}
                                            />
                                      
                                        </div>
                                    </Col>
                                    ) }
                                    {(ipaStatus == 'Open') && (
                                        <>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn btnSubmitPlan" onClick={handleDeleteIPA} >
                                                    Delete IPA
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn btnSubmitPlan" onClick={()=>{setIsEdit(false); clearLineData(); tog_line()}} >
                                                    Add IPA Line
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={3} className='mb-2'>
                                            <div className="d-grid gap-2">
                                                <input
                                                    type="button"
                                                    className="btn btn-success"
                                                    value="Send For Approval"
                                                    onClick={handleSendForApproval}
                                                />
                                            </div>
                                        </Col>
                                        </>
                                    ) }
                                    
                                    </Row>

                                    <Row className='justify-content-center'>
                                    <Col md={3} className='mb-1'>
                                    {((ipaStatus == 'Approved') && (ipaConvert == 'No')) && (
                                        <div className="d-grid gap-2">
                                            <Button color="warning" type="button" className="btn" onClick={handleConverToAppraisal} >
                                                Convert Into Performance Appraisal
                                            </Button>
                                        </div>
                                    ) }
                                    {((ipaStatus == 'Approved') && (ipaConvert == 'Yes')) && (
                                        <div className="d-grid gap-2">
                                            <Button color="secondary" type="button" className="btn" onClick={handleFillAppraisal} >
                                                Fill Performance Appraisal Form
                                            </Button>
                                        </div>
                                    ) }
                                    </Col>
                                    {(ipaStatus == 'Approved') && (
                                        <Col md={3}>
                                        <div className="d-grid gap-2">
                                            <Button color="primary" type="button" className="btn" onClick={handleDownload} >
                                                Print IPA
                                            </Button>
                                        </div> 
                                        </Col>
                                    )}
                                    </Row>
                                    </div>
                                    <br/>
      
                                   
            
                </Col>
                </Row>

                </div>


                
                   

            </Container>
        </div>
        </LoadingOverlay>
        );
    }

export default IPADetails;