import React , {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle, Container } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";


import Calendar from "rc-year-calendar";
import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { APPNAME, LEAVE_PLAN_LINES } from '../../helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";



    const LeaveCalendar = () => {
        const [calDetails, setCalDetails] = useState([]);
        const [selectedDate, setSelectedDate] = useState('');
        const [leaveRequests, setLeaveRequests] = useState([]);

        toastr.options = {
            preventDuplicates: true,
            positionClass: "toast-top-center",
            closeButton: true,
            newestOnTop: true,
            autoClose: 45000,
            pauseOnHover: true,
            draggable: true
        }
        useEffect(() => {
            axios.get(LEAVE_PLAN_LINES, HRConfig).then(function(response){
              setLeaveRequests(response.data.value);
            })
          }, []);
      const cOptions = [];
      leaveRequests.map((lR, key) => {
        const randomColor = "#"+Math.floor(Math.random()*16777215).toString(16);
        cOptions.push({
                id: lR.DocumentNo,
                name: lR.EmployeeName,
                location: lR.LeaveType,
                startDate: new Date(lR.StartDate),
                endDate: new Date(lR.EndDate),
                //color: randomColor
            });
        } );

      

        
        return (
            <>
                <div className="page-content">
                    <MetaTags>
                    <title>Leave Calendar | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Leave Calendar" />
                        <Row>
                        <Col lg="12">
                                <Card>
                                <CardBody>
                                    {calDetails.length > 0 && (
                                        calDetails.map((a,k)=>(
                                            <CardTitle className="mb-4" style={{textAlign:'center' }} key={k}>{a.id} : {a.location}{" "}{a.name}</CardTitle>
                                        ))
                                    )}
                                    {calDetails.length < 1 && (
                                            <CardTitle className="mb-4" style={{textAlign:'center'}}>Click On any Date to View Details</CardTitle>
                                    )}
                                </CardBody>
                                </Card>
                            <Card>
                                <CardBody>
                                    

                                        <Calendar
                                            className="calendar" 
                                            style="background"
                                            dataSource={cOptions}
                                            enableRangeSelection={true}
                                            enableContextMenu={true} 
                                            displayWeekNumber={true}
                                            onDayClick={(e)=>{
                                                setSelectedDate(formatDate(e.date));
                                                setCalDetails(e.events)
                                                
                                                if(e.events.length > 0){
                                                    let str = "";
                                                    e.events.map((a,k)=>(
                                                        toastr.info("<strong>"+a.location+"</strong> : "+a.name)
                                                    ));
                                                    
                                                }

                                            }} 
                                            />
                                </CardBody>
                        </Card>
                        </Col>
                        
                        
                        </Row>
                        
                           

                    </Container>
                </div>
            </>
        );
    }

export default LeaveCalendar;