import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';
import { useParams } from 'react-router-dom';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Select from 'react-select';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const ApprovePurchaseRequisition = () => {
        
        const history = useHistory();
        const {no,id} = useParams();

        const [rows1, setrows1] = useState([{ id: 1 }]);
        const [formRows, setFormRows] = useState([{ id: 1 }]);

        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [budgets, setBudgets] = useState([])
        const [currencies, setCurrencies] = useState([])
        const [locations, setLocations] = useState([])
        
        const [gLAccounts, setGLAccounts] = useState([])
        const [items, setItems] = useState([])
        const [fixedAssets, setFixedAssets] = useState([])
        const [unitOfMeasure, setUnitOfMeasure] = useState([])
        const [vendors, setVendors] = useState([])

        const [rNo, setRNo] = useState("")
        const [form, setForm] = useState("")
        const [subject, setSubject] = useState("")
        const [postingDate, setPostingDate] = useState("")
        const [suppliesWorksServices, setSuppliesWorksServices] = useState("")
        const [budgetName, setBudgetName] = useState("")
        const [procurementPlanReference, setProcurementPlanReference] = useState("")
        const [currencyCode, setCurrencyCode] = useState("")
        const [locationCode1, setLocationCode1] = useState("")
        const [RStatus, setRStatus] = useState("")
       
        const [showApproveBtn, setShowApproveBtn] = useState(true);
        const [showRejectBtn, setShowRejectBtn] = useState(false);
        const [comment, setComment] = useState('')

        const [purchaseRequisitionLines, setPurchaseRequisitionLines] = useState([])

        const [noo, setNoo] = useState([])

        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')

        const [modal_item, setmodal_item] = useState(false);
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
          }
          const tog_item = () => {
            setmodal_item(!modal_item);
            removeBodyCss();
          }

        toastr.options = TOAST_OPTIONS;
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleActionChange = () => {
            const selectedAction = document.getElementsByClassName('lAction')[0].value;
            if(selectedAction == 'APPROVE'){
                setShowApproveBtn(true)
                setShowRejectBtn(false)
            }else if(selectedAction == 'REJECT'){
                setShowApproveBtn(false)
                setShowRejectBtn(true)
            }
        }
        const handleApprovePlan = () => {
            setIsloading(true)
            const lNo = {'no': rNo}
            axios.post(url.APPROVE_LP, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("Purchase Requisition Approved" );
                        history.push(`/approved/REQ`) 
                    }else{
                        toastr.success("Could not Approve" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                })     
        }   
        const handleReject = () =>{
            setIsloading(true)
            const lNo = {"no": rNo, "rejectioncomment":comment}
            axios.post(url.REJECT_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){ 
                    console.log(response)
                    if(response.status == '200'){
                        toastr.success(response.data.value);
                        history.push(`/rejected/REQ`) 
                    }else{
                        toastr.success("Could not Approve Purchase" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    console.log(error)
                    setIsloading(false)
            })  
        }   

        useEffect(() => {
            setIsloading(true)
                axios.get(`${url.PURCHASES1}?$filter=No eq '${no}/${id}'&$expand=purchaseRequisitionLines`, HRConfig)
                    .then(function(r){
                       setRNo(r.data.value[0].No)
                       setEmpNo(r.data.value[0].RequestByNo)
                       setEmpName(r.data.value[0].RequestByName)
                       setSubject(r.data.value[0].SubjectOfProcurement)
                       setForm(r.data.value[0].FormType)
                       setPostingDate(r.data.value[0].PostingDate)
                       setSuppliesWorksServices(r.data.value[0].SuppliesWorksServices)
                       setBudgetName(r.data.value[0].BudgetName)
                       setProcurementPlanReference(r.data.value[0].ProcurementPlanReference)
                       setCurrencyCode(r.data.value[0].CurrencyCode)
                       setLocationCode1(r.data.value[0].LocationCode)
                       setPurchaseRequisitionLines(r.data.value[0].purchaseRequisitionLines)
                       setRStatus(r.data.value[0].Status)
                       axios.get(url.EMPLOYEES + "?$filter=No eq '"+ r.data.value[0].RequestByNo +"'", HRConfig)
                       .then(function(response){
                           if(response.data.value.length > 0){
                               setEmpTitle(response.data.value[0].JobTitle);
                               setUserError(false);
                               
                           }else{
                               toastr.error("Employee Details Not Found in Business Central");
                           }
                           setIsloading(false)
                       }).catch(function (error) {
                           toastr.error(error.response.data.error.message);
                           setUserError(true);
                           console.log(error);
                           setIsloading(false)
                       })
                      
                    }).catch(function (error) {
                        //toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                        setIsloading(false)
                })
                return () => {
                    setIsloading(false)
                };
        
        },[]);
        //console.log(noo)

        
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Approve Purchase Requisition| {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Approve Purchase Requisition" />
                        <div>
                        {userError && ( 
                        <Alert color="danger" role="alert">
                                    Details Not Found in  Business Central
                        </Alert>
                        )}

                        {(ipaSaved == false) ? (
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Requisitioner Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div  className="mb-3">
                                        <label>No</label>
                                        <input type="text" className="form-control EmployeeNo" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div  className="mb-3">
                                        <label>Name</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        </div>
                                    </Col>
                                
                                    <Col md={12}>
                                        <div  className="mb-3">
                                        <label>Title</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        
                                        </div>
                                    </Col>
                                   
                                </Row>
                                
                                
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                            <CardBody>
                            <CardTitle>Requisition Details</CardTitle>
                                <Row>
                                <Col md={6}>
                                        <div  className="mb-3">
                                        <label>No</label>
                                        <input  type="text" className="form-control" defaultValue={rNo} disabled={true}   />
                                        </div>
                                </Col>
                                <Col md={6}>
                                        <div  className="mb-3">
                                        <label>Status</label>
                                        <input  type="text" className="form-control" defaultValue={RStatus} disabled={true}   />
                                        </div>
                                </Col>
                                <Col md={6}>
                                        <div  className="mb-3">
                                        <label>Subject of Procurement</label>
                                        <input  type="text" className="form-control" defaultValue={subject} disabled={true}   />
                                        </div>
                                </Col>
                                <Col md={6}>
                                        <div  className="mb-3">
                                        <label>Form</label>
                                        <input  type="text" className="form-control" defaultValue={form} disabled={true}   />
                                        </div>
                                    </Col>
                                <Col md={6}>
                                        <div  className="mb-3">
                                        <label>Date</label>
                                        <input  type="text" className="form-control" defaultValue={postingDate} disabled={true}   />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div  className="mb-3">
                                        <label>Type</label>
                                        <input  type="text" className="form-control" defaultValue={suppliesWorksServices} disabled={true}   />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                    <div  className="mb-3">
                                        <label>Budget Name</label>
                                        <input  type="text" className="form-control" defaultValue={budgetName} disabled={true}   />
                                    </div>
                                    </Col>
                            
                                    <Col md={6}>
                                        <div   className="mb-3">
                                            <label>Procurement Plan Reference</label>
                                            <input  type="text" className="form-control" defaultValue={procurementPlanReference} disabled={true}   />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div  className="mb-3">
                                            <label>Currency</label>
                                            <input  type="text" className="form-control" defaultValue={currencyCode} disabled={true}   />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div  className="mb-3">
                                            <label>Location</label>
                                            <input  type="text" className="form-control" defaultValue={locationCode1} disabled={true}   />
                                        </div>
                                    </Col>
                                    
                                    </Row>
                                    </CardBody>
                                    </Card>
                                    {purchaseRequisitionLines.map((i, k)=>(
                                        <Row key={k}>
                                            <Col>
                                            <Card>
                                        <CardBody>
                                
                                        <CardTitle className="mb-3">
                                        Item  {k+1} 
                                        </CardTitle>
                                            <Row >
                        
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Type</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Type} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>No</label>
                                                    <input  type="text" className="form-control" defaultValue={i.No} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={6} className="mb-3">
                                                <div>
                                                <label>Description</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Description} disabled={true} />
                                                    
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Description 2</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Description2} disabled={true} />  
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Vendor</label>
                                                    <input  type="text" className="form-control" defaultValue={i.BuyfromVendorNo} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Location Code</label>
                                                    <input  type="text" className="form-control" defaultValue={i.LocationCode} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Unit Cost</label>
                                                    <input  type="text" className="form-control" defaultValue={i.DirectUnitCost} disabled={true} />   
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Unit Of Measure</label>
                                                    <input  type="text" className="form-control" defaultValue={i.UnitofMeasureCode} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3} className="mb-3">
                                                <div>
                                                    <label>Quantity</label>
                                                    <input  type="text" className="form-control" defaultValue={i.QuantitytoConvert} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={6} className="mb-3">
                                                <div>
                                                    <label>Market Price</label>
                                                    <input  type="text" className="form-control" defaultValue={i.MarketPrice} disabled={true} />
                                                </div>
                                            </Col>

                                            
                                            </Row>
                                            </CardBody>
                                            </Card>
                                            </Col>
                                        </Row>
                                    ))}
                                    <Card>
                                        <CardBody>
                                            <Row>
                                            <Col md={12}>
                                                <div className="mb-3">
                                                    <label> Select Action</label>
                                                    <select  className="form-select lAction" onChange={handleActionChange}>
                                                        
                                                        <option value="APPROVE">APPROVE</option>
                                                        <option value="REJECT">REJECT</option>
                                
                                                    </select>
                                                    
                                                </div>
                                            </Col>
                                            </Row>
                                            {showRejectBtn && (
                                                <Row>
                                                <Col md={12}>
                                                    <div className="mb-3">
                                                        <label>Rejection Comment</label>
                                                        <input name='rejectComment'  type="text" className="form-control rejectComment"  onChange={(e)=>setComment(e.target.value)}  />
                                                    </div>
                                                </Col>
                                                </Row>
                                            )}
                                        </CardBody>
                                    </Card>
                                    
                                  
                                            
                                       
                  
                                                    {showRejectBtn && (
                                                    <Col md={12} className="reject">
                                                        <div className="d-grid gap-2">
                                                            <Button color="danger" type="button" className="btn btnRejectRequest" onClick={handleReject}>
                                                                Reject
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                    )}
                                                    {showApproveBtn && (
                                                    <Col md={12} className="approve">
                                                        <div className="d-grid gap-2">
                                                            <Button color="success" type="button" className="btn btnApproveRequest" onClick={handleApprovePlan}>
                                                            Approve 
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                    )}

                                   
                                
                            </Col>
                            <Col>
                           
                                
                                    
                          
              
                                            
                                            <br/>
              
                                           
                    
                        </Col>
                        </Row>
                        ):(
                            <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>Saved</h5>
                                        <p className="text-muted">
                                                Requisition <strong>{rNo}</strong> Saved!
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        )}
                        </div>


                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
         

        );
    }

export default ApprovePurchaseRequisition;