import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import { IPAS, EMPLOYEES, APPNAME } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Timeline from './Timeline';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const IPAAdd = () => {
        const history = useHistory();
        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [empDept, setEmpDept] = useState('');
        const [employee, setEmployee] = useState([]);
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')

        toastr.options = TOAST_OPTIONS;
        const [th, setTh] = useState(['Customer/Stakeholder',' Financial Stewardship','Internal Business Process','Organization Capacity'])
        const [periods , setPeriods] = useState(['January-June', 'July-December'])
        const [cycles, setCycles] = useState(['Probation', '1st Cycle', '2nd Cycle', 'End of Contract'])
        const [cycle, setCycle] = useState('')

        const [perspective, setPerspective] = useState('')
        const [objective, setObjective] = useState('')
        const [individualObjective, setIndividualObjective] = useState('')
        const [initiatives, setInitiatives] = useState('')
        const [deliverables, setDeliverables] = useState('')
        const [mDate, setMDate] = useState('')
        const [weight, setWeight] = useState('')

        const [formRows, setFormRows] = useState([{ id: 1 }]);
        const [lines, setLines] = useState([])

        const [modal_line, setmodal_line] = useState(false)
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
        }
        const tog_line = () => {
            setmodal_line(!modal_line);
            removeBodyCss();
        }
        const clearLineData = () =>{
            setPerspective('')
            setObjective('')
            setIndividualObjective('')
            setInitiatives('')
            setDeliverables('')
            setMDate('')
            setWeight('')
        }
        const handleAddLine = () =>{
            clearLineData()
            tog_line()
        }
        const handleAddLineData = () =>{
            if(perspective === '' || objective === '' || individualObjective === '' || initiatives === ''){
                toastr.error('Fill out the form')
            }else if((parseInt(weight) > 100) || (parseInt(weight) < 0)){
                toastr.error('Weight should be between 0 - 100')
            }else{
                const data = {
                    "Perspective"       : perspective,
                    "strategicObjective": objective,
                    "individualObjective": individualObjective,
                    "Initiatives"       : initiatives,
                    "Measures"          : deliverables,
                    "TargetDate"        : formatDate(mDate),
                    "Weights"           : parseInt(weight)
                }   
                setLines(state =>[...state, data])
                tog_line()        
            }
        }
        const handleRemoveLine = (key) =>{
            const newItems = [...lines];
            newItems.splice(key, 1);
            setLines(newItems);
        }

        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            setIsloading(true);
 
            const data = {
                "EmployeeNo"        : empNo,
                "AppraisalPeriod"   : document.getElementsByClassName('aPd')[0].value,
                "PostingDate"       : formatDate(document.getElementsByClassName('pD')[0]._flatpickr.selectedDates[0]),
                "appraisalCycle"    : cycle,
                "ipaLines"          : lines 
            };
            
            axios.post(IPAS, data, HRConfig)
                .then(function(response){  
                    if(response.data.No){
                        toastr.success("IPA No "+ response.data.No+" created" ); 
                        history.push(`/individual-performance-agreement-details/${response.data.SystemId}`)
                        //setIpaSaved(true);
                        //setIpaNo(response.data.No)
                        setIsloading(false);
                    }else{
                        toastr.success("Could not IPA" )
                        setIsloading(false);
                    }
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false);
                })
                .then(function () {
                // always executed
                
            });
            
            
        }

        useEffect(() => {
            setIsloading(true)
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                const EmployeeNo = obj.uid;
                axios.get(EMPLOYEES + "?$filter=No eq '"+ EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                        setIsloading(false);
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setIsloading(false);
                        console.log(error);
                    })
               
    
            }
        },[]);

        
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Add IPAs | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Add Individual Performance Agreement" />
                        <div>
                        <Modal isOpen={modal_line} toggle={() => { tog_line(); }}  size="l" centered  backdrop={'static'}  >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">{`Add IPA Line` }</h5>
                                    <button type="button"  onClick={() => { setmodal_line(false);  }}  className="close" data-dismiss="modal"aria-label="Close">
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                  <Row>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Perspective and Strategic Thematic Areas</label>
                                                <select className="form-select" value={perspective} onChange={(e)=>setPerspective(e.target.value)}>
                                                <option value=""></option>
                                                {th.map((t,k)=>(
                                                    <option value={t} key={k}>{t}</option>
                                                ))}
                                                </select>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Strategic Objective/KRAs</label>
                                                <textarea rows={3} className="form-control" value={objective} onChange={(e)=>setObjective(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Individual Objectives</label>
                                                <textarea rows={3} className="form-control" value={individualObjective} onChange={(e)=>setIndividualObjective(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Initiatives/Priority Strategies</label>
                                                <textarea rows={3} className="form-control"value={initiatives} onChange={(e)=>setInitiatives(e.target.value)} />
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Measures/Deliverables</label>
                                                <textarea rows={3} className="form-control" value={deliverables} onChange={(e)=>setDeliverables(e.target.value)}/>
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Target/Milestone Date</label>
                                                <Flatpickr
                                                    className="form-control pD"
                                                    options={{
                                                        altInput: true,
                                                        altFormat: "F j, Y",
                                                        dateFormat: "Y-m-d",
                                                        disabled: false
                                                    }}
                                                    value={mDate}
                                                    onChange={(date) => setMDate(date)}
                                                    />
                                            </div>
                                        </Col>
                                        <Col md={12}>
                                            <div className="mb-3">
                                                <label>Weight</label>
                                                <input type='number' className="form-control" value={weight} onChange={(e)=>setWeight(e.target.value)}/>
                                            </div>
                                        </Col>
                                  </Row>
                                  </div>
                                  
                                  <div className="modal-footer justify-content-center">
                          
                                  {(isloading ? 
                                        (<button type="button" className="btn btn-secondary" disabled={true}>Please wait...</button>)
                                      : ( <button type="button" className="btn btn-success" onClick={()=>handleAddLineData()}>Save line details...</button> ))
                                  }</div>
                                </Modal>
        


                        {(ipaSaved == false) ? (
                        <Row>
                        <Col xl={9}>
                        <Card>
                            <CardBody>
                            <CardTitle>Employee Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Employee No</label>
                                        <input type="text" className="form-control EmployeeNo" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Employee Name</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Title</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        
                                        </div>
                                    </Col>
                                   
                                </Row>
                                <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Status</label>
                                        <input  type="text" className="form-control sT" defaultValue={"OPEN"} disabled={true}  />
                                        
                                        </div>
                                    </Col>
                                   
            
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Posting Date</label>
                                        <Flatpickr
                                                    className="form-control pD"
                                                    options={{
                                                        altInput: true,
                                                        altFormat: "F j, Y",
                                                        dateFormat: "Y-m-d",
                                                        disabled: true

                                                    }}
                                                    />
                                        
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Appraisal Period</label>
                                        <select defaultValue="0" className="form-select aPd">
                                                    <option></option>
                                                    {periods.map((a,k)=>(
                                                        <option value={a} key={k}>{a}</option>
                                                    ))}
                                                </select>
                                        
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Appraisal Cycle</label>
                                        <select className="form-select c2A" value={cycle} onChange={(e)=>setCycle(e.target.value)}>
                                            <option value={``}></option>
                                            {cycles.map((c,k)=>(
                                                <option value={c} key={k}>{c}</option>
                                            ))}
                                        </select>
                                        
                                        </div>
                                    </Col>
                                    </Row>
                                
                                </CardBody>
                            </Card>
                            <Row className='justify-content-center'>
                                                <Col md={4}>
                                                    <div className="d-grid gap-2">
                                                        <input
                                                            type="button"
                                                            className="btn btn-success mt-3 mt-lg-0"
                                                            value="Add IPA Line"
                                                            onClick={handleAddLine}
                                                        />
                                                    </div>
                                                </Col>
                                                <Col md={4}>
                                                    <div className="d-grid gap-2">
                                                        <Button color="primary" type="button" className="btn btnSubmitPlan" onClick={handleSubmit}>
                                                            Save IPA
                                                        </Button>
                                                    </div>
                                                </Col>
                                            </Row>

                            </Col>
                            <Col  xl={3}>
                                <Timeline stage={0}/>
                            </Col>
                            <Col>
                            
                           
                                
                                <div>
                                {(lines || []).map((formRow, key) => (
                                    
                                    <Card key={key}>
                                      <CardBody>
                                      <div>
                                          <Row className="mb-3 align-items-center">
                                          <Col sm={9}>
                                              <div className="mt-2">
                                              <h5>IPA Line {key+1}</h5>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Perspective and Strategic Thematic Areas:</h6></Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Perspective}</Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Strategic Objective/KRAs:</h6></Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.strategicObjective}</Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Individual Objectives:</h6></Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.individualObjective}</Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Initiatives/Priority Strategies:</h6> </Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Initiatives}</Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Measures/Deliverables:</h6> </Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Measures}</Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Target/Milestone Date</h6> </Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.TargetDate} </Col>
                                               </Row>
                                               <Row  className="m-1">
                                                  <Col sm={4}><h6>Weight</h6> </Col>
                                                  <Col sm={8}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Weights}</Col>
                                               </Row>
                                              
                                               
                                              </div>
                                          </Col>
                                          <Col sm={3}>
                                              <div className="float-sm-end d-flex align-items-center">
                      
                                                      <Button
                                                          type="button"
                                                          color="info"
                                                          className="btn-sm btn-rounded"
                                                          onClick={()=>handleRemoveLine(key)}
                                                          >
                                                          Remove IPA Line
                                                      </Button>
                                         
                                         
                                              </div>
                                          </Col>
                                          </Row>
                                      </div>
                              
                      
                                          </CardBody>
                                          </Card>
                                          
                                      ))}
                                    
                                           
                                            </div>
                                            <br/>
              
                                           
                    
                        </Col>
                        </Row>
                        ):(
                            <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>Saved</h5>
                                        <p className="text-muted">
                                                Individual Performance Agreement <strong>{ipaNo}</strong> Saved!
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        )}
                        </div>


                        
                           

                    </Container>
                    
                </div>
                </LoadingOverlay>

        );
    }

export default IPAAdd;