import React, {useEffect, useState} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Container } from "reactstrap";
import {
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    UncontrolledTooltip,
    CardText,
    Button,
    Badge,
    Form,
    Input,
    InputGroup,
  } from "reactstrap"
import { Link } from "react-router-dom"


//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import PropTypes from "prop-types";


import axios from 'axios';
import { HRConfig, formatDate, TOAST_OPTIONS } from "../../helpers/backend_helper"
import { APPNAME, APPROVALS } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'
import { useHistory } from 'react-router-dom';

    const Approvals = () => {
      const history = useHistory()

      toastr.options = TOAST_OPTIONS;
      const [approvals, setApprovals] = useState([]);
      const [isloading, setIsloading] = useState(false);

      useEffect(() => {
        
        if (localStorage.getItem("authUser")) {
          const obj = JSON.parse(localStorage.getItem("authUser"));
          const navUserName = obj.nav_username
          setIsloading(true)
          axios.get(APPROVALS + "?$filter=ApproverID eq '"+ navUserName +"' and Status eq 'Open'", HRConfig).then((r)=>{
          //axios.get(APPROVALS+ "?$filter=Status eq 'Open'", HRConfig).then((r)=>{
            setApprovals(r.data.value);
            setIsloading(false)
          }).catch((e)=>{
            console.log(e)
            setIsloading(false)
          })
        }
        
        
      },[]);
      
      const { SearchBar } = Search;
      const columns = [
      {
        dataField: 'DocumentNo',
        text: 'Plan No',
        sort: true,
        formatter: noFormatter
      },
      
      
      {
        dataField: 'ApprovalCode',
        text: 'ApprovalCode',
        sort: true
      }, 
      {
        dataField: 'SenderID',
        text: 'Sender',
        sort: true
      },
      {
        dataField: 'ApproverID',
        text: 'Approver',
        sort: true
      },
      {
        dataField: 'DateTimeSentforApproval',
        text: 'Date Sent',
        sort: true
      },
      /*{
        dataField: 'LastDateTimeModified',
        text: 'Last Modified',
        sort: true
      }, 
      {
        dataField: 'EmployeeNo',
        text: 'No',
        sort: true
      }, */
      {
        dataField: 'EmployeeName',
        text: 'From',
        sort: true
      }, 
      {
        dataField: 'Status',
        text: 'Status',
        sort: true,
        formatter: statusFormatter
      },
      {
        dataField: "action",
        isDummyField: true,
        text: "",
        align:'center',
        headerStyle: { textAlign: 'left' }, 
        formatter: actionFormatter
      }];

      const defaultSorted = [{
        dataField: 'DocumentNo',
        order: 'desc'
      }];

      function noFormatter(cellContent, row){
        return(
          <strong>{cellContent}</strong>
        )
      }

      function actionFormatter(cellContent, row){
        return(
          <>
          <div className="d-flex gap-3">
          
            	{(row.DocumentNo.substring(0, 2) == 'LP') && (row.Status == 'Open') && (
 
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-leave-plan/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            
            )}
            {(row.DocumentNo.substring(0, 2) == 'LR') && (row.Status == 'Open') && (
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-leave-request/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            )}
            {(row.DocumentNo.substring(0, 2) == 'IP') && (row.Status == 'Open') && (
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-individual-performance-agreement/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            )}
            {(row.DocumentNo.substring(0, 2) == 'PD') && (row.Status == 'Open') && (
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-personal-development-plan/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            )}
            {(row.DocumentNo.substring(0, 2) == 'RR') && (row.Status == 'Open') && (

                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-recruitment-requisition/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            )}
            {(row.DocumentNo.substring(0, 3) == 'PRN') && (row.Status == 'Open') && (
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-purchase-requisition/${row.DocumentNo}/${row.SystemId}`); } }
                >
                  Details
                </Button>
            )}
            {(row.DocumentNo.substring(0, 3) == 'PR_') && (row.Status == 'Open') && (
                <Button
                  type="button"
                  color="info"
                  className="btn-sm btn-rounded"
                  onClick={() => {history.push(`/approve-payment-requisition/${row.SystemId}/${row.DocumentNo}`); } }
                >
                  Details
                </Button>
            )}
            
          </div>
        </>
        )
      }
      function statusFormatter(cellContent, row) {
        if (row.Status == "Canceled") {
          return (
            <div>
                <Badge className="me-2 bg-danger">
                { cellContent }
                </Badge>
            </div>
          );
        }else if (row.Status == "Approved"){
          return (
            <div>
                <Badge className="me-2 bg-success">
                { cellContent }
                </Badge>
            </div>
          );
        }else if (row.Status == "Created"){
            return (
              <div>
                  <Badge className="me-2 bg-dark">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else{
          return (
            <div>
                <Badge className="me-2 bg-warning">
                { cellContent }
                </Badge>
            </div>
          );
        }
      }
      if(approvals.length < 1){
        return(
          <>
          <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
            <div className="page-content">
                    <MetaTags>
                    <title>No Approvals | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="No Approvals" />
                        <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>All Done</h5>
                                        <p className="text-muted">
                                                No Pending Approvals
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
          </>
        );

      }else{
        return (
            <>
             <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Approvals | HRMIS</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title="Human Resource Management" breadcrumbItem="Approvals" />

               
                    
        <Row>
            <Col lg={12}>
              <Card>
                <CardBody>
                
                  <CardSubtitle className="mb-3">
                    
                  </CardSubtitle>

                 
                  <ToolkitProvider
                      keyField="SystemId"
                      data={ approvals }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className="mb-2">
                              <Col lg={6} className='mb-2'>
                                  <div  className="rounded bg-light d-grid gap-2">
                                      <SearchBar {...toolkitProps.searchProps} className="form-control bg-transparent border-0 searchTm m-0" />
                                  </div>
                              </Col>
                              <Col sm="4">
                                <div className="text-sm-end">
                                 
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                <div className="table-responsive">
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ approvals } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table align-middle table-nowrap table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             </div>
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  

                   
                  
                </CardBody>
              </Card>
            </Col>
          </Row>
                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
                  }
    }

export default Approvals;