import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Container } from "reactstrap";
import {
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    UncontrolledTooltip,
    Badge,
    Label,
    Button,
    Form,
    Input,
    InputGroup,
  } from "reactstrap"
import { Link, useHistory } from "react-router-dom"
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';


import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from '../../helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import Select from 'react-select';
import FileSaver from 'file-saver';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'



    const LeaveRequestsPosted = () => {
      const history = useHistory();

      toastr.options = TOAST_OPTIONS;

      const [isLoading, setIsLoading] = useState(false)
      const [leaveRequests, setLeaveRequests] = useState([]);
      
      useEffect(() => {
        let obj = JSON.parse(localStorage.getItem("authUser"));
        let EmployeeNo = obj.uid;
        setIsLoading(true)
        axios.get(`${url.POSTED_LR}?$filter=Employee_No eq '${EmployeeNo}'`, HRConfig).then((r)=>{
          setLeaveRequests(r.data.value);
          setIsLoading(false)
        }).catch((e)=>{
          setIsLoading(false)
          console.log(e)
        })
      }, []);

      const handleFilter = (e) => {
          e.preventDefault();
          let obj = JSON.parse(localStorage.getItem("authUser"));
          let EmployeeNo = obj.uid;
          let url = url.POSTED_LR;
          const dateRange = document.getElementsByClassName('filterDate')[0]._flatpickr.selectedDates;
          const startDate = formatDate(dateRange[0]);
          const endDate = formatDate(dateRange[1]);
          const stat = document.getElementsByClassName('filterStatus')[0].value;
          
          let urlParams = '';
      
          if(stat != ''){
            urlParams = urlParams.concat( ` and Status eq '${stat}' `);
          }
 
          if(dateRange.length > 0){
            urlParams = urlParams.concat(` and ( From_Date ge ${startDate} and To_Date le ${endDate} )`);
          }
          url += `?$filter=Employee_No eq '${EmployeeNo}'`
          if(urlParams != ''){
            url += urlParams;
          }
          axios.get(url, HRConfig).then(function(response){    setLeaveRequests(response.data.value);    })
      }
      const handleStatusChanged = () => {
        const stat = document.getElementsByClassName('filterStatus')[0].value;
        stat != 'Approved' ? history.push("/leave-requests") : ""
      }
      const handleDownload = async (No) => {
        const data = {no:No}
        setIsLoading(true)
        axios.post(url.PRINTLEAVE, JSON.stringify(data), HRConfig).then((r)=>{
          const urlx = process.env.REACT_APP_APACHE_URL + r.data.value
            axios({
              url: urlx,
              method: 'GET',
              responseType: 'blob',
            })
              .then(response => {
                const url = window.URL.createObjectURL(new Blob([response.data]));
                const link = document.createElement('a');
                link.href = url;
                link.setAttribute('download', 'leave.pdf');
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
                setIsLoading(false)
              })
              .catch(error => {
                console.error('Error downloading file:', error);
                setIsLoading(false)
              });
          
          setIsLoading(false)
        }).catch((e)=>{
          console.log(e)
          setIsLoading(false)
        })
      }


      

      const { SearchBar } = Search;
      const columns = [
      {
        dataField: 'No',
        text: 'Leave No',
        sort: true,
        formatter: noFormatter
      },
      {
        dataField: 'Employee_No',
        text: 'Employee No',
        sort: true
      }, 
      {
        dataField: 'Employee_Name',
        text: 'Name',
        sort: true
      }, 
      {
        dataField: 'Employee_Title',
        text: 'Title',
        sort: true
      }, 
      {
        dataField: 'Posting_Date',
        text: 'Posting Date',
        sort: true
      },
      {
        dataField: 'No_of_Days',
        text: 'Days',
        sort: true
      }, 
      {
        dataField: 'Status',
        text: 'Status',
        sort: true,
        formatter: statusFormatter
      },{
        dataField: 'Statusx',
        text: '',
        sort: true,
        formatter: actionFormatter
      }
    ];

      const defaultSorted = [{
        dataField: 'No',
        order: 'desc'
      }];

      function noFormatter(cellContent, row){
        return(
          <strong>{cellContent}</strong>
        )
      }
      function actionFormatter(cellContent, row){
        return(
          <Button
            className="btn-sm btn-rounded"
            target="_blank"
            onClick={()=>{ handleDownload(row.No) }}
            >
            Report
        </Button>

        )
      }
      function statusFormatter(cellContent, row) {
        if (row.Status == "Pending Approval") {
          return (
            <div>
                <Badge className="me-2 bg-danger">
                { cellContent }
                </Badge>
            </div>
          );
        }else if (row.Status == "Approved"){
          return (
            <div>
                <Badge className="me-2 bg-success">
                { cellContent }
                </Badge>
            </div>
          );
        }else{
          return (
            <div>
                <Badge className="me-2 bg-warning">
                { cellContent }
                </Badge>
            </div>
          );
        }
      }

        return (
            <>
            <LoadingOverlay
            active={isLoading}
            spinner={<BounceLoader />}
            text='Please wait...'
            >
                <div className="page-content">
                    <MetaTags>
                    <title>Posted Leave Requests | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Posted Leave Requests" />
                        
                        
                        <Row>
                        <Col lg={12}>
                          <Card>
                            <CardBody>
                              <CardTitle className="h5 mb-4">Filter Leave Requests</CardTitle>
                              <Form className="row gy-2 gx-3 align-items-center" onSubmit={handleFilter}>
                                <Row>
                                <Col lg={3}>
                                <div className="col-sm-auto">
                                    <Flatpickr
                                      className="form-control d-block filterDate"
                                      placeholder="dd M,yyyy"
                                      options={{
                                        mode: "range",
                                        dateFormat: "Y-m-d"
                                      }}
                                    />
                                </div>
                                </Col>
                                
                                <Col lg={3}>
                                <div className="col-sm-auto">
                                  <label className="visually-hidden" htmlFor="autoSizingSelect">By Status</label>
                                  <select defaultValue="0" className="form-select filterStatus" onChange={handleStatusChanged}>
                                    <option value=""> Select Status </option>
                                    <option value="">All</option>
                                    <option value="Open">Open</option>
                                    <option value="Approved">Approved</option>
                                    <option value="Pending Approval">Pending Approval</option>
                                  </select>
                                </div>
                                </Col><Col lg={3}>
                                <div className="col-sm-auto">
                                  <button type="submit" className="btn btn-primary w-md">Filter</button>
                                </div>
                                </Col>
                                <Col lg={3}>
                                <div className="text-sm-end">
                                  <Link
                                    className="btn btn-success"
                                    to="/add-leave-request"
                                  >
                                    <i className="mdi mdi-plus me-1" />
                                    Create Leave Request
                                  </Link>
                                </div>
                                </Col>
                                </Row>
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>                
                    <Row>
          

            <Col lg={12}>
              <Card>
                <CardBody>
                   {leaveRequests && (leaveRequests.length > 0) && (
                    <ToolkitProvider
                      keyField="No"
                      data={ leaveRequests }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className="mb-2">
                              <Col sm="4">
                                <div className="search-box me-2 mb-2 d-inline-block">
                                  <div className="position-relative">
                                    <SearchBar {...toolkitProps.searchProps} />
                                    <i className="bx bx-search-alt search-icon" />
                                  </div>
                                </div>
                              </Col>
                              <Col sm="8">
                               
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ leaveRequests } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table-sm align-middle table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  )}
                  {leaveRequests && (leaveRequests.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Leave Request Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                  </div>

                  )}
                </CardBody>
              </Card>
            </Col>

            

          </Row>

                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default LeaveRequestsPosted;