import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import Select from 'react-select'

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import  * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const ApproveIPA = () => {
        const {no} = useParams();
        const history = useHistory();
        toastr.options = TOAST_OPTIONS;
        const [rows1, setrows1] = useState([{ id: 1 }]);
        const [th, setTh] = useState(['Customer','Financial','Internal Business Process','Learning and Growth'])
        const [periods , setPeriods] = useState(['January-June', 'July-December'])
        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [empDept, setEmpDept] = useState('');
        const [employee, setEmployee] = useState([]);
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')
        const [ipaStatus, setIpaStatus] = useState('')
        const [ipaDate, setIpaDate] = useState('')
        const [ipaPerformanceYear, setIpaPerformanceYear] = useState('')
        const [ipaConvert, setIpaConvert] = useState('')
        const [appraisalPeriod, setAppraisalPeriod] = useState('')
        const [appraiserNo, setAppraiserNo] = useState('')
        const [appraiserName, setAppraiserName] = useState('')
        const [appraiserTitle, setAppraiserTitle] = useState('')
        const [documentType, setDocumentType] = useState('')
        const [performanceType, setPerformanceType] = useState('')
        const [stage, setStage] = useState('')
        const [lines, setLines] = useState([])
        const [showApproveBtn, setShowApproveBtn] = useState(true);
        const [showRejectBtn, setShowRejectBtn] = useState(false);
        const [comment, setComment] = useState('')
        

        const fetchData = () => {
            setIsloading(true)
            axios.get(`${url.IPAS_X}?$filter=No eq '${no}'&$expand=ipaLines`, HRConfig)
                .then(function(response){                  
                    axios.get(`${url.EMPLOYEES}?$filter=No eq '${response.data.value[0].EmployeeNo}'`, HRConfig).then((r)=>{ 
                        setEmpName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setEmpTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    axios.get(`${url.EMPLOYEES}?$filter=No eq '${response.data.value[0].Appraiser}'`, HRConfig).then((r)=>{ 
                        setAppraiserName(r.data.value[0].FirstName +" "+r.data.value[0].LastName)
                        setAppraiserTitle(r.data.value[0].JobTitle)
                    }).catch((e)=>{ console.log(e) })
                    
                    setEmpNo(response.data.value[0].EmployeeNo)   
                    setAppraiserNo(response.data.value[0].Appraiser)
                   
                    setIpaDate(response.data.value[0].PostingDate)
                    setIpaPerformanceYear(response.data.value[0].PerformanceYear)
                    setIpaStatus(response.data.value[0].Status)
                    setIpaConvert(response.data.value[0].ConvertToAppraisal)
                    setAppraisalPeriod(response.data.value[0].AppraisalPeriod)
                    setIpaNo(response.data.value[0].No)
                    setDocumentType(response.data.value[0].DocumentType)
                    setPerformanceType(response.data.value[0].PerformanceType)
                    setStage(response.data.value[0].Stage)
                    setLines(response.data.value[0].ipaLines)
                    setIsloading(false)
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setUserError(true);
                    setIsloading(false)
            })
        }
        useEffect(() => {
           fetchData();
        },[]);

        const handleRejectIpa = () =>{
            setIsloading(true)
            const lNo = {"no": ipaNo, "rejectioncomment":comment}
            axios.post(url.REJECT_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){ 
                    if(response.status == '200'){
                        toastr.success(response.data.value);
                        history.push(`/rejected/${ipaNo}`) 
                    }else{
                        toastr.success("Could not Approve leave plan" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    console.log(error)
                    setIsloading(false)
            })  
        }

        const handleApproveIpa = () =>{
            setIsloading(true)
            const lNo = {'no': ipaNo}
            axios.post(url.APPROVE_LR, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("IPA Approved" );
                        history.push(`/approved/${ipaNo}`) 
                    }else{
                        toastr.success("Could not Approve IPA" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                })
        }

        const handleActionChange = () =>{
            const selectedAction = document.getElementsByClassName('lAction')[0].value;
            if(selectedAction == 'APPROVE'){
                setShowApproveBtn(true)
                setShowRejectBtn(false)
            }else if(selectedAction == 'REJECT'){
                setShowApproveBtn(false)
                setShowRejectBtn(true)
            }
        }

        return (
            <LoadingOverlay
            active={isloading}
            spinner={<BounceLoader />}
            text='Please wait...'
            >
            <div className="page-content">
            <MetaTags>
            <title>Approve IPA | {url.APPNAME}</title>
            </MetaTags>
            <Container fluid={true}>
                <Breadcrumbs title={url.APPNAME} breadcrumbItem="Approve Individual Performance Agreement" />
                <div>
                <Row>                
                    <Alert color='danger' role="alert">
                        <strong>Pending Approval</strong>
                    </Alert>      
                <Col xl={4}>
                <Card>
                    <CardBody>
                    <CardTitle>Employee Details</CardTitle>
                            <Row>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {empNo}   />
                                <label htmlFor="floatingIdInput">Appraisee Employee No</label>
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control" disabled={true} defaultValue={empName}   />
                                <label htmlFor="floatingnameInput">Appraisee Name</label>
                                </div>
                            </Col>
                            </Row>
                            <Row>
                      
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control" disabled={true} defaultValue={empTitle}  />
                                <label htmlFor="floatingDepartInput">Appraisee Title</label>
                                </div>
                            </Col>
                            <hr/>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input type="text" className="form-control EmployeeNo" disabled={true}  value = {appraiserNo}   />
                                <label htmlFor="floatingIdInput">Appraiser Employee No</label>
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserName}   />
                                <label htmlFor="floatingnameInput">Appraiser Name</label>
                                </div>
                            </Col>
                            </Row>
                            <Row>
                      
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control" disabled={true} defaultValue={appraiserTitle}  />
                                <label htmlFor="floatingDepartInput">Appraiser Title</label>
                                </div>
                            </Col>
                           
                        </Row>
                        </CardBody>
                    </Card>

                    </Col>
                    <Col  xl={8}>
                    <Card>
                        <CardBody>
                        <CardTitle className="mb-3">IPA Details</CardTitle>
                        <Row>
                        <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control dNo" defaultValue={ipaNo} disabled={true}  />
                                <label htmlFor="floatingDepartInput">No</label>
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control c2A" defaultValue={documentType} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Document Type</label>
                                </div>
                            </Col>
                    
                            <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control sT" defaultValue={ipaStatus} disabled={true}  />
                                <label htmlFor="floatingDepartInput">Status</label>
                                </div>
                            </Col>
                            <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="date" className="form-control c2A" defaultValue={ipaDate} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Posting Date</label>
                                </div>
                            </Col>
                        
                            <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control pY" defaultValue={ipaPerformanceYear} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Performance Year</label>
                                </div>
                            </Col>
                           
                            <Col md={6}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control c2A" defaultValue={ipaConvert} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Convert To Appraisal</label>
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control c2A" defaultValue={performanceType} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Performance Type</label>
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <select className="form-select aPd" disabled={true} >
                                            <option value={appraisalPeriod} >{appraisalPeriod}</option>
                                            {periods.map((a,k)=>(
                                                <option value={a} key={k}>{a}</option>
                                            ))}
                                        </select>
                                <label htmlFor="floatingDepartInput">Appraisal Period</label>
                                </div>
                            </Col>
                            <Col md={12}>
                                <div className="form-floating mb-3">
                                <input  type="text" className="form-control c2A" defaultValue={stage} disabled={true}   />
                                <label htmlFor="floatingDepartInput">Stage</label>
                                </div>
                            </Col>
                            </Row>
                        
                        </CardBody>
                    </Card>

                    </Col>
                    <Col>
                   
                        
                        <div>
                            {(lines || []).map((formRow, key) => (

                                    
                                    
                                <Card key={key}>
                                <CardBody>
                                <div>
                                    <Row className="mb-3 align-items-center">
                                    <Col sm={(ipaStatus == 'Open') ? 9:12}>
                                        <div className="mt-2">
                                        <h5>IPA Line {key+1}</h5>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Perspective and Strategic Thematic Areas:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Perspective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Strategic Objective/KRAs:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.strategicObjective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Individual Objectives:</h4></Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.individualObjective}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Initiatives/Priority Strategies:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Initiatives}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Measures/Deliverables:</h4> </Col>
                                            <Col sm={12}><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Measures}</Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Target/Milestone Date</h4> </Col>
                                            <Col sm={12}><h4><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.TargetDate} </h4></Col>
                                         </Row>
                                         <Row  className="m-1">
                                            <Col sm={12}><h4>Weight</h4> </Col>
                                            <Col sm={12}><h4><i className="mdi mdi-chevron-right text-primary me-1" />{" "}{formRow.Weights}</h4></Col>
                                         </Row>
                                        
                                         
                                        </div>
                                    </Col>
                                    {(ipaStatus == 'Open') && (
                                    <Col sm={3}>
                                        <div className="float-sm-end d-flex align-items-center">
                
                                            
                                                <Button
                                                    type="button"
                                                    color="info"
                                                    className="btn-sm btn-rounded"
                                                    onClick={()=>handleClickLineDetails(key)}
                                                    >
                                                    Edit/Delete IPA Line
                                                </Button>
                                            
                                   
                                        </div>
                                    </Col>
                                    )}
                                    </Row>
                                </div>
                        
                
                                    </CardBody>
                                    </Card>
                                    
                                ))}
                                {(ipaStatus == 'Pending Approval') && (
                                    <>
                                        <Row>
                                        <Col md={12}>
                                                    <div className="form-floating mb-3">
                                                        <select  className="form-select lAction" onChange={handleActionChange}>
                                                            
                                                            <option value="APPROVE">APPROVE IPA</option>
                                                            <option value="REJECT">REJECT IPA</option>
                                    
                                                        </select>
                                                        <label htmlFor="floatingSelectGrid"> Select Action</label>
                                                    </div>
                                                </Col>
                                        </Row>
                                        {showRejectBtn && (
                                                <Row>
                                                <Col md={12}>
                                                    <div className="form-floating mb-3">
                                                        <input  type="text" className="form-control" onChange={(e)=>setComment(e.target.value)}  />
                                                        <label htmlFor="floatingSelectGrid"> Rejection Comment</label>
                                                    </div>
                                                </Col>
                                                </Row>
                                            )}
                                        <Row>
                                                {showRejectBtn && (
                                                <Col md={12} className="reject">
                                                    <div className="d-grid gap-2">
                                                        <Button color="danger" type="button" className="btn btnRejectRequest" onClick={handleRejectIpa}>
                                                            Reject
                                                        </Button>
                                                    </div>
                                                </Col>
                                                )}
                                                {showApproveBtn && (
                                                <Col md={12} className="approve">
                                                    <div className="d-grid gap-2">
                                                        <Button color="success" type="button" className="btn btnApproveRequest" onClick={handleApproveIpa}>
                                                            Approve 
                                                        </Button>
                                                    </div>
                                                </Col>
                                                )}
                                            </Row>
                                    </>)}

                          
                                    </div>
                                    <br/>
      
                                   
            
                </Col>
                </Row>

                </div>


                
                   

            </Container>
        </div>
        </LoadingOverlay>
        );
    }


export default ApproveIPA;