import React, {useState} from "react";
import MetaTags from 'react-meta-tags';
import { Link } from "react-router-dom";
import { Col, Container, Form, Row, Input, Label, FormFeedback } from "reactstrap";

import axios from "axios";
import * as url from "../../helpers/url_helper"
import toastr from "toastr";
import "toastr/build/toastr.min.css";
import { TOAST_OPTIONS, HRConfig2 } from "helpers/backend_helper";
import { useHistory } from "react-router-dom";

// import images
import logodark from "../../assets/images/logo-dark.png";
import logolight from "../../assets/images/logo-light.png";
import CarouselPage from "./CarouselPage";

const PublicApplicantsForgotPassword = () => {
    const history = useHistory();
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const handleSubmit = () =>{
        //console.log(`${email} - ${password}`)
        const data = {
            "email": email,
            "password": password
        }
        axios.post(url.APPLICANT_LOGIN, JSON.stringify(data), HRConfig2).then((r)=>{
            console.log(r.data)
            if(r.data.success == "1"){
                history.push("/applications")
            }
        }).catch((e)=>{
            console.log(e)
        })
    }

  return (
    <React.Fragment>
      <div>
        <MetaTags>
          <title>Login | UEGCL</title>
        </MetaTags>
        <Container fluid className="p-0">
          <Row className="g-0">
            <CarouselPage />

            <Col xl={3}>
              <div className="auth-full-page-content p-md-5 p-4" style={{ backgroundColor: 'white' }}>
                <div className="w-100">
                  <div className="d-flex flex-column h-100">
                    <div className="mb-4 mb-md-5">
                      <Link to="#" className="d-block auth-logo">
                        <img
                          src={logodark}
                          alt=""
                          height="78"
                          className="auth-logo-dark"
                        />
                        <img
                          src={logolight}
                          alt=""
                          height="78"
                          className="auth-logo-light"
                        />
                      </Link>
                    </div>
                    <div className="my-auto">
                      <div>
                        <p className="text-muted">
                          Enter your email to reset your password...
                        </p>
                      </div>
                     

                      <div className="mt-4">
                        
                          <div className="mb-3">
                            <Label className="form-label">Email</Label>
                            <Input
                              className="form-control"
                              type="email"
                              onChange={(e)=>setEmail(e.target.value)}
                            />
                 
                          </div>
                          <div className="mt-4">
                            <button
                              className="btn btn-primary btn-block "
                              type="submit"
                              onClick={handleSubmit}
                            >
                              Submit
                            </button>
                          </div>
                          

           
                      </div>
                    </div>

                    <div className="mt-4 mt-md-5 text-center">
                      <p className="mb-0">
                         {new Date().getFullYear()} UEGCL
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default PublicApplicantsForgotPassword;
