import React, { useEffect, useState } from "react"
import { Container, Row, Col } from "reactstrap"
import MetaTags from "react-meta-tags"

//Import Components
import CardUser from "./card-user"
import CardWelcome from "./card-welcome"
import MiniWidget from "./mini-widget"

import SalesAnalytics from "./sales-analytics"
//import TotalSellingProduct from "./total-selling-product"
import Tasks from "./tasks"
//import ChatBox from "./chat-box"
import Notifications from './notifications';
import Oerview from './overview';
import User from "./user"

import axios from "axios";
import { APPROVALS, COUNT_LEAVE_PLANS, COUNT_LEAVE_REQUESTS, COUNT_REQUESTS_TO_APPROVE, COUNT_RECRUITMENT_REQUESTS, COUNT_TRAINING_REQUESTS, COUNT_IPAS,COUNT_IMPROVEMENT_PLANS,COUNT_EXIT_INTERVIEWS,COUNT_STAFF_HAND_OVER, NOTIF_CONTRACT, NOTIF_BIRTHDAYS, PAS_X, APPNAME  } from "helpers/url_helper";
import { HRConfig, formatDate } from "helpers/backend_helper";
import { EMPLOYEES } from './../../helpers/url_helper';

const Dashboard = props => {
  const [approvals, setApprovals] = useState([]);
  const [loading, setLoading] = useState(true);

  const [username, setusername] = useState("")
  const [employeeNo, setEmployeeNo] = useState("")
  const [role, setrole] = useState("")

  const [pendingApprovals, setPendingApprovals] = useState("-")
  const [yourLPlans, setYourLPlans] = useState("-")
  const [yourLRequests, setYourLRequests] = useState("-")

  const [RRs, setRRs] = useState("-")
  const [TRs, setTRs] = useState("-")
  const [IPAs, setIPAs] = useState("-")
  const [IPs, setIPs] = useState("-")
  const [exits, setExits] = useState("-")
  const [handOvers, setHandOvers] = useState("-")

  const [contracts, setContracts] = useState([])
  const [birthdays, setBirthdays] = useState([])

  const [yourLeaves, setYourLeaves] = useState([])
  const [appraiserRatings, setAppraiserratings] = useState(0)

  

  const fetchData = () =>{
    if (localStorage.getItem("authUser")) {
      const obj = JSON.parse(localStorage.getItem("authUser"));
      const navUserName =  obj.nav_username 
      axios.get(APPROVALS + "?$filter=ApproverID eq '"+ navUserName +"' and Status eq 'Open'", HRConfig).then((response)=>{ setApprovals(response.data.value); })
        .catch((error)=>{  console.log(error);   });
      setusername(obj.username)
      setEmployeeNo(obj.uid)
      setrole(obj.role)
      const currentUserName = {'navusername':obj.nav_username} 
      const currentUserNo   = {'employeeno': obj.uid} 
      axios.get(`${PAS_X}?$filter=Appraiser eq '${employeeNo}' and Stage eq 'Appraiser Rating'&$expand=pALines,pALines2`, HRConfig).then((r)=>{ setAppraiserratings(r.data.value.length); console.log(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_REQUESTS_TO_APPROVE, JSON.stringify(currentUserName), HRConfig).then((r)=>{ setPendingApprovals(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_LEAVE_REQUESTS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setYourLRequests(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_LEAVE_PLANS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setYourLPlans(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_RECRUITMENT_REQUESTS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setRRs(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_TRAINING_REQUESTS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setTRs(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_IPAS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setIPAs(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_IMPROVEMENT_PLANS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setIPs(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_EXIT_INTERVIEWS, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setExits(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.post(COUNT_STAFF_HAND_OVER, JSON.stringify(currentUserNo), HRConfig).then((r)=>{ setHandOvers(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.get(NOTIF_CONTRACT+"?$filter=EmployeeNo eq '"+obj.uid+"'",  HRConfig).then((r)=>{ setContracts(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.get(NOTIF_BIRTHDAYS,  HRConfig).then((r)=>{ setBirthdays(r.data.value); }).catch((e)=>{ console.log(e); });
      axios.get(`${PAS_X}?$filter=Appraiser eq '${obj.uid}' and Stage eq 'Appraiser Rating'&$expand=pALines,pALines2`, HRConfig).then((r)=>{ setAppraiserratings(r.data.value.length);  }).catch((e)=>{ console.log(e); });
      axios.get(EMPLOYEES+"?$filter=No eq '"+obj.uid+"'",  HRConfig)
        .then((r)=>{ 
          //console.log(r.data.value)
            const leave = [
              {
                'leaveType': 'Annual', 
                'entitled':r.data.value[0].AnnualLeaveDaysEntitlement,
                'balance': r.data.value[0].AnnualLeaveBalance
              },
              {
                'leaveType': 'Compassionate', 
                'entitled':r.data.value[0].CompationateLeaveEntitlement,
                'balance': r.data.value[0].CompassionateLeaveBalance
              },
              {
                'leaveType': 'Maternity', 
                'entitled':r.data.value[0].MaternityLeaveEntitilement,
                'balance': r.data.value[0].maternityLeaveBalance
              },
              {
                'leaveType': 'Paternity', 
                'entitled':r.data.value[0].PaternityLeaveEntitlement,
                'balance': r.data.value[0].paternityLeaveBalance
              },
              {
                'leaveType': 'Sick', 
                'entitled':r.data.value[0].SickLeaveEntitilement,
                'balance': r.data.value[0].SickLeaveBalance
              },
              {
                'leaveType': 'Study', 
                'entitled':r.data.value[0].StudyLeaveEntitlement,
                'balance': r.data.value[0].StudyLeaveBalance
              }
            ];
            setYourLeaves(leave)
          }).catch((e)=>{ console.log(e); });
      //console.log(NOTIF_CONTRACT+"?$filter=EmployeeNo eq '"+obj.uid+"'")
  }
  }

  useEffect(() => {
    fetchData();
  }, []);

  const reports = [
    {
      icon: "bx bx-copy-alt",
      title: "Recruitment",
      value: RRs,
      color: "primary",
      url:"/recruitment"
    },
    {
      icon: "bx bx-archive-in",
      title: "Trainings",
      value: TRs,
      color: "success",
      url:"/training"
    },
    {
      icon: "bx bx-purchase-tag-alt",
      title: "IPAs",
      value: IPAs,
      color: "dark",
      url:"/individual-performance-agreement"
    },
    {
      icon: "bx bxs-message-square-dots",
      title: "MIPs",
      color: "success",
      value: IPs,
      url:"/monthly-improvement-plan"
    }
    ,
    {
      icon: "bx bxs-note",
      title: "Exits",
      value: exits,
      color: "danger",
      url:"/exit"
    },
    ,
    {
      icon: "bx bxs-book-bookmark",
      title: "Handovers",
      value: handOvers,
      color: "warning",
      url:"/staff-handover"
    }
];

  
  return (
    <React.Fragment>
      <div className="page-content">
        <MetaTags>
          <title>
            Home | {APPNAME}
          </title>
        </MetaTags>
        <Container fluid>
  
           <CardUser username={username} role={role} pendingApprovals={pendingApprovals} yourLPlans={appraiserRatings} yourLRequests={yourLRequests} /> 
           <Row>
            
          </Row>

          <Row>
            <CardWelcome /> 
            

            <Col xl="8">
              <Row>
                <User reports={reports}/>
              </Row>
            </Col>
          </Row>


          
          <Row>
            <SalesAnalytics contracts ={contracts} />
            <Notifications approvals={approvals} birthdays={birthdays}/>
            <Tasks leave={yourLeaves} showReports={true} />
          </Row>
        
     
        </Container>
      </div>
    </React.Fragment>
  )
}

export default Dashboard
