import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardText, Form, Container, Button, Alert, Modal } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import Select from 'react-select';

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'

    const AddPurchaseRequisition = () => {
        const history = useHistory();

        const [inputs, setInputs] = useState([]);
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [budgets, setBudgets] = useState([])
        const [currencies, setCurrencies] = useState([])
        const [locations, setLocations] = useState([])
        
        const [gLAccounts, setGLAccounts] = useState([])
        const [items, setItems] = useState([])
        const [fixedAssets, setFixedAssets] = useState([])
        const [unitOfMeasure, setUnitOfMeasure] = useState([])
        const [vendors, setVendors] = useState([])

        const [form, setForm] = useState("")
        const [subject, setSubject] = useState("")
        const [postingDate, setPostingDate] = useState("")
        const [suppliesWorksServices, setSuppliesWorksServices] = useState("")
        const [budgetName, setBudgetName] = useState("")
        const [procurementPlanReference, setProcurementPlanReference] = useState("")
        const [currencyCode, setCurrencyCode] = useState("")
        const [locationCode1, setLocationCode1] = useState("")
       
        const [selectedType, setSelectedType] = useState('')
        const [selectedNo, setSelectedNo]= useState('')
        const [description, setDescription] = useState('')
        const [description2, setDescription2] = useState('')
        const [selectedVendor, setSelectedVendor] = useState('')
        const [locationCode, setLocationCode] = useState('')
        const [quantity, setQuantity] = useState(0)
        const [selectedUnitOfMeasure, setSelectedUnitOfMeasure] = useState('')
        const [directUnitCost, setDirectUnitCost] = useState(0)
        const [quantityToConvert, setQuantityToConvert] = useState(0)
        const [marketPrice, setMarketPrice] = useState(0)

        const [purchaseRequisitionLines, setPurchaseRequisitionLines] = useState([])

        const [noo, setNoo] = useState([])

        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [ipaSaved, setIpaSaved] = useState(false)
        const [ipaNo, setIpaNo] = useState('')

        const [modal_item, setmodal_item] = useState(false);
        const removeBodyCss = () => {
            document.body.classList.add("no_padding");
          }
          const tog_item = () => {
            setmodal_item(!modal_item);
            removeBodyCss();
          }

        toastr.options = TOAST_OPTIONS;

        const handleAddItem = () =>{
            
            const itemLine =  {
                "Type": selectedType,
                "No": selectedNo,
                "Description": description,
                "Description2": description2,
                "BuyfromVendorNo": selectedVendor,
                "LocationCode": locationCode,
                "Quantity": parseInt(quantity),
                "DirectUnitCost": parseInt(directUnitCost),
                "UnitofMeasureCode": selectedUnitOfMeasure,
                "QuantitytoConvert": parseInt(quantityToConvert),
                "MarketPrice": parseFloat(marketPrice)
            }
            setPurchaseRequisitionLines(state =>[...state, itemLine])
            tog_item()
        }
        const handleRemoveItem = (key) =>{
            const newItems = [...purchaseRequisitionLines];
            newItems.splice(key, 1);
            setPurchaseRequisitionLines(newItems);
        }

        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleTypeChange = (e) =>{
            setSelectedType(e.target.value)
            setIsloading(true)
            if(e.target.value == 'Item'){
                setNoo(items)
                if(items.length > 0){ setIsloading(false) }
            }else if(e.target.value == 'G/L Account'){
                setNoo(gLAccounts)
                if(gLAccounts.length > 0){ setIsloading(false) }
            }else if(e.target.value == 'Fixed Assets'){
                setNoo(fixedAssets)
                if(fixedAssets.length > 0) { setIsloading(false) }
            }else{
                setNoo([])
                setIsloading(false)
            }
        }
        const handleNoChange = (e)=>{
            setSelectedNo(e.value); 
            if(selectedType == 'Item'){
                axios.get(`${url.UNITOFMEASURE}?$filter=ItemNo eq '${e.value}'`, HRConfig).then((r)=>{setSelectedUnitOfMeasure(r.data.value[0].Code)}).catch((e)=>{ console.log(e)}) 
            }
        }
        const handleSubmit = (event) => {
            setIsloading(true); 
            const data = {
                
                "FormType": form,
                "RequestByNo": empNo,
                "PostingDate": postingDate,
                "SuppliesWorksServices": suppliesWorksServices,
                "BudgetName": budgetName,
                "ProcurementPlanReference": procurementPlanReference,
                "SubjectOfProcurement": subject,
                "CurrencyCode": currencyCode,
                "LocationCode": locationCode1,
                "RequestedReceiptDate": "0001-01-01",
                "purchaseRequisitionLines": purchaseRequisitionLines
            };
           
            axios.post(url.PURCHASES, data, HRConfig)
                .then(function(response){  
                    if(response.data.No){
                        toastr.success("Purchase Requisition No "+ response.data.No+" created" ); 
                        setIpaSaved(true);
                        setIpaNo(response.data.No)
                        setIsloading(false);
                    }else{
                        toastr.success("Could not Create Purchase Requisition" )
                        setIsloading(false);
                    }
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false);
            })
               
         
            
            
        }

        useEffect(() => {
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                const EmployeeNo = obj.uid;
                axios.get(url.EMPLOYEES + "?$filter=No eq '"+ EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            setUserError(false);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                        console.log(error);
                })
                    
            }
            axios.get(url.BUDGETS, HRConfig).then((r)=>{ setBudgets(r.data.value)}).catch((e)=>{ console.log(e)})
            axios.get(url.CURRENCIES, HRConfig).then((r)=>{ setCurrencies(r.data.value)}).catch((e)=>{ console.log(e)})
            axios.get(url.LOCATIONS, HRConfig).then((r)=>{ setLocations(r.data.value)}).catch((e)=>{ console.log(e)})
            axios.get(url.GLACCOUNTS, HRConfig).then((r)=>{ 
                let options = []
                r.data.value.map((a)=>{options.push({value:a.No, label:`${a.Name} ${a.AccountType}`})})
                setGLAccounts(options)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.ITEMS, HRConfig).then((r)=>{ 
                let options = []
                r.data.value.map((a)=>{options.push({value:a.No, label:`${a.Description}`})}) 
                setItems(options)
            }).catch((e)=>{ console.log(e)})
            axios.get(url.FIXEDASSETS, HRConfig).then((r)=>{ 
                let options = []
                r.data.value.map((a)=>{options.push({value:a.No, label:`${a.Description}`})})  
                setFixedAssets(options)
            }).catch((e)=>{ console.log(e)})
            //axios.get(url.UNITOFMEASURE, HRConfig).then((r)=>{ setUnitOfMeasure(r.data.value);}).catch((e)=>{ console.log(e)})
            axios.get(url.VENDORS, HRConfig).then((r)=>{ 
                let options = []
                r.data.value.map((a)=>{options.push({value:a.No, label:`${a.Name}`})}) 
                setVendors(options)
            }).catch((e)=>{ console.log(e)})
        },[]);
        //console.log(noo)

        
        return (
      
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Purchase Requisition | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Purchase Requisition" />
                        <div>
                        {userError && ( 
                        <Alert color="danger" role="alert">
                                    Your Employee Number Not Found in Dynamics 365 Business Central
                        </Alert>
                        )}

                        {(ipaSaved == false) ? (
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Requisitioner Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>No</label>
                                        <input type="text" className="form-control" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Name</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Title</label>
                                        <input  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                            <CardBody>
                            <CardTitle>Requisition Details</CardTitle>
                                <Row>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Subject of Procurement</label>
                                        <input  type="text" className="form-control" onChange={(e)=>setSubject(e.target.value)} value={subject}   />
                                    </div>
                                </Col>
                                <Col md={6}>
                                    <div className="mb-3">
                                        <label>Form</label>
                                        <select className="form-select" onChange={(e)=>setForm(e.target.value)} defaultValue={form}>
                                                <option value="">Select</option>
                                                <option value="Form 5">Form 5</option>
                                                <option value="Form 18">Form 18</option>
                                        </select>
                                        </div>
                                    </Col>
                                <Col md={6}>
                                        <div className="mb-3">
                                        <label>Date</label>
                                        <Flatpickr
                                            className="form-control"
                                            onChange={(e)=>setPostingDate(formatDate(e))}
                                            options={{
                                                altInput: true,
                                                altFormat: "F j, Y",
                                                dateFormat: "Y-m-d",
                                                disabled: true

                                            }}
                                            />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label>Type</label>
                                        <select className="form-select" onChange={(e)=>setSuppliesWorksServices(e.target.value)}>
                                            <option value="">Select</option>
                                            <option value="Supplies">Supplies</option>
                                            <option value="Works">Works</option>
                                            <option value="Non-consultancy Services">Non-consultancy Services</option>
                                        </select>
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                            <label>Budget Name</label>
                                            <select className="form-select" onChange={(e)=>setBudgetName(e.target.value)}>
                                                <option value="">Select</option> 
                                                { budgets.map((b,k)=>( <option key={k} value={b.Name}>{b.Description}</option> ))}
                                            </select>
                                        </div>
                                    </Col>
                            
                                    <Col md={6}>
                                        <div className="mb-3">
                                            <label>Procurement Plan Reference</label>
                                            <input  type="text" className="form-control" onChange={(e)=>setProcurementPlanReference(e.target.value)} />
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                            <label>Currency</label>
                                            <select className="form-select" onChange={(e)=>setCurrencyCode(e.target.value)}> 
                                            <option value="">UGX</option>
                                            { currencies.map((c,k)=>( <option key={k} value={c.Code}>{c.Code} {` `} {c.Description}</option> ))}</select>
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                    <div className="mb-3">
                                        <label>Location</label>
                                        <select className="form-select" onChange={(e)=>setLocationCode1(e.target.value)}> 
                                        <option value="">Select</option>
                                        { locations.map((l,k)=>( <option key={k} value={l.Code}>{l.Name}</option> ))}</select>
                                        </div>
                                    </Col>
                                    
                                    </Row>
                                    </CardBody>
                                    </Card>
                                    {purchaseRequisitionLines.map((i, k)=>(
                                        <Row key={k}>
                                            <Col>
                                            <Card>
                                        <CardBody>
                                
                                        <CardTitle className="mb-3">
                                        Item  {k+1} 
                                        </CardTitle>
                                            <Row >
                        
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label>Type</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Type} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label>No</label>
                                                    <input  type="text" className="form-control" defaultValue={i.No} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Description</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Description} disabled={true} /> 
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label>Description 2</label>
                                                    <input  type="text" className="form-control" defaultValue={i.Description2} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                <label>Vendor</label>
                                                    <input  type="text" className="form-control" defaultValue={i.BuyfromVendorNo} disabled={true} />
                                                    
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Location Code</label>
                                                    <input  type="text" className="form-control" defaultValue={i.LocationCode} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                <label>Unit Cost</label>
                                                    <input  type="text" className="form-control" defaultValue={i.DirectUnitCost} disabled={true} />
                                                    
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label>Unit Of Measure</label>
                                                    <input  type="text" className="form-control" defaultValue={i.UnitofMeasureCode} disabled={true} />
                                                </div>
                                            </Col>
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label htmlFor="">Quantity</label>
                                                    <input  type="text" className="form-control" defaultValue={i.QuantitytoConvert} disabled={true} />
                                                </div>
                                            </Col>
                                           
                                            <Col lg={3}>
                                                <div className="mb-3">
                                                    <label>Market Price</label>
                                                    <input  type="text" className="form-control" defaultValue={i.MarketPrice} disabled={true} /> 
                                                </div>
                                            </Col>
                                            <Row>
                                            <Col lg={3} className="align-self-center">
                                                <div className="mb-3">
                                                
                                                <input
                                                    type="button"
                                                    className="btn btn-primary"
                                                    value="Remove"
                                                    onClick={() => handleRemoveItem(k)}
                                                />
                                                </div>
                                            </Col>
                                            </Row>
                                            
                                            </Row>
                                            </CardBody>
                                            </Card>
                                            </Col>
                                        </Row>
                                    ))}

                                    <Row>
                                            <Col md={6}>
                                                <div className="d-grid gap-2">
                                                    <input
                                                        type="button"
                                                        className="btn btn-success mt-3 mt-lg-0"
                                                        value="Add Requisition Item"
                                                        onClick={() => {  tog_item() }}
                                                    />
                                                </div>
                                            </Col>
                                            {(purchaseRequisitionLines.length > 0) && (
                                            <Col md={6}>
                                                <div className="d-grid gap-2">
                                                    <Button color="primary" type="button" className="btn" onClick={handleSubmit}>
                                                        Save Requisition
                                                    </Button>
                                                </div>
                                            </Col>
                                            )}
                                    </Row>

                                
                            </Col>
                            <Col>
                           
                                
                                    
                            <Modal
                                  isOpen={modal_item}
                                  toggle={() => {
                                    tog_item()
                                  }}
                                  centered
                                >
                                  <div className="modal-header">
                                    <h5 className="modal-title mt-0" id="myModalLabel">Add Requisition Item</h5>
                                    <button
                                      type="button"
                                      onClick={() => {
                                        setmodal_item(false);
                                      }}
                                      className="close"
                                      data-dismiss="modal"
                                      aria-label="Close"
                                    >
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                            
                                    
                                
                                     
                                            <Row >
                        
                                            <Col lg={12}>
                                                {isloading && ( <Alert color="danger" role="alert">Please wait...   </Alert>)}
                                                <div className="mb-3">
                                                    <label>Account Type</label>
                                                    <select className="form-select" value={selectedType} onChange={(e) =>{handleTypeChange(e)} }>
                                                    <option value=" ">Select</option>
                                                    <option value="G/L Account">G/L Account</option>
                                                    <option value="Item">Item</option>
                                                    <option value="Fixed Asset">Fixed Asset</option></select>
                                                    
                                                </div>
                                            </Col>
                                     
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Account No</label>
                                                    <Select options={noo} onChange={handleNoChange} />
                                                </div>
                                            </Col>
                                            
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Description</label>
                                                    <input  type="text" className="form-control" onChange={(e)=>setDescription(e.target.value)}   />
                                                </div>
                                            </Col>
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Description 2</label>
                                                    <input  type="text" className="form-control" onChange={(e)=>setDescription2(e.target.value)}   />
                                                </div>
                                            </Col>
                                            <Col lg={12} className="mb-3">
                                                <div className="mb-3">
                                                    <label>Vendor</label>
                                                    <Select options={vendors} onChange={(e)=>{ setSelectedVendor(e.value) }} />
                                                </div>
                                            </Col>
                                            <Col lg={12}>
                                                <div className="mb-3">
                                                    <label>Location Code</label>
                                                    <select className="form-select" onChange={(e)=>setLocationCode(e.target.value)}> <option value=''>select</option>{ locations.map((l,k)=>( <option key={k} value={l.Code}>{l.Name}</option> ))}</select>
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Unit Cost</label>
                                                    <input  type="number" className="form-control"  onChange={(e)=>{ setQuantity(e.target.value)}} />  
                                                </div>
                                            </Col>
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Direct Unit Cost</label>
                                                    <input  type="number" className="form-control" onChange={(e)=>{ setDirectUnitCost(e.target.value)}}  />
                                                </div>
                                            </Col>
                                           
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label>Quantity</label>
                                                    <input  type="number" className="form-control" onChange={(e)=>setQuantityToConvert(e.target.value)}  />
                                                </div>
                                            </Col>
                                          
                                            <Col lg={6}>
                                                <div className="mb-3">
                                                    <label >Market Price</label>
                                                    <input  type="number" className="form-control" onChange={(e)=>setMarketPrice(e.target.value)}   />
                                                </div>
                                            </Col>
                                            
                                         
                                            </Row>
                                          
                                            
                                            </div>
                                  <div className="modal-footer">
                                    <button
                                      type="button"
                                      onClick={() => {  tog_item() }}
                                      className="btn btn-danger "
                                      data-dismiss="modal"
                                    >
                                      Cancel
                                    </button>
                                    <button
                                      type="button"
                                      className="btn btn-success "
                                      onClick={()=>{handleAddItem()}}
                               
                                    >
                                      Add Item...
                                    </button>
                                  </div>
                                </Modal>
              
                                            
                                            <br/>
              
                                           
                    
                        </Col>
                        </Row>
                        ):(
                            <Row className="d-flex justify-content-center">
                                <Col lg={8} >
                                <Card outline color="success" className="border">
                                    <CardBody>
                                    <CardTitle className="mb-4 text-white">
                                        
                                    </CardTitle>
                                    <CardText>
                                        
                                    </CardText>
                                    <div className="text-center">
                                        <div className="mb-4">
                                        <i className="mdi mdi-check-circle-outline text-success display-4" />
                                        </div>
                                        <div>
                                        <h5>Saved</h5>
                                        <p className="text-muted">
                                                Requisition <strong>{ipaNo}</strong> Saved!
                                        </p>
                                        </div>
                                    </div>
                                    </CardBody>
                                </Card>
                                </Col>
                            </Row>
                        )}
                        </div>


                        
                           

                    </Container>
                </div>
                </LoadingOverlay>
         

        );
    }

export default AddPurchaseRequisition;