import React, {useState, useEffect} from "react";
import MetaTags from 'react-meta-tags';
import { Link } from "react-router-dom";
import { Col, Container, Form, Row, Input, Label, FormFeedback } from "reactstrap";

import axios from 'axios';
import { HRConfig2, TOAST_OPTIONS2, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";
import { useHistory } from "react-router-dom";

// import images
import logodark from "../../assets/images/logo-dark.png";
import logolight from "../../assets/images/logo-light.png";
import CarouselPage from "./CarouselPage";

const PublicApplicantsRegister = () => {
  const history = useHistory()
  toastr.options = TOAST_OPTIONS2;
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [phone, setPhone] = useState('')

  const handleRegister = () =>{
    if(firstName.trim().length < 2){
      toastr.error('Enter your first name')
    }else if(lastName.trim().length < 2){
      toastr.error('Enter your last name')
    }else if(phone.trim().length != 10){
      toastr.error('Enter phone number in formart 0771234567')
    }else if(email.trim().length < 3){
      toastr.error('Enter your email address')
    }else{
      const data = {
        fname: firstName,
        lname: lastName,
        email: email,
        phone: phone
      }
      axios.post(url.APPLICANT_REGISTER, JSON.stringify(data), HRConfig2).then((r)=>{
        console.log(r)
        if(r.data.status == '1'){
          toastr.success(r.data.message)
          setFirstName('')
          setLastName('')
          setEmail('')
          setPhone('')
          history.push('/applicants-login')
        }else{
          toastr.error(r.data.message)
        }
      }).catch((e)=>{
        console.log(e)
      })
    }
  }

  return (
    <React.Fragment>
      <div>
        <MetaTags>
          <title>Sign up | UEGCL</title>
        </MetaTags>
        <Container fluid className="p-0">
          <Row className="g-0">
            <CarouselPage />

            <Col xl={3}>
              <div className="auth-full-page-content p-md-5 p-4" style={{ backgroundColor: 'white' }}>
                <div className="w-100">
                  <div className="d-flex flex-column h-100">
                    <div className="mb-4 mb-md-5">
                      <Link to="#" className="d-block auth-logo">
                        <img
                          src={logodark}
                          alt=""
                          height="78"
                          className="auth-logo-dark"
                        />
                        <img
                          src={logolight}
                          alt=""
                          height="78"
                          className="auth-logo-light"
                        />
                      </Link>
                    </div>
                    <div className="my-auto">
                      <div>
                        <h5 className="text-primary">Register Account</h5>
                        <p className="text-muted">
                          Details will be used to access and Apply for vacancies
                        </p>
                      </div>

                      <div className="mt-4">
                        <Form className="form-horizontal"
                          onSubmit={(e) => {
                            e.preventDefault();
                            return false;
                          }}
                        >
                          <div className="mb-3">
                            <Label className="form-label">First Name</Label>
                            <input  className="form-control" type="text" value={firstName} onChange={(e)=>setFirstName(e.target.value)} />
                          </div>

                          <div className="mb-3">
                            <Label className="form-label">Last Name</Label>
                            <input  className="form-control" type="text" value={lastName} onChange={(e)=>setLastName(e.target.value)}  />
                          </div>

                          <div className="mb-3">
                            <Label className="form-label">Email</Label>
                            <input className="form-control" type="email" value={email} onChange={(e)=>setEmail(e.target.value)}  />
                          </div>

                          <div className="mb-3">
                            <Label className="form-label">Phone Number</Label>
                            <input className="form-control" type="phone" value={phone} onChange={(e)=>setPhone(e.target.value)} />
                          </div>

                          <div>
                            <p className="mb-0">
                              By registering you agree to our 
                              <a href="#" className="text-primary">
                                Terms of Use
                              </a>
                            </p>
                          </div>

                          <div className="mt-4">
                            <button
                              className="btn btn-primary btn-block "
                              type="submit"
                              onClick={handleRegister}
                            >
                              Register
                            </button>
                          </div>

                        </Form>

                       
                        <div className="mt-5 text-center">
                          <p>
                            Already have an account ?<br/>
                            <Link
                              to="/applicants-login"
                              className="fw-medium text-primary"
                            >
                              Signin
                            </Link>
                          </p>
                        </div>
                      </div>
                    </div>

                    <div className="mt-4 mt-md-5 text-center">
                      <p className="mb-0">
                         {new Date().getFullYear()} UEGCL
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default PublicApplicantsRegister;
