import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardSubtitle, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { Link, useHistory, useParams } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'



    const LeaveRequestDetails = () => {

        const history = useHistory();
        const {id} = useParams();

        const [lR, setLR] = useState({})
        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [gender, setGender] = useState('');
        const [department, setDepartment] = useState('');
        const [leaveNo, setLeaveNo] = useState('');
        const [telephone, setTelephone] = useState('');
        const [leaveAddress, setLeaveAddress] = useState('');
        const [leaveCategory, setLeaveCategory] = useState('');
        const [startDate, setStartDate] = useState(new Date());
        const [endDate, setEndDate] = useState(new Date());
        const [leaveStatus, setLeaveStatus] = useState('');
        const [st, setSt] = useState('danger');
        const [noOfDays, setNoOfDays] = useState('0');
        const [leaveBalance, setLeaveBalance] = useState(0);
        const [documentDate, setDocumentDate] = useState('');
        const [leaveDueCurrentYear, setLeaveDueCurrentYear] = useState(0);
        const [leaveDuePreviousYear, setLeaveDuePreviousYear] = useState('');
        const [leaveInlineWithRooster, setLeaveInlineWithRooster] = useState('No');
        const [isloading, setIsloading] = useState(false);
        const [userError, setUserError] = useState(true);
        const [selectedDates, setSelectedDates] = useState([])

        toastr.options = TOAST_OPTIONS;
        const [inputs, setInputs] = useState([]);
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            toastr.success("Leave Request No  created" ); 
        }
   
        const handleCancelApprovalRequest = (e) => {
            setIsloading(true)
            const lNo = {'no': leaveNo}
            axios.post(url.CANCEL_APPROVAL_REQUEST, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("Approval Request Cancelled" ); 
                        history.go(0)
                    }else{
                        toastr.success("Could not Cancel Approval Request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    console.log(error)
                    setIsloading(false)
            })
        }
        const handleApprovalRequest = (e) => {
            setIsloading(true)
            const lNo = {'no': leaveNo}
            axios.post(url.SEND_LR_APPROVAL_REQUEST, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success("Leave Request Sent For Approval" ); 
                        history.go(0)
                    }else{
                        toastr.success("Could not Send leave request for approval" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                    console.log(error)
            })

        }
        const handleDeleteLeaveRequest = () =>{
            setIsloading(true)
            const lNo = {'no': leaveNo}
            axios.post(url.DELETELR, JSON.stringify(lNo), HRConfig)
                .then(function(response){     
                    if(response.status == '200'){
                        toastr.success(response.data.value ); 
                        history.push("/leave-requests")
                    }else{
                        toastr.warning("Could not Delete leave request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
                    console.log(error)
            })

        }
        const handleUpdateLeaveRequest = () =>{
            //setIsloading(true)
            let data = {}
            if(leaveCategory == 'ANNUAL'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Annual_Leave_WS     : 'Yes' 
                }
            }else if(leaveCategory == 'COMPASSION'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Compassionate_Leave_WS     : 'Yes' 
                }
            }else if(leaveCategory == 'SICK'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Sick_Leave_WS       : 'Yes' 
                }
            }else if(leaveCategory == 'MATERNITY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Maternity_Leave_WS       : 'Yes' 
                }
            }else if(leaveCategory == 'PATERNITY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Paternity_Leave_WS       : 'Yes' 
                }
            }else if(leaveCategory == 'STUDY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Study_Leave_WS       : 'Yes' 
                }
            }else{
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Unpaid_Leave_WS       : 'Yes' 
                }
            }          
            setIsloading(true)
            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : lR['@odata.etag']
                }
              }
            axios.patch(`${url.LEAVE_REQUESTS}(${lR.SystemId})`, JSON.stringify(data), Config)
                .then(function(response){     
                    //console.log(response.data); 
                    if(response.data.No){
                        toastr.success("Leave Request Updated" ); 
                        history.go(0)
                    }else{
                        toastr.success("Could not Update leave request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
            })
        }

        useEffect(() => {
            setIsloading(true)
            axios.get(url.LEAVE_REQUESTS + "("+ id +")", HRConfig)
                .then(function(response){
                    setLR(response.data);
                    setEmpNo(response.data.Employee_No);
                    setEmpName(response.data.Employee_Name);
                    setEmpTitle(response.data.Employee_Title);
                    setGender('');
                    setLeaveNo(response.data.No);
                    setTelephone(response.data.Telephone_Number);
                    setLeaveAddress(response.data.Leave_Address);
                    setLeaveStatus(response.data.Status);
                    setStartDate(response.data.From_Date);
                    setEndDate(response.data.To_Date);
                    setNoOfDays(response.data.No_of_Days);
                    setLeaveBalance(response.data.Balance);
                    setSelectedDates([ new Date(response.data.From_Date), new Date(response.data.To_Date)])
                    setDocumentDate(response.data.Document_Date);
                    setLeaveDueCurrentYear(response.data.Leave_Due_Current_Year);
                    setLeaveDuePreviousYear(response.data.Leave_Due_Previous_Year);
                    (response.data.Leave_in_Line_with_Roster == false) ? setLeaveInlineWithRooster('No') : setLeaveInlineWithRooster('Yes') ;
                    if(response.data.Annual_Leave_WS == 'Yes'){
                        setLeaveCategory('ANNUAL');
                    }else if(response.data.Compassionate_Leave_WS == 'Yes'){
                        setLeaveCategory('COMPASSION');
                    }else if(response.data.Maternity_Leave_WS == 'Yes'){
                        setLeaveCategory('MATERNITY');
                    }else if(response.data.Paternity_Leave_WS == 'Yes'){
                        setLeaveCategory('PATERNITY');
                    }else if(response.data.Sick_Leave_WS == 'Yes'){
                        setLeaveCategory('SICK');
                    }else if(response.data.Other_Leave_WS == 'Yes'){
                        setLeaveCategory('OTHER');
                    }else if(response.data.Study_Leave_WS == 'Yes'){
                        setLeaveCategory('STUDY');
                    }else if(response.data.Unpaid_Leave_WS == 'Yes'){
                        setLeaveCategory('WITHOUT PAY');
                    }
                    if(leaveStatus == 'Pending Approval'){
                        setSt('warning');
                    }else if(leaveStatus == 'Approved'){
                        setSt('success');
                    }else{
                        setSt('danger');
                    }
                    setIsloading(false)
                    setUserError(false);
                }).catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setUserError(true);
                    setIsloading(false)
                })
                
        
        },[]);
       

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Leave Request Details | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem={`Leave Request Details`} />
                        <Form onSubmit={handleSubmit}>
                    
                            <Alert color={`${st}`} role="alert">
                                    <strong>{leaveStatus}</strong>
                            </Alert>
           
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Personal Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control"  value = {empNo} onChange={handleChange} disabled={true} />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" defaultValue={empName} onChange={handleChange} disabled={true}  />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" defaultValue={empTitle} onChange={handleChange} disabled={true}/>
                                        <label htmlFor="floatingDepartInput">Title</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>

                            </Col>
                            <Col  xl={8}>
                            <Card>
                                <CardBody>
                                    <CardTitle className="mb-3">
                                        Details
                                    </CardTitle>
                                    <Row>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingIdInput">Leave Request No</label>
                                        <input type="text" className="form-control" defaultValue={leaveNo}   disabled={true} />
                                        
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingIdInput">Telephone Number</label>
                                        <input type="text" className="form-control" defaultValue={telephone}   onChange={(e)=>setTelephone(e.target.value)}  />
                                        
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingnameInput">Leave Address</label>
                                        <input type="text" className="form-control" defaultValue={leaveAddress}  onChange={(e)=>setLeaveAddress(e.target.value)}  />
                                        
                                        </div>
                                    </Col>
                                  
                                        <Col md={6}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingSelectGrid">Leave Category</label>
                                                <select  className="form-select" onChange={(e)=>setLeaveCategory(e.target.value)}>
                                                    <option>{leaveCategory}</option>
                                                    <option>ANNUAL</option>
                                                    <option>COMPASSION</option>
                                                    <option>MATERNITY</option>
                                                    <option>PATERNITY</option> 
                                                    <option>STUDY</option>
                                                    <option>WITHOUT PAY</option>
                                                </select>
                                            </div>
                                        </Col>
                                    
                              
                 
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingnameInput">Start Date - End Date </label>
                                            <Flatpickr
                                                className="form-control"
                                                options={{
                                                    mode: 'range',
                                                    dateFormat: 'Y-m-d',
                                                    defaultDate: [ new Date(startDate), new Date(endDate)]
                                                }}
                                                value={selectedDates}
                                                onChange={(dates) =>{setSelectedDates(dates)} }
                                            />
                                     
                                            </div>
                                        </Col>
                                       
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Number Of Days</label>
                                            <input name='noOfDays' type="text" className="form-control noOfDays" disabled={true}  value={noOfDays}  onChange={handleChange}  />
                                            </div>
                                        </Col>
                            
                                     
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingIdInput">Document Date</label>
                                            <input name='documentDate' type="text" className="form-control documentDate" disabled={true} value={documentDate}   onChange={handleChange}  />
                                            </div>
                                        </Col>
                              
                                    
                                        <Col md={6}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingnameInput">Status</label>
                                            <input name='leaveStatus' disabled={true}  type="text" className="form-control" defaultValue={leaveStatus}  onChange={handleChange}   />
                                            </div>
                                        </Col>
                                    </Row>
                                    
          
                                </CardBody>
                            </Card>
                                            
                   
                                    <Row>
                                    {(leaveStatus == 'Open') && (
                                        <>
                                        <Col md={4}>
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn" onClick={handleDeleteLeaveRequest}>
                                                    Delete Leave Request
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="d-grid gap-2">
                                                <Button color="primary" type="button" className="btn" onClick={handleUpdateLeaveRequest}>
                                                    Update Leave Request
                                                </Button>
                                            </div>
                                        </Col>
                                        <Col md={4}>
                                            <div className="d-grid gap-2">
                                                <Button color="warning" type="button" className="btn" onClick={handleApprovalRequest}>
                                                   Send Approval Request
                                                </Button>
                                            </div>
                                        </Col>
                                        </>
                                    )}
                                    {(leaveStatus == 'Pending Approval') && (
                                        <>
                                        <Col md={12}>
                                            <div className="d-grid gap-2">
                                                <Button color="danger" type="button" className="btn btnVancelApprovalRequest" onClick={handleCancelApprovalRequest}>
                                                    Cancel Approval Request
                                                </Button>
                                            </div>
                                        </Col>
                                       
                                        </>
                                    )}
                                    
                                    </Row>
                         
                                            
                    
                        </Col>
                        </Row>
                        </Form>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default LeaveRequestDetails;