import React, { useState, useEffect } from "react"
import {
  Col,
  Card,
  CardBody,
  Row,
  Nav,
  NavItem,
  NavLink,
  Table,
  Input,
  Label,
  Button,
  Modal
} from "reactstrap"
import { Link } from "react-router-dom"
import classnames from "classnames"
import PropTypes from 'prop-types';
//Simple bar
import SimpleBar from "simplebar-react"
import axios from "axios";
import * as url from "../../helpers/url_helper"
import { HRConfig } from "helpers/backend_helper";

const TotalSellongProduct = props => {
  //const [activeTab, setActiveTab] = useState("1")
  const [isLoading, setIsLoading] = useState(false)
  const [modal_period, setmodal_period] = useState(false)
  const [period, setPeriod] = useState('')
  const [slip, setSlip] = useState('')
  const [periodList, setPeriodList] = useState([])

  const removeBodyCss = () => {
      document.body.classList.add("no_padding");
  }
  const tog_period = () => {
      setmodal_period(!modal_period);
      removeBodyCss();
  }

  useEffect(()=>{
    const obj = JSON.parse(localStorage.getItem("authUser"));
    //axios.get(`${url.PAYROLL_HEADERS}?$filter=FirstName eq 'Rosette'`, HRConfig).then((r)=>{
    axios.get(`${url.PAYROLL_HEADERS}?$filter=EmployeeNo eq '${obj.uid}'`, HRConfig).then((r)=>{
      setPeriodList(r.data.value)
      //console.log(r)
    }).catch((e)=>{
      console.log(e)
    })
  },[]);

  const handlePayslips = () =>{
    tog_period()
    setSlip('Payslip')
  }
  const handleNSSF = () =>{
    tog_period()
    setSlip('NSSF')
  }
  const handlePAYE = () =>{
    tog_period()
    setSlip('PAYE')
  }
  const handleRBS = () =>{
    tog_period()
    setSlip('RBS')
  }
  const handleGetSlip = (e) =>{
    tog_period()
    const obj = JSON.parse(localStorage.getItem("authUser"));
    const data = {no:obj.uid, period:e.target.value}
    if(slip == 'Payslip'){
      setIsLoading(true)
      axios.post(url.PRINT_PAYSLIP, JSON.stringify(data), HRConfig).then((rr)=>{
        const urlx = process.env.REACT_APP_APACHE_URL + rr.data.value
        axios({
          url: urlx,
          method: 'GET',
          responseType: 'blob',
        })
          .then(response => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', e.target.value+'-Payslip.pdf');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            setIsLoading(false)
          })
          .catch(error => {
            console.error('Error downloading file:', error);
            setIsLoading(false)
          });
        setIsLoading(false)
      }).catch((ee)=>{
        console.log(ee)
        setIsLoading(false)
      })
    }else if(slip == 'NSSF'){
      setIsLoading(true)
      axios.post(url.PRINT_NSSF, JSON.stringify(data), HRConfig).then((rr)=>{
        const urlx = process.env.REACT_APP_APACHE_URL + rr.data.value
        axios({
          url: urlx,
          method: 'GET',
          responseType: 'blob',
        })
          .then(response => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', e.target.value+'-NSSF.pdf');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            setIsLoading(false)
          })
          .catch(error => {
            console.error('Error downloading file:', error);
            setIsLoading(false)
          });
        setIsLoading(false)
      }).catch((ee)=>{
        console.log(ee)
        setIsLoading(false)
      })
    }else if(slip == 'PAYE'){
      setIsLoading(true)
      axios.post(url.PRINT_PAYEE, JSON.stringify(data), HRConfig).then((rr)=>{
        const urlx = process.env.REACT_APP_APACHE_URL + rr.data.value
        axios({
          url: urlx,
          method: 'GET',
          responseType: 'blob',
        })
          .then(response => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', e.target.value+'-PAYE.pdf');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            setIsLoading(false)
          })
          .catch(error => {
            console.error('Error downloading file:', error);
            setIsLoading(false)
          });
        setIsLoading(false)
      }).catch((ee)=>{
        console.log(ee)
        setIsLoading(false)
      })
    }else if(slip == 'RBS'){
      setIsLoading(true)
      axios.post(url.PRINT_RBS, JSON.stringify(data), HRConfig).then((rr)=>{
        const urlx = process.env.REACT_APP_APACHE_URL + rr.data.value
        axios({
          url: urlx,
          method: 'GET',
          responseType: 'blob',
        })
          .then(response => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', e.target.value+'-RBS.pdf');
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            setIsLoading(false)
          })
          .catch(error => {
            console.error('Error downloading file:', error);
            setIsLoading(false)
          });
        setIsLoading(false)
      }).catch((ee)=>{
        console.log(ee)
        setIsLoading(false)
      })
    }
      
  }
  const reports = [
    {
      'type': 'Payslip',
      'link':'#'
    },
    {
      'type': 'RBS',
      'link':'#'
    },
    {
      'type': 'NSSF',
      'link':'#'
    },
    {
      'type': 'PAYE',
      'link':'#'
    }];

  return (
    <React.Fragment>
    <Modal isOpen={modal_period} toggle={() => { tog_period() }} centered >
      <div className="modal-header">
        <h5 className="modal-title mt-0" id="myModalLabel">Period</h5>
        <button type="button" onClick={() => { setmodal_period(false); }} className="close" data-dismiss="modal" aria-label="Close" >
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div className="modal-body">
        <Row>
          <Col lg={12}>
              <div className="mb-3">
                  <label>Select Payroll Period</label>
                  <select className="form-select" value={period} onChange={(e)=>handleGetSlip(e)} >
                      <option></option>
                    {periodList.map((p,k)=>(
                      <option key={k} value={p.PayrollID}>{p.PayrollID}</option>
                    ))}
                  </select>
              </div>
          </Col>
        </Row>
      </div>
      <div className="modal-footer">
                                    <button type="button" onClick={() => {  tog_period() }} className="btn btn-danger" data-dismiss="modal">Cancel</button>
      </div>
      </Modal>
      <Col xl="4">
      {(props.showReports == true) && (
        <Card className="bg-success bg-soft">
        <div>
          <Row>
            <Col xs="12">
              <div className="text-primary p-3">
                <h5 className="text-primary">Reports:</h5>

                <ul className="ps-3 mb-0">
                  <li className="py-1"><Link to="#" onClick={()=>handlePayslips()}>Payslip</Link> </li>
                  <li className="py-1"><Link to="#" onClick={()=>handleNSSF()}>NSSF</Link></li>
                  <li className="py-1"><Link to="#" onClick={()=>handlePAYE()}>PAYE</Link></li>
                  <li className="py-1"><Link to="#" onClick={()=>handleRBS()}>RBS</Link></li>
                </ul>
                <div>{isLoading && (`loading...`)}</div>
              </div>
            </Col>
          </Row>
        </div>
      </Card>
      )}
         <Card>
          <CardBody>
            <h4 className="card-title mb-4">Leave</h4>
            <div className="mt-4">
              <SimpleBar style={{ maxHeight: "320px" }}>
                <div className="table-responsive">
                  <Table className="table table-nowrap align-middle table-hover mb-0 table table-sm m-0">
                    
                    
                    <thead>
                    <tr>
                      <th>Category</th>
                      <th>Entitled</th>
                      <th>Balance</th>
                      </tr>
                    </thead>
                    <tbody>
                    {props.leave && props.leave.map((l,k)=>(
                      <tr key={k}>
           
                        <td style={{ width: "90px" }}>
                          <h5 className="text-truncate font-size-14 mb-1">
                              {l.leaveType}
                          </h5>
                        </td>
                        <td style={{ width: "50px" }}>
                          <h5 className="text-truncate font-size-14 mb-1">
                            {l.entitled}
                          </h5>
                        </td>
                        
                        <td style={{ width: "50px" }}>
                          <h5 className="text-truncate font-size-14 mb-1">
                            {l.balance}
                          </h5>
                        </td>
                      </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              </SimpleBar>
            </div>
          </CardBody>
        </Card>


      </Col>
    </React.Fragment>
  )
}
TotalSellongProduct.propTypes = {
  leave: PropTypes.any,
  showReports: PropTypes.any
}
export default TotalSellongProduct
