import React from "react"
import { Carousel } from "react-responsive-carousel"
import "react-responsive-carousel/lib/styles/carousel.min.css"
import { Col } from "reactstrap"
import bg from "assets/images/background_image.jpg";

const CarouselPage = () => {
  return (
    <React.Fragment>
      <Col xl={9}>
        <div className="pt-lg-5 p-4" style={{position:'absolute', top:'0px', right:'0px', bottom:'0px',  left:'0px', margin:0, backgroundImage: "url(" + bg + ")", backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center',}}>
          <div className="w-100">
            <div className=""></div>
            <div className="d-flex h-100 flex-column">
              <div className="p-4 mt-auto">
                <div className="row justify-content-center">
                  <div className="col-lg-7">
                    <div className="text-center">
                      <h4 className="mb-3">
                        <i className="bx bxs-quote-alt-left text-primary h1 align-middle me-3"></i>
                        <span style={{ color: 'white' }}>100</span>+ Satisfied
                        employees
                      </h4>
                      <div dir="ltr">
                        <Carousel showThumbs={false} className="slider_css">
                          <div>
                            <div className="item">
                              <div className="py-3">
                                <p className="font-size-16 mb-4" style={{ color: 'white' }}>
                                  &quot; Apply and join the great team at UEGCL &ldquo;
                                </p>

                                <div>
                                  <h4 className="font-size-16" style={{ color: 'white' }}>
                                    
                                  </h4>
                                  <p className="font-size-14 mb-0" style={{ color: 'white' }}>
                                     
                                  </p>
                                </div>
                              </div>
                            </div>
                          </div>
                          <div>
                            <div className="item">
                              <div className="py-3">
                                <p className="font-size-16 mb-4" style={{ color: 'white' }}>
                                  &quot; It is a great learning experience that comes with proffessional and personal growth &ldquo;
                                </p>

                                <div>
                                  <h4 className="font-size-16" style={{ color: 'white' }}>
                                    
                                  </h4>
                                  <p className="font-size-14 mb-0" style={{ color: 'white' }}>
                                    
                                  </p>
                                </div>
                              </div>
                            </div>
                          </div>
                        </Carousel>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Col>
    </React.Fragment>
  )
}
export default CarouselPage
