import React, {useEffect, useState}  from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import {     
    Table,
    Row,
    Col,
    Card,
    CardBody,
    CardTitle,
    CardSubtitle,
    UncontrolledTooltip,
    Label,
    Button,
    Badge,
    Form,
    Input,
    InputGroup, Container,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
 } from "reactstrap";
import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";
import Select from 'react-select';
import { Link, useHistory, useParams, useLocation } from "react-router-dom"

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import axios from 'axios';
import { HRConfig, formatDate, TOAST_OPTIONS } from "../../helpers/backend_helper"
import { APPNAME, PAS_X  } from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";


import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import PropTypes from "prop-types";
import LoadingOverlay from 'react-loading-overlay';
import BounceLoader from 'react-spinners/BounceLoader'


    const PAs = () => {
        const {type} = useParams();
        const location = useLocation();

        const history = useHistory()
        const [paList, setPaList] = useState([])
        const [isloading, setIsloading] = useState(false)
        //console.log(location.pathname)

        useEffect(() => {
            const obj = JSON.parse(localStorage.getItem("authUser"));
            const EmployeeNo = obj.uid;
            let url = " url string";
            (location.pathname == '/performance-appraisals') ? ( url = `${PAS_X}?$filter=EmployeeNo eq '${EmployeeNo}'&$expand=pALines,pALines2`) : (url = `${PAS_X}?$filter=Appraiser eq '${EmployeeNo}' and Stage eq 'Appraiser Rating'&$expand=pALines,pALines2`)
            setIsloading(true)
            axios.get(url, HRConfig).then(function(response){
              setPaList(response.data.value);
              setIsloading(false)
            }).catch((e)=>{
              setIsloading(false)
              console.log(e)
            })
          }, []);
        

        const { SearchBar } = Search;
        const columns = [
        {
            dataField: 'Date',
            text: 'Date',
            sort: true
        }, 
        {
            dataField: 'cycle',
            text: 'Period',
            sort: true,
             // eslint-disable-next-line react/display-name
             formatter: (cellContent, row) => (
              <>
              <p className="text-muted mb-0">{row.No}</p>
                <h5 className="font-size-14 mb-1">
                  <Link to="#" className="text-dark">
                  {row.AppraisalPeriod}
                  </Link>
                </h5>
                <p className="text-muted mb-0">{row.AppraisalCycle}</p>
              </>
            ),
          }, 
        {
          dataField: 'appraisee',
          text: 'Appraisee',
          sort: true,
           // eslint-disable-next-line react/display-name
           formatter: (cellContent, row) => (
            <>
            <p className="text-muted mb-0">{row.EmployeeNo}</p>
              <h5 className="font-size-14 mb-1">
                <Link to="#" className="text-dark">
                {row.AppraiseeFullNames}
                </Link>
              </h5>
              <p className="text-muted mb-0">{row.AppraiseeJobTitle}</p>
            </>
          ),
        }, 
        {
            dataField: 'appraiser',
            text: 'Appraiseer',
            sort: true,
             // eslint-disable-next-line react/display-name
             formatter: (cellContent, row) => (
              <>
              <p className="text-muted mb-0">{row.Appraiser}</p>
                <h5 className="font-size-14 mb-1">
                  <Link to="#" className="text-dark">
                  {row.AppraiserNames}
                  </Link>
                </h5>
                <p className="text-muted mb-0">{row.AppraiserJobTitle}</p>
              </>
            ),
          }, 

          {
            dataField: 'performance',
            text: 'Total Performance',
            sort: true,
             // eslint-disable-next-line react/display-name
             formatter: (cellContent, row) => (
              <>
                <p className="text-muted mb-0">Performance: {row.TotalAppraiserPerforance}</p>
                <p className="text-muted mb-0">Behavior :{row.TotalAppraiserBehavioral}</p>
              </>
            ),
          }, 
        {
          dataField: 'Stage',
          text: 'Stage',
          sort: true,
          formatter: statusFormatter
        },
        {
          dataField: "action",
          isDummyField: true,
          text: "",
          formatter: actionFormatter
        }];
  
        const defaultSorted = [{
          dataField: 'Date',
          order: 'desc'
        }];
  
        function linesFormatter(cellContent, row){
          return(
            <strong></strong>
          )
        }
        function noFormatter(cellContent, row){
          return(
            <strong>{cellContent}</strong>
          )
        }
  
        function actionFormatter(cellContent, row){
          return(
            <Button
              type="button"
              color="info"
              className="btn-sm btn-rounded"
              onClick={()=>{ history.push(`/staff-performance-appraisal/${row.SystemId}`)}}
            >
              Details
            </Button>
          
          )
        }
        function statusFormatter(cellContent, row) {
          if (row.Stage == "Appraisee Rating") {
            return (
              <div>
                  <Badge className="me-2 bg-danger">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else if (row.Stage == "Closed"){
            return (
              <div>
                  <Badge className="me-2 bg-success">
                  { cellContent }
                  </Badge>
              </div>
            );
          }else{
            return (
              <div>
                  <Badge className="me-2 bg-warning">
                  { cellContent }
                  </Badge>
              </div>
            );
          }
        }
        return (
            <>
             <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Performance Appraisals | {APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={APPNAME} breadcrumbItem="Performance Appraisals" />
                        
                      
                      <Row>
            <Col lg={12}>
              <Card>
                <CardBody>
                  <CardTitle></CardTitle>
                  <CardSubtitle className="mb-3">
                    
                  </CardSubtitle>

                 {paList && (paList.length > 0) && (
                  <ToolkitProvider
                      keyField="No"
                      data={ paList }
                      columns={ columns }
                      search
                    >
                      { toolkitProps => (
                          <React.Fragment>
                            <Row className='mb-5'>
                              <Col lg={8}>
                                  <div  className="rounded bg-light d-grid gap-2">
                                    <SearchBar {...toolkitProps.searchProps} className="form-control bg-transparent border-0 searchTm m-0" />
                                  </div>
                              </Col>
                              
                                
                              <Col lg={4}> 
                                <div className="d-grid gap-2">
                                  <Link
                                    className="btn btn-primary"
                                    to="#"
                                  >
                                    <i className="mdi mdi-search me-1" />
                                    Filter
                                  </Link>
                                </div>
                              </Col>
                            </Row>
                            <Row>
                              <Col xl="12">
                                <div className="table">
                            
                                <BootstrapTable 
                                  keyField='No' 
                                  data={ paList } 
                                  columns={ columns } 
                                  striped
                                  hover
                                  condensed
                                  bordered={ false }
                                  noDataIndication="No Records" 
                                  defaultSorted={ defaultSorted }
                                  classes={ "table align-middle table-nowrap table-check" }
                                    {...toolkitProps.baseProps}
                                  />
                             </div>
                             </Col>
                             </Row>
                             </React.Fragment>
                            
                      )
                    }
                  </ToolkitProvider>
                  

                  )}
                  {paList && (paList.length < 1) && (
                    <div className="text-center">
                      <div className="mb-4">
                      <i className="mdi mdi-flask-empty-minus-outline text-danger display-4" />
                      </div>
                      <div>
                      <h5>No Performance Appraisal Found</h5>
                      <p className="text-muted">
                              
                      </p>
                      </div>
                  </div>

                  )}
                  
                </CardBody>
              </Card>
            </Col>
          </Row>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default PAs;