import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags';  // Added Meta Tag npm Package
import { Row, Col, Card, CardBody, CardTitle,CardSubtitle, Form, Container, Button, Alert } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import { Link, useHistory } from "react-router-dom"
 import "flatpickr/dist/themes/material_blue.css";
import Flatpickr from "react-flatpickr";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate } from "../../helpers/backend_helper"
import * as url from 'helpers/url_helper';

import toastr from "toastr";
import "toastr/build/toastr.min.css";

import LoadingOverlay from 'react-loading-overlay';
LoadingOverlay.propTypes = undefined
import BounceLoader from 'react-spinners/BounceLoader'
import Tasks from "../Dashboard/tasks"


    const AddLeaveRequest = () => {

        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [gender, setGender] = useState('');
        const [isloading, setIsloading] = useState(false);
        const [yourLeaves, setYourLeaves] = useState([])
        const [leaveCategory, setLeaveCategory] = useState('')
        const [telephone, setTelephone] = useState('')
        const [leaveAddress, setLeaveAddress] = useState('')
        const [selectedDates, setSelectedDates] = useState([])
        const [fileData, setFileData] = useState(null);

        const history = useHistory();

        toastr.options = TOAST_OPTIONS;
        const [inputs, setInputs] = useState([]);
        const handleChange = (event) => {
             const name = event.target.name;
             const value = event.target.value;
             setInputs(values => ({...values, [name]: value}));
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            

            let data = {}
            if(leaveCategory == 'ANNUAL'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Annual_Leave_WS     : 'YES' 
                }
            }else if(leaveCategory == 'COMPASSION'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Compassionate_Leave_WS     : 'YES' 
                }
            }else if(leaveCategory == 'SICK'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Sick_Leave_WS       : 'YES' 
                }
            }else if(leaveCategory == 'MATERNITY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Maternity_Leave_WS       : 'YES' 
                }
            }else if(leaveCategory == 'PATERNITY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Paternity_Leave_WS       : 'YES' 
                }
            }else if(leaveCategory == 'STUDY'){
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Study_Leave_WS       : 'YES' 
                }
            }else{
                data = {
                    Employee_No         : empNo,
                    Telephone_Number    : telephone,
                    Leave_Address       : leaveAddress,
                    From_Date           : formatDate(selectedDates[0]),
                    To_Date             : formatDate(selectedDates[1]),
                    Unpaid_Leave_WS       : 'YES' 
                }
            }          
            setIsloading(true)
            axios.post(url.LEAVE_REQUESTS, JSON.stringify(data), HRConfig)
                .then(function(response){     
                    //console.log(response.data); 
                    if(response.data.No){
                        if(leaveCategory == 'SICK'){
                            const fileData = {no:response.data.No}
                            axios.post(url.ATTACHFILE, JSON.stringify(fileData)).then((r)=>{
                                console.log(r)
                            }).catch((e)=>{ console.log(e) })
                        }
                        toastr.success("Leave Request No "+ response.data.No+" created" ); 
                        setTimeout(function(){ history.push('/leave-requests') } ,5000 );
                    }else{
                        toastr.success("Could not Create leave request" )
                    }
                    setIsloading(false)
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                    setIsloading(false)
            })

        }
        const [userError, setUserError] = useState(true);
        const handleFileChange = (event) => {
            const file = event.target.files[0];
            const reader = new FileReader();
            reader.readAsBinaryString(file);
            reader.onload = () => {
              // Encode the data as Base64
              const data_b64 = btoa(reader.result);
        
              // Create a JSON object with the file data
              const json_data = {
                filename: file.name,
                data: data_b64,
              };
        
              // Update the file data state
              setFileData(json_data);
            };
          };
        useEffect(() => {
            if (localStorage.getItem("authUser")) {
                const obj = JSON.parse(localStorage.getItem("authUser"));
                const EmployeeNo = obj.uid;
                
                
                axios.get(url.EMPLOYEES + "?$filter=No eq '"+ EmployeeNo +"'", HRConfig)
                    .then(function(response){
                        if(response.data.value.length > 0){
                            setEmpNo(response.data.value[0].No);
                            setEmpName(response.data.value[0].FirstName + " " + response.data.value[0].MiddleName+ " " + response.data.value[0].LastName);
                            setEmpTitle(response.data.value[0].JobTitle);
                            setGender(response.data.value[0].Gender);
                            document.addEventListener("DOMContentLoaded", function(event) {
                                document.getElementsByClassName('btnSubmitRequest')[0].disabled = false;
                            });
                            const leave = [
                                {
                                  'leaveType': 'Annual', 
                                  'entitled':response.data.value[0].AnnualLeaveDaysEntitlement,
                                  'balance': response.data.value[0].AnnualLeaveBalance
                                },
                                {
                                  'leaveType': 'Compassionate', 
                                  'entitled':response.data.value[0].CompationateLeaveEntitlement,
                                  'balance': response.data.value[0].CompassionateLeaveBalance
                                },
                                {
                                  'leaveType': 'Maternity', 
                                  'entitled':response.data.value[0].MaternityLeaveEntitilement,
                                  'balance': response.data.value[0].MaternityDaysUsed
                                },
                                {
                                  'leaveType': 'Paternity', 
                                  'entitled':response.data.value[0].PaternityLeaveEntitlement,
                                  'balance': response.data.value[0].PaternityDaysUsed
                                },
                                {
                                  'leaveType': 'Sick', 
                                  'entitled':response.data.value[0].SickLeaveEntitilement,
                                  'balance': response.data.value[0].SickLeaveDaysused
                                },
                                {
                                  'leaveType': 'Study', 
                                  'entitled':response.data.value[0].StudyLeaveEntitlement,
                                  'balance': response.data.value[0].StudyLeaveDaysUsed
                                }
                              ];
                              setYourLeaves(leave)
                            setUserError(false);
                        }else{
                            toastr.error("Employee Details Not Found in Business Central");
                        }
                    }).catch(function (error) {
                        toastr.error(error.response.data.error.message);
                        setUserError(true);
                    })
                    .then(function () {
                    // always executed
                });

                
            }
        },[]);

        
        

        return (
            <>
            <LoadingOverlay
                active={isloading}
                spinner={<BounceLoader />}
                text='Please wait...'
                >
                <div className="page-content">
                    <MetaTags>
                    <title>Add Leave Request | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Add Leave Request" />
                        <Form onSubmit={handleSubmit}>
                        {userError && ( 
                            <Alert color="danger" role="alert">
                                    Your Employee Number Not Found in Dynamics 365 Business Central
                            </Alert>
                        )}
                        <Row>
                        <Col xl={4}>
                        <Card>
                            <CardBody>
                            <CardTitle>Employee Details</CardTitle>
                                    <Row>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeNo' id="EmployeeNo" type="text" className="form-control" disabled={true}  value = {empNo} onChange={handleChange}  />
                                        <label htmlFor="floatingIdInput">Employee No</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='EmployeeName' id="EmployeeName"  type="text" className="form-control" disabled={true} defaultValue={empName} onChange={handleChange}   />
                                        <label htmlFor="floatingnameInput">Employee Name</label>
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                              
                                    <Col md={12}>
                                        <div className="form-floating mb-3">
                                        <input name='Title' id="Title"  type="text" className="form-control" disabled={true} defaultValue={empTitle} onChange={handleChange} />
                                        <label htmlFor="floatingDepartInput">Title</label>
                                        </div>
                                    </Col>
                                </Row>
                                </CardBody>
                            </Card>
                            
                            </Col>
                            <Tasks leave={yourLeaves} showReports={false}/>
                            <Col  xl={4}>
                            <Card>
                                <CardBody>
                                    <CardTitle className="mb-3">
                                        Leave Request
                                    </CardTitle>
                                    <Row>
                                
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingIdInput">Telephone Number</label>
                                        <input type="text" className="form-control"   onChange={(e)=>setTelephone(e.target.value)}  />
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label>Leave Address</label>
                                        <input type="text" className="form-control"  onChange={(e)=>setLeaveAddress(e.target.value)}   />
                                        
                                        </div>
                                    </Col>
                                    </Row>
                                    <Row>
                                        <Col md={12}>
                                        <div className="mb-3">
                                                <label htmlFor="floatingSelectGrid"> Leave Category</label>
                                                <select  className="form-select" onChange={(e)=>setLeaveCategory(e.target.value)}>
                                                    <option>ANNUAL</option>
                                                    <option>SICK</option>
                                                    <option>COMPASSION</option>
                                                    {(gender == 'Female') ? (<option>MATERNITY</option>) : (<option>PATERNITY</option>)} 
                                                    <option>STUDY</option>
                                                    <option>WITHOUT PAY</option>
                                                </select>
                                                
                                            </div>
                                        </Col>
                                    </Row>
                                    {/*
                                    {(leaveCategory == 'SICK') && (
                                    <Col md={12}>
                                        <div className="mb-3">
                                        <label htmlFor="floatingnameInput">Attach File</label>
                                        <input type="file" className="form-control"  onChange={handleFileChange}   />
                                        
                                        </div>
                                    </Col>
                                    )}
                                    */}
                                    <Row>
                              
                                        <Col md={12}>
                                            <div className="mb-3">
                                            <label htmlFor="floatingDepartInput">Start Date - End Date</label>
                                            <Flatpickr
                                                className="form-control"
                                                placeholder="dd M,yyyy"
                                                options={{
                                                    mode: "range",
                                                    dateFormat: "Y-m-d"
                                                }}
                                                value={selectedDates}
                                                onChange={(dates) =>{setSelectedDates(dates)} }
                                                />
                                            
                                            </div>
                                        </Col>
                                    </Row>
                                    
          
                         
                        
                                </CardBody>
                            </Card>
                            <Row>
                                <Col md={12}>
                                    <div className="d-grid gap-2">
                                        <Button color="primary" type="submit" className="btn btnSubmitRequest">
                                            Submit Leave Request
                                        </Button>
                                    </div>
                                </Col>
                    
                            </Row>
                                    
                    
                        </Col>
                        
                        </Row>
                        </Form>


                    </Container>
                </div>
                </LoadingOverlay>
            </>
        );
    }

export default AddLeaveRequest;