import React, {useState, useEffect} from 'react';
import MetaTags from 'react-meta-tags'; 
import { useParams } from 'react-router-dom'; // Added Meta Tag npm Package
import {   
    Card,
    Col,
    Container,
    Row,
    CardBody,
    Alert,
    Label,
    Button,
    Form,
    Input,
    InputGroup, } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb";

import axios from 'axios';
import { HRConfig, TOAST_OPTIONS, formatDate, yearsPast } from "../../helpers/backend_helper"
import { EXIT_INTERVIEW, EXIT_INTERVIEW4APPROVAL, DELETE_EXIT_INTERVIEW} from 'helpers/url_helper';
import * as url from "../../helpers/url_helper"

import toastr from "toastr";
import "toastr/build/toastr.min.css";

    const ExitInterviewDetails = () => {
        const {id} = useParams();

        const [empNo, setEmpNo] = useState('');
        const [empName, setEmpName] = useState('');
        const [empTitle, setEmpTitle] = useState('');
        const [yearsEmployed, setYearsEmployed] = useState('')
        const [reason, setReason] = useState('')
        const [restrictions, setRestrictions] = useState('')

        const [one, setOne] = useState('');
        const [two, setTwo] = useState('');
        const [three, setThree] = useState('');
        const [four, setFour] = useState('');
        const [five, setFive] = useState('');
        const [six, setSix] = useState('');
        const [seven, setSeven] = useState('');
        const [eight, setEight] = useState('');
        const [nine, setNine] = useState('');
        const [ten, setTen] = useState('');
        const [eleven, setEleven] = useState('');
        const [twelve, setTwelve] = useState('');
        const [thirteen, setThirteen] = useState('');
        const [fourteen, setFourteen] = useState('');
        const [fifteen, setFifteen] = useState('');
        
        const [etag, setEtag] = useState('')
        const [no, setNo] = useState('')
        const [status, setStatus] = useState('')

        toastr.options = TOAST_OPTIONS;

        const handleDeleteExitInterview = () => {
            const data = {
                "no": no
            }
            axios.post(DELETE_EXIT_INTERVIEW, JSON.stringify(data), HRConfig)
                .then((r) =>{
                    console.log(r);
                }).catch((e) => {
                    console.log(e);
                })
        }
        const handleSendInterviewForApproval = () => {
            const data = {
                "no": no
            }
            axios.post(EXIT_INTERVIEW4APPROVAL, JSON.stringify(data), HRConfig)
                .then((r) =>{
                    console.log(r);
                }).catch((e) => {
                    console.log(e);
                })
        }
        const handleUpdateInterview = (e) => {
            const interviewDate = formatDate(Date.now())
            const durationOfService = document.getElementsByClassName('durationOfService')[0].value
            const reasonForLeaving = document.getElementsByClassName('reasonForLeaving')[0].value
            const informedOfRestrictions = document.getElementsByClassName('informedOfRestrictions')[0].value

            const Q1 = document.getElementsByClassName('11')[0].value
            const Q2 = document.getElementsByClassName('12')[0].value
            const Q3 = document.getElementsByClassName('13')[0].value
            const Q4 = document.getElementsByClassName('14')[0].value
            const Q5 = document.getElementsByClassName('15')[0].value
            const Q6 = document.getElementsByClassName('16')[0].value
            const Q7 = document.getElementsByClassName('17')[0].value
            const Q8 = document.getElementsByClassName('18')[0].value
            const Q9 = document.getElementsByClassName('19')[0].value
            const Q10 = document.getElementsByClassName('110')[0].value
            const Q11 = document.getElementsByClassName('111')[0].value
            const Q12 = document.getElementsByClassName('112')[0].value
            const Q13 = document.getElementsByClassName('113')[0].value
            const Q14 = document.getElementsByClassName('114')[0].value
            const Q15 = document.getElementsByClassName('115')[0].value

            const exitData = {
                'Date'                  : interviewDate,
                'EmployeeNo'            : empNo,
                'DurationOfService'     : durationOfService,
                'EmpContrRecognized'    : Q1,
                'MgtBackingOnTheJob'    : Q2,
                'FeelSecureInYourJob'   : Q3,
                'IsWorkRewarding'       : Q4, 
                'OrganizationTreatment' : Q5,
                'SalaryAdequate'        : Q6,
                'ContentWithWorkingCondition' : Q7,
                'WasTrainingAdequate'   : Q8,
                'UnderstandOrgPolicies' : Q9,
                'IncidencesOfTheft'     : Q10,
                'DetailOfTheft'         : Q11,
                'AbuseOfPrivileges'     : Q12,
                'EmployeeRespectTheOrg' : Q13,
                'YourOpinion'           : Q14,
                'GeneralEmployeeFeedback'   : Q15,

                //'ReasonForLeaving'      : reasonForLeaving, // not available
                //'InformedOfRestrictions': informedOfRestrictions, // not available
                
            }

            const Config = {
                auth: {
                  username: url.HRMIS,
                  password: url.HRMISPASS,
                },
                headers: {
                  'Content-Type': 'application/json',
                  'If-Match'    : etag
                }
              }
              //console.log(JSON.stringify(Config))
            axios.patch(`${EXIT_INTERVIEW}(${id})`, JSON.stringify(exitData), Config)
                .then(function(response){     
                    console.log(response); 
                    if(response.data.No){
                        toastr.success("Exit Interview Updated" ); 
                        //history.push('/exit-interview-submitted') 
                        getData();
                    }else{
                        toastr.success("Could not Submit" )

                    }
                })
                .catch(function (error) {
                    toastr.error(error.response.data.error.message);
                })
                .then(function () {
                // always executed
                
            });
        }

        const getData = () => {
            axios.get(`${EXIT_INTERVIEW}(${id})`, HRConfig)
            .then(function(response){
                   // console.log(response.data)
                    setNo(response.data.No)
                    setStatus(response.data.Status)
                    setEtag(response.data['@odata.etag'])
                    setEmpNo(response.data.EmployeeNo);
                    setEmpName(response.data.EmployeeName);
                    setEmpTitle(response.data.PositionName);
                    setYearsEmployed(response.data.DurationOfService)
                    setReason(response.data.ReasonforLeaving)
                    setRestrictions(response.data.InformedofRestrictionson)
                    setOne(response.data.EmpContrRecognized)
                    setTwo(response.data.MgtBackingOnTheJob)
                    setThree(response.data.FeelSecureInYourJob)
                    setFour(response.data.IsWorkRewarding)
                    setFive(response.data.OrganizationTreatment)
                    setSix(response.data.SalaryAdequate)
                    setSeven(response.data.ContentWithWorkingCondition)
                    setEight(response.data.WasTrainingAdequate)
                    setNine(response.data.UnderstandOrgPolicies)
                    setTen(response.data.IncidencesOfTheft)
                    setEleven(response.data.DetailOfTheft)
                    setTwelve(response.data.AbuseOfPrivileges)
                    setThirteen(response.data.EmployeeRespectTheOrg)
                    setFourteen(response.data.YourOpinion)
                    setFifteen(response.data.GeneralEmployeeFeedback)
                    
            }).catch(function (error) {
                //toastr.error(error.response.data.message);
                console.log(error);
            })
            .then(function () {
            // always executed
        });
        }
        useEffect(() => {
            getData();
        },[]);

        return (
            <>
                <div className="page-content">
                    <MetaTags>
                    <title>Exit Interview | {url.APPNAME}</title>
                    </MetaTags>
                    <Container fluid={true}>
                        <Breadcrumbs title={url.APPNAME} breadcrumbItem="Exit Interview" />
                        
                        <Row>
                        <Col lg={12}><Alert color={(status == 'Open') ? `danger`: `success`} role="alert"> <strong>{status}</strong></Alert></Col>
                                    <Col lg={5}>
                                    <Card>
                                        <CardBody>
                                        

                                        
                                        <Row>
                                        
                                            <Col md={12}>
                                                <div className="mb-3">
                                                <Label htmlFor="formrow-email-Input">No</Label>
                                                <Input
                                                    type="text"
                                                    className="form-control No"
                                                    defaultValue={no}
                                                    disabled={true}
                                                />
                                                </div>
                                            </Col>
                                            <Col md={6}>
                                                <div className="mb-3">
                                                <Label htmlFor="formrow-email-Input">Employee No</Label>
                                                <Input
                                                    type="text"
                                                    className="form-control employeeNo"
                                                    defaultValue={empNo}
                                                    disabled={true}
                                                />
                                                </div>
                                            </Col>
                                            <Col md={6}>
                                                <div className="mb-3">
                                                <Label htmlFor="formrow-email-Input">Employee Name</Label>
                                                <Input
                                                    type="text"
                                                    className="form-control employeeName"
                                                    defaultValue={empName}
                                                    disabled={true}
                                                />
                                                </div>
                                            </Col>
                                            </Row>
                                            <Row>
                                            
            
               
                                           
                                            <Col md={12}>
                                                <div className="mb-3">
                                                <Label htmlFor="formrow-email-Input">Position</Label>
                                                <Input
                                                    type="text"
                                                    className="form-control position"
                                                    defaultValue={empTitle}
                                                    disabled={true}
                                                />
                                                </div>
                                            </Col>
                                            </Row><Row>
                                
                                            <Col md={12}>
                                                <div className="mb-3">
                                                <Label htmlFor="formrow-password-Input">Duration of Employement</Label>
                                                <Input
                                                    type="text"
                                                    className="form-control durationOfService"
                                                    defaultValue={yearsEmployed}
                                                    disabled={true}
                                                />
                                                </div>
                                            </Col>
                                            </Row>
                                        </CardBody>
                                        </Card>
                                        <Card>
                                        <CardBody>
                                            <Row>
                                            <Col md={12}>
                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input">Reason for Leaving the Organization</Label>
                                            <Input
                                                type="text"
                                                className="form-control reasonForLeaving"
                                                defaultValue={reason}
                                            />
                                            </div>
                                            </Col>
                                            <Col md={12}>
                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input">Employee informed of Restriction on:</Label>
                                            <select
                                                className="form-control informedOfRestrictions"
                                                placeholder='Choose...'
                                                defaultValue={restrictions}
                                            >   <option value="">Select</option>
                                                <option value="Divulging Confidential Information">Divulging Confidential Information</option>
                                                <option value="Removing Organization Documents">Removing Organization Documents</option>
                                                <option value="Other">Other</option>
                                            </select>
                                            </div>
                                            </Col>
                                            </Row>
                                            <div>
                                            </div>
                                       
                                        </CardBody>
                                    </Card>
                                    </Col>





                                    <Col lg={7}>
                                    <Card>
                                        <CardBody>
                                        <Form>
                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>1. Do you believe Management adequately recognizes employee contributions?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 11"
                                                defaultValue={one}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>2. Do uou feel that you have had the backing of Management on the Job?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 12"
                                                defaultValue={two}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>3. Did you feel secure on your Job?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 13"
                                                defaultValue={three}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>4. Did you find the work rewarding?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 14"
                                                defaultValue={four}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>5. Do you feel you have been faily treated by the Organization</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 15"
                                                defaultValue={five}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>6. Were you paid an adequate salary for the work you did?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 16"
                                                defaultValue={six}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>Were you content with your working conditions?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 17"
                                                defaultValue={seven}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>8. Do you feel your Training was adequate?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 18"
                                                defaultValue={eight}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>9 Did you understand Organization policies and the reason for them?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 19"
                                                defaultValue={nine}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>10. Did you observe incidences of theft of Organization Property ?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 110"
                                                defaultValue={ten}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>11. Can you give Details ?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 111"
                                                defaultValue={eleven}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>12. Do Employees regulary abuse proviledges for example making personal phone calls, by using the copy machines for personal business?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 112"
                                                defaultValue={twelve}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>13. Do Employees in General respect Management of this Organization?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 113"
                                                defaultValue={thirteen}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>14. What is your Opinion, have you found as the Organization s weak point?</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 114"
                                                defaultValue={fourteen}
                                            />
                                            </div>

                                            <div className="mb-3">
                                            <Label htmlFor="formrow-firstname-Input"><b>General Employee Feedback</b></Label>
                                            <Input
                                                type="text"
                                                className="form-control 115"
                                                defaultValue={fifteen}
                                            />
                                            </div>

                                            

                                            
                                            <div>
                                            <Row>
                                            <Col md={4}>
                                            <div className="d-grid gap-2">
                                            <Button  className="btn btn-danger w-md" onClick={handleDeleteExitInterview}>
                                                Delete
                                            </Button>
                                            </div>
                                            </Col>
                                            <Col md={4}>
                                            <div className="d-grid gap-2">
                                            <Button  className="btn btn-primary w-md" onClick={handleUpdateInterview}>
                                                Update
                                            </Button>
                                            </div>
                                            </Col>
                                            <Col md={4}>
                                            <div className="d-grid gap-2">
                                            <Button  className="btn btn-warning w-md" onClick={handleSendInterviewForApproval}>
                                                Send For Approval
                                            </Button>
                                            </div>
                                            </Col>
                                            </Row>
                                            
                                            
                                            
                                            </div>
                                        </Form>
                                        </CardBody>
                                    </Card>
                                    </Col>
                        </Row> 

                    </Container>
                </div>
            </>
        );
    }

export default ExitInterviewDetails;